/* eslint-disable no-console */
import {
  sendSignInLinkToEmail,
  fetchSignInMethodsForEmail,
} from "firebase/auth";
import config from "../../util/config";
import { auth } from "../../util/firebase";

console.log({ config });

const { baseUrl, useEmulator, bundleId, firebaseDynamicLinkDomain } = config;

const actionCodeSettings = (groupPin) => {
  const settings = {
    url: `${baseUrl}/#/verifyEmail`,
    handleCodeInApp: true,
    iOS: {
      bundleId,
    },
    dynamicLinkDomain: firebaseDynamicLinkDomain,
  };

  if (groupPin) {
    settings.url = `${settings.url}?pin=${groupPin}`;
  }
  return settings;
};

const emailLogin =
  ({ setSuccess, setAlternativeLogin, leaguePin }) =>
  async (values) => {
    const email = values.Email;

    console.log({ baseUrl, bundleId, useEmulator });

    try {
      console.log("send signin link to email", email);
      const methods = await fetchSignInMethodsForEmail(auth, email);

      if (methods[0] === "facebook.com") {
        setAlternativeLogin({ method: "Facebook", email });
        return;
      }

      await sendSignInLinkToEmail(auth, email, actionCodeSettings(leaguePin));

      window.localStorage.setItem("emailForSignIn", email);
    } catch (error) {
      const errorCode = error.code;
      const errorMessage = error.message;

      console.log(error);

      console.log({ errorCode, errorMessage });
    }

    setSuccess(true);

    // const response = await login({ ...values, journey });

    // handleLogin(response, "login");
  };

export default emailLogin;
