/* eslint-disable no-empty */
import { logUrl } from "../util/config";

const sendLog = (level) => async (data) => {
  try {
    const rawUserData = localStorage.getItem("userData");
    let userData;
    try {
      userData = JSON.parse(rawUserData);
    } catch (e) {
      userData = rawUserData;
    }
    const jwt = localStorage.getItem("JWTtoken");

    if (logUrl !== "local") {
      await fetch(logUrl, {
        method: "POST",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "content-type": "application/json",
        },
        credentials: "include",
        body: JSON.stringify({ level, data, userData, jwt }),
      });
    } else {
      // eslint-disable-next-line no-console
      console.log({ level, data, userData });
    }
  } catch (err) {}
};

const log = {
  error: sendLog("error"),
  info: sendLog("info"),
  warn: sendLog("warn"),
};

export default log;
