/* eslint-disable react/prop-types */
import React, { useState, useContext } from "react";
import { useLocation } from "react-router-dom";
import { Form, Button, Result } from "antd";
import Title from "antd/lib/typography/Title";

import emailLogin from "./emailLogin";

import GoogleLoginInput from "./GoogleLoginInput";
import EmailSignUpInput from "./EmailSignUpInput";
import ContinueWithEmailButton from "./ContinueWithEmailButton";
import ContinueWithAppleButton from "./ContinueWithAppleButton";
import UserContext from "../../contexts/UserContext";

// import { facebookAppId } from "../../util/config";

const AlternativeLogin = ({
  alternativeLogin,
  feedback,
  setAlternativeLogin,
}) => (
  <Result
    title={`To access your account log in with ${alternativeLogin.method}`}
    extra={
      <>
        {alternativeLogin.method === "Google" ? (
          <GoogleLoginInput setAlternativeLogin={setAlternativeLogin} />
        ) : (
          <EmailSignUpInput
            setAlternativeLogin={setAlternativeLogin}
            feedback={feedback}
            value={alternativeLogin.email}
          />
        )}
        <Button
          size="large"
          block
          type="link"
          style={{
            maxWidth: "300px",
          }}
          onClick={() => setAlternativeLogin(null)}
        >
          Back
        </Button>
      </>
    }
  />
);

const LoginForm = () => {
  const [feedback] = useState({ message: null, type: null });
  const [success, setSuccess] = useState(false);
  const [alternativeLogin, setAlternativeLogin] = useState(null);
  const [showEmailSignUp, setShowEmailSignUp] = useState(false);

  const { search } = useLocation();
  const authError = new URLSearchParams(search).get("authError");
  const userContext = useContext(UserContext);

  const { extraLoginInfo } = userContext;

  const onFinishFailed = () => {};

  return (
    <>
      <Form
        className="login-form"
        style={{ textAlign: "center" }}
        name="basic"
        initialValues={{
          remember: true,
        }}
        onFinish={emailLogin({
          setSuccess,
          setAlternativeLogin,
          leaguePin: extraLoginInfo.link,
        })}
        onFinishFailed={onFinishFailed}
      >
        <Title level={2} style={{ paddingTop: "20px" }}>
          Welcome to Who&apos;s Game
        </Title>
        {extraLoginInfo ? (
          extraLoginInfo.message
        ) : (
          <p style={{ padding: "0", maxWidth: "300px", margin: "auto" }}>
            Create an account to manage your players and your upcoming matches.
            Track their availability and review their stats.
          </p>
        )}

        {!success && !!authError ? (
          <Result
            status="error"
            title="Email Link Expired"
            subTitle="Please request a new email and click the new link."
          />
        ) : (
          <></>
        )}
        {success ? (
          <Result
            status="success"
            title="Check your email"
            subTitle="You should receive a confirmation email with a link to login."
            extra={
              <Button
                size="large"
                block
                type="link"
                style={{
                  maxWidth: "300px",
                }}
                onClick={() => setSuccess(false)}
              >
                Back
              </Button>
            }
          />
        ) : (
          <>
            {alternativeLogin ? (
              <AlternativeLogin
                setAlternativeLogin={setAlternativeLogin}
                alternativeLogin={alternativeLogin}
                feedback={feedback}
              />
            ) : (
              <div style={{ paddingTop: authError ? "0px" : "40px" }}>
                {!showEmailSignUp && (
                  <>
                    <GoogleLoginInput
                      setAlternativeLogin={setAlternativeLogin}
                    />
                    <ContinueWithAppleButton />
                    <ContinueWithEmailButton
                      setShowEmailSignUp={setShowEmailSignUp}
                    />
                  </>
                )}
                {showEmailSignUp && (
                  <>
                    <EmailSignUpInput
                      setAlternativeLogin={setAlternativeLogin}
                      feedback={feedback}
                    />
                    <Button
                      size="large"
                      block
                      type="link"
                      style={{
                        maxWidth: "300px",
                      }}
                      onClick={() => setShowEmailSignUp(false)}
                    >
                      Back
                    </Button>
                  </>
                )}
              </div>
            )}
          </>
        )}
        <p
          style={{
            marginTop: "30px",
            maxWidth: "300px",
            display: "inline-block",
          }}
        >
          Click Continue to agree to the Who&apos;s Game Terms of Service and
          acknowledge that the Who&apos;s Game Privacy Policy applies to you.
        </p>
      </Form>
    </>
  );
};

export default LoginForm;
