import React, { useContext } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { message, Button, Form, Input } from "antd";

import UserContext from "../../contexts/UserContext";
import { resetPassword } from "../../services/api-service";

const PasswordReset = () => {
  const navigate = useNavigate();

  const { setModalState, setLoginVisible } = useContext(UserContext);
  const query = new URLSearchParams(useLocation().search);

  const token = query.get("token");
  const tokenId = query.get("tokenId");

  const changePassword = async ({ password }) => {
    const response = await resetPassword({ password, token, tokenId });

    if (response.errors && response.errors[0].message.includes("expired")) {
      navigate(`/new-group`);
      setModalState("resetPasswordExpired");
      return;
    }

    try {
      if (response.data.resetPassword.status === "done") {
        message.success("Password successfully reset, now log in.");
        navigate(`/new-group`);
        setModalState("");
        setLoginVisible(true);
      }
    } catch (error) {
      message.error(
        "There was an error resetting your password, please try again or contact support."
      );
      navigate(`/new-group`);
      setModalState("");
    }
  };

  if (token && tokenId) {
    return (
      <>
        <p>Choose a new pasword</p>
        <Form onFinish={changePassword}>
          <Form.Item
            name="password"
            rules={[
              {
                required: true,
                message: "Input your password",
              },
              {
                type: "string",
                min: 5,
                message: "Password must be 5 or more characters",
              },
            ]}
          >
            <Input type="password" placeholder="password" />
          </Form.Item>
          <Button block type="primary" htmlType="submit">
            Set new password
          </Button>
        </Form>
      </>
    );
  }

  setModalState("");

  return <>Sorry there was an error, please contact support.</>;
};

export default PasswordReset;
