/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import { Col, Modal } from "antd";
import React, { useContext, useState } from "react";
import { useParams } from "react-router-dom";
import useSWR, { mutate } from "swr";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import moment from "moment";
import { containerStyle } from "../countdownStyles";
import UserContext from "../../contexts/UserContext";
import { getNextGame, updateAvailability } from "../../services/api-service";
import LoadingBall from "../icons/LoadingBall";
import WhiteButton from "../atoms/WhiteButton";

const AvailableBox = ({ children, style }) => (
  <Col key="available" span={24}>
    <div
      style={{ ...containerStyle, ...style }}
      className="gradient-background main-fader"
      data-test-id="available-dash-container"
    >
      <div>{children}</div>
    </div>
  </Col>
);

/** @return {React.CSSProperties} */
const selectedStyleAvailable = (available) => {
  if (!available) {
    return {};
  }
  return {
    boxShadow: "inset 0px 0px 0px 1px white",
    fontWeight: "bold",
  };
};

const selectedStyleNotAvailable = (notAvailable) => {
  if (!notAvailable) {
    return {};
  }
  return {
    boxShadow: "inset 0px 0px 0px 1px white",
    fontWeight: "bold",
  };
};

const AvailableDash = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);
  const [availableState, setAvailableState] = useState(undefined);
  const [notAvailableState, setNotAvailableState] = useState(undefined);

  let inTeam = null;
  let playerId;
  try {
    playerId = userData.leagues.find(
      ({ leagueId: lId }) => lId === leagueId
    ).playerId;
  } catch (e) {
    // eslint-disable-next-line no-console
    console.log(e);
  }

  const { data } = useSWR(`getNextGame${leagueId}`, async () =>
    getNextGame({ leagueId })
  );

  if (!playerId) {
    return <></>;
  }

  if (!data) {
    return (
      <AvailableBox style={{ minHeight: "0px", height: "116px" }}>
        <LoadingBall fill="white" />
      </AvailableBox>
    );
  }

  if (!data.nextGame || !data.nextGame.id) {
    return <></>;
  }

  if (moment(data.nextGame.date).unix() < moment().unix()) {
    return <></>;
  }

  let available = false;
  let notAvailable = false;

  try {
    if (inTeam === null) {
      const inTeamOne = data.nextGame.teamOne.players.find(
        ({ id }) => id === playerId
      );
      const inTeamTwo = data.nextGame.teamTwo.players.find(
        ({ id }) => id === playerId
      );
      available = !!data.nextGame.available.find(({ id }) => id === playerId);
      notAvailable = !!data.nextGame.notAvailable.find(
        ({ id }) => id === playerId
      );

      if (availableState === undefined) {
        setAvailableState(available);
        setNotAvailableState(notAvailable);
      }
      inTeam = false;

      if (inTeamOne) {
        inTeam = data.nextGame.teamOne.name || "One";
      } else if (inTeamTwo) {
        inTeam = data.nextGame.teamTwo.name || "Two";
      }
    }
  } catch (err) {
    // eslint-disable-next-line no-console
    console.log(err);
  }

  const setAvailability = async (availabilityStatus) => {
    await updateAvailability({
      id: playerId,
      leagueId,
      status: availabilityStatus,
    });

    await Promise.all([
      mutate(`getAvailability${leagueId}`, () => null),
      mutate(`getTeams${leagueId}`, () => null),
      mutate(`getNextGame${leagueId}`),
    ]);
  };

  const dropout = async () => {
    Modal.confirm({
      className: "dropout-modal",
      title: "Are you sure you want to dropout?",
      icon: <ExclamationCircleOutlined />,
      content: "You will be removed from the team",
      okText: <span data-test-id="dropout-confirm">Dropout</span>,
      okType: "danger",
      cancelText: "Cancel",
      onOk: async () => {
        setAvailableState(false);
        setNotAvailableState(true);
        await updateAvailability({
          id: playerId,
          status: "NOT_AVAILABLE",
          leagueId,
        });
        await Promise.all([
          mutate(`getTeams${leagueId}`, () => null),
          mutate(`getAvailability${leagueId}`, () => null),
          mutate(`getNextGame${leagueId}`),
        ]);
      },
      onCancel() {
        // eslint-disable-next-line no-console
        console.log("cancel dropout");
      },
    });
  };

  return (
    <AvailableBox style={{ minHeight: "0px", height: "auto" }}>
      {!inTeam ? (
        <span data-test-id="team-info-message" style={{ fontSize: "14px" }}>
          Are you available to play in the next game?
        </span>
      ) : (
        <>
          <span data-test-id="team-info-message" style={{ fontSize: "14px" }}>
            You are on {inTeam}
            <br />
            If you need to drop out click dropout
          </span>
          <br />
        </>
      )}
      {!inTeam ? (
        <div style={{ paddingTop: "5px", paddingBottom: "10px" }}>
          <WhiteButton
            data-test-id={availableState ? "yes-selected" : "yes-not-selected"}
            type="text"
            style={{
              border: "none",
              ...selectedStyleAvailable(availableState),
            }}
            onClick={() => {
              setAvailableState(true);
              setNotAvailableState(false);
              setAvailability("AVAILABLE");
            }}
          >
            Yes
          </WhiteButton>
          <WhiteButton
            data-test-id={notAvailableState ? "no-selected" : "no-not-selected"}
            type="text"
            style={{
              border: "none",
              marginLeft: "10px",
              ...selectedStyleNotAvailable(notAvailableState),
            }}
            onClick={() => {
              setAvailableState(false);
              setNotAvailableState(true);
              setAvailability("NOT_AVAILABLE");
            }}
          >
            No
          </WhiteButton>
        </div>
      ) : (
        <WhiteButton
          data-test-id="dropout"
          data-test-id-state={available ? "selected" : "not-selected"}
          onClick={dropout}
        >
          Dropout
        </WhiteButton>
      )}
    </AvailableBox>
  );
};

export default AvailableDash;
