/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { Avatar, Upload } from "antd";
import ImgCrop from "antd-img-crop";
import { CameraOutlined } from "@ant-design/icons";

import generateColor from "../../util/generateColor";
import { setProfilePicture } from "../../services/api-service";
import UserContext from "../../contexts/UserContext";
import log from "../../services/logger";

const imgSize = 200;

const PlayerAvatar = ({
  name,
  picture = undefined,
  noMargin = false,
  style = {},
  size = "default",
  upload = false,
}) => {
  const { userData, updateUserData } = useContext(UserContext);

  const uploadAction = (file) =>
    new Promise((resolve) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        try {
          const imageObj = new Image();
          const canvas = document.createElement("CANVAS");

          const ctx = canvas.getContext("2d");
          ctx.canvas.width = imgSize;
          ctx.canvas.height = imgSize;

          imageObj.onload = async () => {
            ctx.drawImage(imageObj, 0, 0, imgSize, imgSize);
            const dataUrl = canvas.toDataURL();
            await setProfilePicture({ picture: dataUrl });
            await updateUserData({ ...userData, picture: dataUrl });
            resolve(dataUrl);
          };

          imageObj.src = reader.result.toString();
        } catch (err) {
          log.error({
            message:
              "Failed to load user profile image when user was trying to upload a new one.",
            err,
          });
        }
      };
      reader.readAsDataURL(file);
    });

  const avatar = () => (
    <Avatar
      src={picture}
      style={{
        marginRight: noMargin ? 0 : "10px",
        ...generateColor(name).style,
        ...style,
      }}
      size={size}
      referrerpolicy="no-referrer"
    >
      {!picture && name ? name.charAt(0).toUpperCase() : ""}
    </Avatar>
  );

  if (upload) {
    return (
      <ImgCrop rotate>
        <Upload
          name="file"
          action={uploadAction}
          listType="picture"
          showUploadList={false}
        >
          <div style={{ cursor: "pointer", width: "130px" }}>
            {avatar()}
            <Avatar
              style={{
                backgroundColor: "#3b5998",
                float: "right",
                marginTop: "-32px",
              }}
              icon={<CameraOutlined />}
            />
          </div>
        </Upload>
      </ImgCrop>
    );
  }

  return avatar();
};

export default PlayerAvatar;
