/* eslint-disable react/prop-types */
import { Menu, Button, Drawer, Typography, Input, Form } from "antd";
import { MenuOutlined, PlusCircleOutlined } from "@ant-design/icons";
import React, { useState, useContext } from "react";
import { NavLink, useLocation, useParams, useNavigate } from "react-router-dom";

import { buildId } from "../../../util/config";

import findUsersLeague from "../../../util/findUsersLeague";
import PlayerAvatar from "../../atoms/PlayerAvatar";
import "./navbar.css";
import UserContext from "../../../contexts/UserContext";

const { Title } = Typography;

const titleStyle = {
  width: "100%",
  // position: 'fixed',
  top: 0,
  textAlign: "center",
  fontSize: "18px",
  paddingTop: "12px",
  fontWeight: "bold",
  color: "#187499",
};

/** @type {React.CSSProperties} */
const logoStyle = {
  fontSize: "100px",
  color: "white",
  // lineHeight: '100px',
  verticalAlign: "middle",
  display: "inline-block",
  width: "80%",
  padding: "0 50px",
  backgroundImage: "url(/playon-logo.png)",
  backgroundSize: "contain",
  backgroundRepeat: "no-repeat",
  backgroundPosition: "center",
  height: "34px",
  marginTop: "-5px",
};

const PageTitle = ({ pathname, leagueId, userData }) => {
  let title = "";

  const urlParts = pathname.split("/");

  if (pathname === "/") {
    title = "Dashboard";
  }

  if ([2, 3].includes(urlParts.length)) {
    if (["league", "dashboard"].includes(urlParts[1])) {
      try {
        const league = findUsersLeague(userData, leagueId);
        title = league.leagueName;
      } catch {
        title = "Dashboard";
      }
    }

    if (urlParts[1] === "features" || urlParts[1] === "all-features") {
      title = <div style={logoStyle} />;
    }

    if (urlParts[1] === "new-group") {
      title = "New Group";
    }

    if (urlParts[1] === "sign-up") {
      title = "Log In";
    }

    if (urlParts[1] === "log-in") {
      title = "Log In";
    }
  }

  if (urlParts.length === 4) {
    if (urlParts[3] === "availability") {
      title = "Availability";
    }

    if (urlParts[3] === "payment-management") {
      title = "Payment Management";
    }

    if (urlParts[3] === "fixtures") {
      title = "Fixtures";
    }

    if (urlParts[3] === "game-history") {
      title = "Game History";
    }

    if (urlParts[3] === "invite-player") {
      title = "Invite Player";
    }

    if (urlParts[3] === "game-management") {
      title = "Game Management";
    }

    if (urlParts[3] === "player-management") {
      title = "Player Management";
    }
  }

  if (urlParts[3] === "highlights") {
    title = "Highlights";
  }

  if ([4, 5, 6, 7].includes(urlParts.length)) {
    if (urlParts[3] === "player-stats") {
      title = "Player Stats";
    }
  }

  // if (urlParts.length === 6 && urlParts[3] === 'player-stats')
  //   title = pageTitle

  return <Title style={titleStyle}>{title}</Title>;
};

const Navbar = () => {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const { userData, setLoginVisible } = useContext(UserContext);
  const { leagueId, pageName } = useParams();

  const userHasLeagues =
    userData && userData.leagues && userData.leagues.length;

  const menuKeys = [
    ...(userHasLeagues ? userData.leagues.map(({ name }) => name) : []),
    "new-group",
  ];

  const selectedMenuItem = menuKeys.find((key) => pathname.includes(key));

  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  const menuItem = (key, to, text, Icon) => (
    <Menu.Item icon={<Icon />} data-test-id={`menu-${key}`} key={key}>
      <NavLink to={to}>{text}</NavLink>
    </Menu.Item>
  );

  const userLeagues = () =>
    userData.leagues.map(({ leagueId: lId, leagueName }) =>
      menuItem(lId, `/league/${lId}`, leagueName, () => (
        <PlayerAvatar size="small" name={leagueName} />
      ))
    );

  const menuLinks = () => [
    ...(userHasLeagues ? userLeagues() : []),
    menuItem("new-group", "/new-group", "Create a new group", () => (
      <PlusCircleOutlined
        style={{
          fill: `rgba(0, 0, 0, 0.65)`,
          fontSize: "24px",
          marginTop: "-2px",
          verticalAlign: "middle",
        }}
      />
    )),
  ];

  return (
    <div style={{ height: "48px" }}>
      <div
        style={{
          position: "fixed",
          top: "0",
          width: "100%",
          zIndex: 20,
          display: "inline-block",
          height: "48px",
          background: "white",
          borderBottom: "1px solid rgb(240, 240, 240)",
        }}
      >
        <PageTitle
          pathname={pathname}
          leagueId={leagueId}
          pageName={pageName}
          userData={userData}
        />
        <Button
          type="text"
          data-test-id="mobile-menu-button"
          className="mobile-menu-button"
          onClick={toggleMenu}
          style={{
            left: "0",
            top: "0",
            paddingTop: "10px",
            paddingLeft: "20px",
            height: "48px",
            width: "100px",
            textAlign: "left",
          }}
        >
          <MenuOutlined style={{ fontSize: "18px" }} />
        </Button>
        <Button
          type="text"
          data-test-id="open-login"
          className="mobile-menu-button"
          onClick={() => {
            if (!userData) {
              navigate("/log-in");
            } else {
              setLoginVisible(true);
            }
          }}
          style={{ right: "0", top: "0", height: "48px", paddingTop: "3px" }}
        >
          {userData && userData.name && (
            <PlayerAvatar
              name={userData.name}
              picture={userData.picture}
              noMargin
              style={{ verticalAlign: "top" }}
            />
          )}
          {userData && !userData.name && (
            <span style={{ width: "100%", height: "100%", paddingTop: "10px" }}>
              Profile
            </span>
          )}
          {!userData && (
            <span style={{ width: "100%", height: "100%", paddingTop: "10px" }}>
              Log in
            </span>
          )}
        </Button>
        <Drawer
          placement="left"
          visible={menuOpen}
          bodyStyle={{ padding: 0 }}
          onClose={toggleMenu}
          maskStyle={{ backgroundColor: "rgba(0, 0, 0, 0.75)" }}
        >
          <Menu
            onClick={toggleMenu}
            mode="inline"
            selectedKeys={[selectedMenuItem]}
          >
            <Menu.Item style={{ marginBottom: "25px" }}>
              <div
                style={{
                  backgroundImage: `url("/playon-logo.png")`,
                  backgroundSize: "contain",
                  backgroundRepeat: "no-repeat",
                  height: "80%",
                  marginTop: "10px",
                }}
              />
            </Menu.Item>
            {menuLinks()}
            <JoinLeaguePinInput />
            <p
              style={{
                position: "absolute",
                bottom: 0,
                padding: "20px",
                width: "100%",
                textAlign: "center",
              }}
            >
              (version <span data-test-id="build-id">{buildId}</span>)
            </p>
          </Menu>
        </Drawer>
      </div>
    </div>
  );
};

const JoinLeaguePinInput = () => {
  const navigate = useNavigate();

  const joinGroupWithPin = ({ "Group Pin": pin }) => {
    // eslint-disable-next-line no-console
    navigate(`/join/${pin}`);
  };

  return (
    <div style={{ padding: "20px" }}>
      <p>Join an existing group using a pin</p>
      <Form
        name="Join Group"
        onFinish={joinGroupWithPin}
        style={{
          width: "100%",
          maxWidth: 300,
          textAlign: "center",
          display: "inline-block",
          paddingTop: "20px",
        }}
      >
        <Form.Item
          name="Group Pin"
          rules={[
            {
              required: true,
              message: "Enter the group pin",
            },
          ]}
        >
          <Input placeholder="Group Pin" data-test-id="pin-input" />
        </Form.Item>
        <Form.Item>
          <Button
            type="primary"
            htmlType="submit"
            block
            data-test-id="submit-group-pin"
          >
            Join Group
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default Navbar;
