/* eslint-disable consistent-return */
/* eslint-disable react/prop-types */
import React, { useState } from "react";

import copy from "copy-text-to-clipboard";

import { Button } from "antd";
import { LinkOutlined, UserAddOutlined } from "@ant-design/icons";

import { getLeague } from "../../services/api-service";
import { containerStyle } from "../countdownStyles";

const DebugText = ({ inviteClicked }) => (
  <>
    <div style={{ display: "none" }} data-test-id="invite-link">
      {inviteClicked}
    </div>
    <div style={{ display: "none" }} data-test-id="invite-copied">
      {inviteClicked ? "true" : "false"}
    </div>
  </>
);

const InviteLink = ({ leagueId, block, style = {}, leagueName, dashboard }) => {
  const [inviteClicked, setInviteClicked] = useState("");

  const generateInvite = async () => {
    const { inviteLink: link } = await getLeague(leagueId);

    if (navigator.share === undefined) {
      copy(link);
      return setInviteClicked(link);
    }

    const title = "Who's Game";
    const text = `Join the ${leagueName} group`;

    try {
      await navigator.share({ title, text, url: link });
    } catch (err) {
      copy(link);
    }
  };

  if (dashboard) {
    const Container = ({ children }) => (
      <Button
        onClick={generateInvite}
        className="gradient-background main-fader"
        data-test-id="invite-player-dash-container"
        style={{ ...containerStyle, minHeight: "100px" }}
      >
        <div style={{ width: "100%" }}>{children}</div>
      </Button>
    );
    return (
      <Container leagueId={leagueId}>
        <UserAddOutlined style={{ color: "white", fontSize: "50px" }} />
        <h2
          style={{
            color: "white",
            fontSize: "12px",
            fontWeight: "bold",
            paddingTop: "12px",
          }}
        >
          {!inviteClicked
            ? "Invite Player to League"
            : "Invite copied to clipboard"}
        </h2>
        <DebugText inviteClicked={inviteClicked} />
      </Container>
    );
  }

  return (
    <>
      <Button
        style={{ ...style, width: "100%", maxWidth: "300px" }}
        block={block}
        type="primary"
        data-test-id="copy-invite-link"
        onClick={generateInvite}
      >
        <LinkOutlined />{" "}
        {!inviteClicked
          ? "Invite Player to League"
          : "Invite copied to clipboard"}
      </Button>
      <DebugText inviteClicked={inviteClicked} />
    </>
  );
};

export default InviteLink;
