/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable react/prop-types */
import React from "react";
import "./teamSelectionCard.css";
import { Col, Row, Typography, List, Modal } from "antd";
import { InfoCircleOutlined } from "@ant-design/icons";
import PlayerAvatar from "../atoms/PlayerAvatar";

const { Title, Text } = Typography;

const MyList = ({ players }) => (
  <Col span={12} style={{ paddingBottom: "20px" }}>
    <List
      style={{ display: "inline-block", textAlign: "left", maxWidth: "100%" }}
      size="small"
      split={false}
      dataSource={players}
      renderItem={(player, index) => (
        <List.Item
          style={{ textAlign: "left", padding: "8px" }}
          data-test-id={`waiting-list-player-${index}`}
        >
          {player.name ? (
            <Text ellipsis style={{ maxWidth: "160px", color: "white" }}>
              <PlayerAvatar
                name={player.name}
                picture={player.picture}
                size="small"
              />
              {player.name}
            </Text>
          ) : (
            <Text ellipsis style={{ maxWidth: "160px" }}>
              <PlayerAvatar name="? " size="small" />
              TBC
            </Text>
          )}
        </List.Item>
      )}
      data-test-id="fixtures-available-players"
    />
  </Col>
);

const PlayersCardText = ({
  teamNumber,
  title = null,
  players,
  winPercentage = null,
  ranking = null,
}) => {
  if (!title) {
    const listOne = players.filter((_, i) => i % 2 === 0);
    const listTwo = players.filter((_, i) => i % 2 !== 0);

    if (listOne.length === 0) {
      return <></>;
    }

    return (
      <>
        <Row>
          {listOne.length > 0 && <MyList players={listOne} colNum="1" />}
          {listTwo.length > 0 && <MyList players={listTwo} colNum="2" />}
        </Row>
      </>
    );
  }

  const size = players.length;

  const displayPlayers = players;

  for (let index = 0; index < size; index += 1) {
    if (!displayPlayers[index]) {
      displayPlayers[index] = {};
    }
  }

  const showRankingInfo = () => {
    Modal.info({
      title: "Team Ranking",
      content: (
        <div>
          <p>
            Team Ranking is a balanced metric that takes into account the
            percentage of games played as well as the win percentage.
          </p>
          <p>A higher ranking is better.</p>
        </div>
      ),
      onOk() {
        // eslint-disable-next-line no-console
        console.log("Ok");
      },
    });
  };

  // if (!displayPlayers.find((x) => x && x.name)) {
  //   return <></>;
  // }

  return (
    <Col span={12}>
      <List
        style={{ display: "inline-block", textAlign: "left", maxWidth: "100%" }}
        size="small"
        header={
          title && (
            <div>
              <Title
                level={4}
                style={{
                  paddingLeft: "10px",
                  marginBottom: "0px",
                  color: "white",
                }}
              >
                {title}
              </Title>
            </div>
          )
        }
        split={false}
        footer={
          <div style={{ paddingLeft: "10px" }}>
            <Text strong style={{ color: "white" }}>
              Team Win:{" "}
            </Text>
            <Text
              data-test-id={`team-${teamNumber}-WinPercentage`}
              style={{ color: "white" }}
            >
              {winPercentage.toFixed(0)}%
            </Text>
            <br />
            <div
              onClick={showRankingInfo}
              onKeyDown={showRankingInfo}
              role="button"
              tabIndex={0}
            >
              <Text strong style={{ color: "white" }}>
                Team Ranking:{" "}
              </Text>
              <Text
                style={{ color: "white" }}
                data-test-id={`team-${teamNumber}-Ranking`}
              >
                {ranking.toFixed(0)}{" "}
                <InfoCircleOutlined
                  style={{
                    fontSize: "12px",
                    verticalAlign: "top",
                    color: "white",
                  }}
                />
              </Text>
            </div>
          </div>
        }
        dataSource={displayPlayers}
        renderItem={(player, index) => (
          <List.Item
            style={{ textAlign: "left", padding: "8px" }}
            data-test-id={`team-${teamNumber}-player-${index}`}
          >
            {player.name ? (
              <Text ellipsis style={{ maxWidth: "160px", color: "white" }}>
                <PlayerAvatar
                  name={player.name}
                  picture={player.picture}
                  size="small"
                />
                {player.name}
              </Text>
            ) : (
              <Text ellipsis style={{ maxWidth: "160px", color: "white" }}>
                <PlayerAvatar name="? " size="small" />
                TBC
              </Text>
            )}
          </List.Item>
        )}
        data-test-id={`team-${teamNumber}-selection-list`}
      />
    </Col>
  );
};

export default PlayersCardText;
