import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";

import { Form, Button } from "antd";
import { GoogleCircleFilled } from "@ant-design/icons";

import {
  signInWithPopup,
  GoogleAuthProvider,
  fetchProvidersForEmail,
  fetchSignInMethodsForEmail,
} from "firebase/auth";
import UserContext from "../../contexts/UserContext";
import { getUserData } from "../../services/api-service";
import { useEmulator } from "../../util/config";
import { auth } from "../../util/firebase";

const googleProvider = new GoogleAuthProvider();

const googleLogin =
  ({
    setUserToken,
    updateUserData,
    navigate,
    setAlternativeLogin,
    setExtraLoginInfo,
    extraLoginInfo,
  }) =>
    async () => {
      try {
        const result = await signInWithPopup(auth, googleProvider);
        const { user } = result;

        // This gives you a Google Access Token. You can use it to access the Google API.
        const credential = GoogleAuthProvider.credentialFromResult(result);
        const { accessToken } = credential;

        console.log({ user, accessToken, credential });

        setUserToken(accessToken);

        const userDataResponse = await getUserData();

        const userData =
          userDataResponse &&
          userDataResponse.data &&
          userDataResponse.data.getUserData;

        console.log({ userData });

        if (userData) {
          updateUserData(userData);

          if (userData.leagues[0]) {
            const { leagueId } = userData.leagues[0];
            navigate(`/league/${leagueId}`);
          }
          return;
        }

        console.log({ extraLoginInfo });

        if (!extraLoginInfo.name && user.displayName) {
          console.log("setting extraLoginInfo");

          let picture;
          if (user.photoURL) {
            // this gets a larger picture from google
            picture = user.photoURL.replace(/(.+)(?:=.*)$/, "$1=s250");

            console.log(picture);
          }

          const newExtraLoginInfo = {
            ...extraLoginInfo,
            name: user.displayName,
            picture,
          };

          console.log({ newExtraLoginInfo });

          setExtraLoginInfo(newExtraLoginInfo);
        }

        navigate(`/user-details`);
        return;
      } catch (error) {
        console.log({ error });

        try {
          // Handle Errors here.
          const errorCode = error.code;
          const errorMessage = error.message;
          // The email of the user's account used.
          const { email } = error.customData;
          // The AuthCredential type that was used.
          const credential = GoogleAuthProvider.credentialFromError(error);

          if (errorCode === "auth/account-exists-with-different-credential") {
            try {
              const methods = await fetchSignInMethodsForEmail(auth, email);
              console.log(methods);
              if (methods[0] === "emailLink") {
                setAlternativeLogin({ method: "Email", email });
              } else if (methods[0] === "facebook.com") {
                setAlternativeLogin({ method: "Facebook", email });
              }
              // Asks the user their password.
              // In real scenario, you should handle this asynchronously.
              // const password = promptUserForPassword(); // TODO: implement promptUserForPassword.
              // getAuth()
              //   .signInWithEmailAndPassword(email, password)
              //   .then((result) =>
              //     // Step 4a.
              //     result.user.linkWithCredential(pendingCred)
              //   )
              //   .then(() => {
              //     // Google account successfully linked to the existing Firebase user.
              //     goToApp();
              //   });
            } catch (error3) {
              console.log(error3);
            }
          }
        } catch (error4) {
          console.log(error4);
        }
      }
    };

const GoogleLoginInput = ({ setAlternativeLogin }) => {
  const navigate = useNavigate();

  const {
    updateUserData,
    setUserToken,
    setExtraLoginInfo,
    extraLoginInfo,
    // setLoginVisible
  } = useContext(UserContext);

  return (
    <Form.Item>
      <Button
        onClick={googleLogin({
          setUserToken,
          updateUserData,
          navigate,
          setAlternativeLogin,
          setExtraLoginInfo,
          extraLoginInfo,
        })}
        name="Continue with Facebook"
        icon={<GoogleCircleFilled />}
        size="large"
        block
        type="primary"
        style={{
          color: "black",
          background: "white",
          borderColor: "black",
          maxWidth: "300px",
          textAlign: "left",
          padding: "6.4px 20px 6.4px 20px",
        }}
      >
        <p
          style={{
            maxWidth: "100%",
            textAlign: "center",
            display: "inline-block",
            width: "calc(100% - 32px)",
          }}
        >
          Continue with Google
        </p>
      </Button>
    </Form.Item>
  );
};

export default GoogleLoginInput;
