/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { useParams } from "react-router-dom";
import InviteLink from "../atoms/InviteLink";
import UserContext from "../../contexts/UserContext";

const InvitePlayerDash = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);

  let leagueName;

  try {
    leagueName = userData.leagues.find(
      ({ leagueId: x }) => x === leagueId
    ).leagueName;
  } catch (e) {
    // eslint-disable-next-line no-console
    console.log(e);
  }

  return <InviteLink dashboard leagueId={leagueId} leagueName={leagueName} />;
};

export default InvitePlayerDash;
