/* eslint-disable no-shadow */
/* eslint-disable react/prop-types */
import React from "react";
import { Row, Col, List, Typography } from "antd";
import PlayerAvatar from "../atoms/PlayerAvatar";

const { Text, Title } = Typography;

const results = {
  draw: "Result Draw",
  void: "Result Void",
  pending: "Pending Game",
};

const formatTeams = (render) => (game, index) => {
  const thereIsAWin = ["team-one-wins", "team-two-wins"].includes(game.result);
  const teamOneWin = thereIsAWin && game.result === "team-one-wins";

  const teams = [
    {
      title: thereIsAWin ? "Winning Team" : results[game.result],
      players: teamOneWin ? game.teamOne : game.teamTwo,
    },
    {
      title: thereIsAWin ? "Losing Team" : results[game.result],
      players: teamOneWin ? game.teamTwo : game.teamOne,
    },
  ];

  return render({ game, teams }, index);
};

const border = {
  boxShadow: `0 1px 1px rgba(0,0,0,0.11), 
0 2px 2px rgba(0,0,0,0.11), 
0 4px 4px rgba(0,0,0,0.11), 
0 6px 8px rgba(0,0,0,0.11),
0 8px 16px rgba(0,0,0,0.11)`,
};

const GameCards = ({ games }) => (
  <section className="games-section">
    {games.map(
      formatTeams(({ game, teams }, index) => (
        <div
          ref={game.ref}
          key={game._id}
          data-test-id={`game-${index}`}
          style={{ paddingTop: "30px" }}
        >
          <Title level={4} style={{ fontWeight: "normal", fontSize: "18px" }}>
            {game.dateText}
          </Title>
          <Row gutter={[16, 16]}>
            {teams.map(({ title, players }, index) => (
              <Col
                span={12}
                key={`${game._id}-${title}`}
                className="team-table"
              >
                <List
                  className="gradient-background"
                  style={{ color: "white", ...border }}
                  split={false}
                  data-test-id={`team-${index}-${title}`}
                  size="small"
                  header={title}
                  bordered
                  dataSource={players}
                  renderItem={(player) => (
                    <List.Item style={{ textAlign: "left" }}>
                      <Text ellipsis style={{ color: "white" }}>
                        <PlayerAvatar
                          name={player.name}
                          picture={player.picture}
                          size="small"
                        />
                        {player.name}
                      </Text>
                    </List.Item>
                  )}
                />
              </Col>
            ))}
          </Row>
        </div>
      ))
    )}
  </section>
);

export default GameCards;
