/* eslint-disable react/prop-types */
import React, { useState } from "react";
import useSWR from "swr";

import { Result } from "antd";
import GameCards from "../components/GameCards/GameCards";
import GameDateSelection from "../components/GameDateSelection";
import getGames from "../services/getGames";
import Loading from "../components/atoms/Loading";

const Games = ({ leagueId }) => {
  const [selectedDate, setSelectedDate] = useState(null);

  const { data } = useSWR(`${leagueId}_games`, getGames({ leagueId }));

  let showZero;
  if (data === "getGames failed") {
    showZero = true;
  }

  if (showZero) {
    return (
      <Result
        data-test-id="play-message"
        extra="Games will appear after the first game has been played"
      />
    );
  }

  if (!data || !data.games) {
    return <Loading />;
  }

  const nonVoidGames = data.games.filter(({ result }) => result !== "void");

  const gamesWithDate = nonVoidGames.map((game) => {
    const [month, year] = game.dateMonthYear.split(" ");
    return { ...game, month, year };
  });

  if (data && selectedDate === null && gamesWithDate[0]) {
    setSelectedDate({
      month: gamesWithDate[0].month,
      year: gamesWithDate[0].year,
    });
  }

  const getMonthsForYear = (yearToMatch) =>
    gamesWithDate
      .filter(({ year }) => year === yearToMatch)
      .map(({ month }) => month);

  if (!selectedDate) {
    return (
      <Result
        data-test-id="play-message"
        extra="Games will appear after the first game has been played"
      />
    );
  }

  const gamesForMonth = gamesWithDate.filter(
    ({ year, month }) =>
      year === selectedDate.year && month === selectedDate.month
  );

  const gamesForMonthInOrder = gamesForMonth.sort(
    (a, b) => new Date(b.date) - new Date(a.date)
  );

  const availableYears = [...new Set(gamesWithDate.map(({ year }) => year))];
  const availableMonths = getMonthsForYear(selectedDate.year);

  const setYear = (year) => {
    const months = getMonthsForYear(year);
    setSelectedDate({ month: months[0], year });
  };

  const setMonth = (month) => {
    setSelectedDate({ year: selectedDate.year, month });
  };

  return (
    <section
      className="data-view game-history-page"
      style={{ paddingBottom: "100px" }}
    >
      <p>
        View the results of all the games played. Select which month you want to
        see the games for using the dropdown below.
      </p>
      <GameDateSelection
        games={gamesWithDate}
        selectedDate={selectedDate}
        setYear={setYear}
        setMonth={setMonth}
        availableYears={availableYears}
        availableMonths={availableMonths}
      />
      <GameCards games={gamesForMonthInOrder} />
    </section>
  );
};

export default Games;

export const GameHistoryPageTitle = "Game History";
