/* eslint-disable react/prop-types */
import React from "react";
import { Typography } from "antd";

const { Link } = Typography;

const FootyStatsHero = ({
  noLogo = false,
  hero = "field",
  heroHeight = "200px",
  content = undefined,
}) => {
  const backgroundImages = {
    field: "url(/dashboard-pictures/field.jpg)",
    grass: "url(/hero.jpg)",
  };

  const img = (name) => ({ backgroundImage: backgroundImages[name] });

  /** @type {React.CSSProperties} */
  const heroStyle = {
    height: heroHeight,
    textAlign: "center",
    backgroundPosition: "center",
    backgroundSize: "cover",
    lineHeight: heroHeight,
  };

  /** @type {React.CSSProperties} */
  const titleStyle = {
    fontSize: "100px",
    color: "white",
    lineHeight: "100px",
    verticalAlign: "middle",
    display: "inline-block",
    width: "80%",
    padding: "0 50px",
    backgroundImage: "url(/playon-logo.png)",
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    height: "80px",
  };

  return (
    <Link href="/">
      <div style={{ ...heroStyle, ...img(hero) }}>
        {!noLogo && <div style={titleStyle} />}
        {content}
      </div>
    </Link>
  );
};

export default FootyStatsHero;
