/* eslint-disable react/prop-types */
import React from "react";
import { Card, Typography, Avatar } from "antd";
import { useParams } from "react-router-dom";

import PlayerAvatar from "./atoms/PlayerAvatar";
import "./mobileCards.css";
import generateColor from "../util/generateColor";

const { Text } = Typography;

/** @return {React.CSSProperties} */
const mobileCardStyle = (player) => {
  const { color } = generateColor(player.name);

  return {
    border: "none",
    marginTop: "1rem",
    textAlign: "left",
    borderRadius: "8px",
    background: `linear-gradient(to right, ${color[4]}, ${color[6]})`,
  };
};

const MobileCards = ({ playerStats = [], columns }) => {
  const { leagueId, year } = useParams();

  let defaultYear;
  if (!year) {
    defaultYear = new Date().getFullYear().toString();
  } else {
    defaultYear = year;
  }

  return (
    <>
      {playerStats.map((player) => (
        <a
          key={player.id}
          href={`/#/league/${leagueId}/player-stats/${defaultYear}/${player.id}`}
        >
          <Card
            size="small"
            className="mobile-stats"
            style={mobileCardStyle(player)}
            data-test-id={`stats-${player.name}`}
          >
            <Text strong style={{ color: "white" }}>
              <PlayerAvatar name={player.name} picture={player.picture} />
              {player.name}
            </Text>
            <Text strong style={{ color: "white", float: "right" }}>
              Rank{" "}
              <Avatar
                style={{
                  marginRight: "5px",
                  marginLeft: "5px",
                  ...generateColor(player.name).style,
                }}
                size="default"
              >
                {player.rankingNum}
              </Avatar>
            </Text>
            <table
              data-test-id={`stats-${player.name}`}
              style={{
                width: "100%",
                textAlign: "center",
                color: "white",
                marginTop: "10px",
              }}
            >
              <thead>
                <tr>
                  {columns.map(({ title, key }) => (
                    <th key={`title-${key}`}>{title}</th>
                  ))}
                </tr>
              </thead>
              <tbody>
                <tr>
                  {columns.map(({ dataIndex, key, format, testId }) => (
                    <td
                      data-test-id={`${player.name.replace(
                        /\s+/g,
                        "-"
                      )}-${testId}`}
                      key={`data-${key}`}
                    >
                      {format ? format(player[dataIndex]) : player[dataIndex]}
                    </td>
                  ))}
                </tr>
              </tbody>
            </table>
          </Card>
        </a>
      ))}
    </>
  );
};

export default MobileCards;
