/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
import React from "react";
import useSWR, { mutate } from "swr";
import { Typography, Button, Modal, Card } from "antd";
import moment from "moment";

import { containerStyle } from "../components/countdownStyles";
import PlayerPayments from "../components/PlayerPayments";
import Loading from "../components/atoms/Loading";
import { getPayments, updatePayment } from "../services/api-service";
import WhiteButton from "../components/atoms/WhiteButton";
import PlayerAvatar from "../components/atoms/PlayerAvatar";
import generateColor from "../util/generateColor";

/** @return {React.CSSProperties} */
const mobileCardStyle = (player) => {
  const { color } = generateColor(player.name);

  return {
    border: "none",
    marginTop: "1rem",
    textAlign: "left",
    borderRadius: "8px",
    background: `linear-gradient(to right, ${color[4]}, ${color[6]})`,
  };
};

const { confirm } = Modal;
const { Title } = Typography;

const asMoney = (value) => `${(Math.abs(value) / 100).toFixed(2)}`;

const PaymentManagement = ({ leagueId }) => {
  const { data } = useSWR(`${leagueId}_payments`, () =>
    getPayments({ leagueId })
  );

  if (!data) {
    return <Loading />;
  }

  const playersWithBalance = data.map((player) => {
    const nonPendingPayments = player.payments
      ? player.payments.filter(({ pending }) => !pending)
      : [];
    const balance = nonPendingPayments.reduce((a, { amount }) => a + amount, 0);

    return { ...player, balance };
  });

  const withPaid = playersWithBalance.map((player) => {
    if (player.payments && player.payments.length > 0) {
      const owesInOrder = player.payments
        .filter(({ amount }) => amount < 0)
        .sort((a, b) => new Date(a.date) - new Date(b.date));
      const credits = player.payments.filter(({ amount }) => amount > 0);

      let remaining = credits.reduce((a, { amount }) => a + amount, 0);

      const paidOrNot = owesInOrder.map((payment) => {
        remaining += payment.amount;
        const paid = remaining >= 0;

        return { ...payment, paid };
      });

      return { ...player, payments: [...paidOrNot, ...credits] };
    }

    return player;
  });

  const owesList = withPaid
    .filter(({ balance }) => balance < 0)
    .sort((a, b) => a.name.localeCompare(b.name));
  const equalList = withPaid
    .filter(({ balance }) => balance === 0)
    .sort((a, b) => a.name.localeCompare(b.name));
  const creditList = withPaid
    .filter(({ balance }) => balance > 0)
    .sort((a, b) => a.name.localeCompare(b.name));

  const totalBalance =
    owesList.reduce((a, { balance }) => a + balance, 0) +
    creditList.reduce((a, { balance }) => a + balance, 0);

  const totalCredit = creditList.reduce((a, { balance }) => a + balance, 0);
  const totalDebit = owesList.reduce((a, { balance }) => a + balance, 0);

  const pendingTransactions = withPaid.reduce(
    (a, { id, name, picture, payments }) => {
      const pendingPayments = payments
        ? payments.filter(({ pending }) => !!pending)
        : [];

      a.push(
        ...pendingPayments.map((payment) => ({ id, name, picture, payment }))
      );

      return a;
    },
    []
  );

  return (
    <section
      className="data-view payments-management-page"
      style={{
        paddingBottom: "100px",
      }}
    >
      <p>
        Manage players payments. When a game is created with teams then the
        payments are updated to show the cost of that latest game.
      </p>
      <Title style={{ paddingTop: "10px" }} level={4}>
        Payment Summary
      </Title>
      <div
        style={{ ...containerStyle, minHeight: "0", flexDirection: "column" }}
        className="gradient-background main-fader"
        data-test-id="payment-summary"
      >
        <div
          style={{
            width: "100%",
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-around",
          }}
        >
          <div data-test-id="total-credit-summary-text">
            <p style={{ margin: "0px" }}>Total Credit:</p>
            <span
              data-test-id="total-credit-amount"
              style={{ fontSize: "20px" }}
            >
              {asMoney(totalCredit)}
            </span>
          </div>
          <div data-test-id="total-debit-summary-text">
            <p style={{ margin: "0px" }}>Total Debit:</p>
            <span
              data-test-id="total-debit-amount"
              style={{ fontSize: "20px" }}
            >
              {asMoney(totalDebit)}
            </span>
          </div>
        </div>
        <div
          data-test-id="total-balance-summary-text"
          style={{ marginTop: "20px" }}
        >
          <p style={{ margin: "0px" }}>Your overall balance is:</p>
          <p style={{ margin: "0px" }}>
            <span
              data-test-id="total-balance-amount"
              style={{ fontSize: "20px" }}
            >
              {" "}
              {asMoney(totalBalance)}{" "}
            </span>
            in
            <span style={{ fontSize: "20px" }}>
              {totalBalance < 0 ? " debit" : " credit"}
            </span>
          </p>
        </div>
      </div>
      {/* <div style={{ textAlign: "left" }}>
        <Input
          placeholder="Find player"
          style={{ width: 200, borderRadius: "50px" }}
          prefix={<SearchOutlined />}
        />
      </div> */}
      {pendingTransactions.length > 0 && (
        <div data-test-id="pendingTransactionsList">
          <Title style={{ paddingTop: "30px" }} level={4}>
            Pending Transactions
          </Title>
          {pendingTransactions.map((player, index) => (
            <Card
              size="small"
              // className={`mobile-payments ${transition ? "slideDown" : ""}`}
              style={{
                ...mobileCardStyle(player),
                color: "white",
              }}
              data-test-id={`pending-payments-${index}`}
              key={player.id}
            >
              <p style={{ marginBottom: 0 }}>
                <PlayerAvatar name={player.name} picture={player.picture} />
                <span data-test-id="player-name">{player.name}</span>
              </p>
              <span style={{ paddingBottom: "15px", marginBottom: 0 }}>
                <span style={{ paddingLeft: "43px", paddingRight: "40px" }}>
                  {moment(player.payment.date).format("DD/MM/YYYY")}
                </span>
                {asMoney(player.payment.amount)}
              </span>
              <WhiteButton
                style={{
                  marginTop: "-4px",
                  marginRight: "10px",
                  float: "right",
                }}
                onClick={() => {
                  const modal = confirm();
                  modal.update({
                    title: `Has ${player.name} sent you ${asMoney(
                      player.payment.amount
                    )}?`,
                    okButtonProps: { style: { display: "none" } },
                    closable: true,
                    maskClosable: true,
                    content: (
                      <div style={{ textAlign: "center" }}>
                        <Button
                          data-test-id="select-credit"
                          style={{
                            maxWidth: "100px",
                            borderRadius: "50px",
                            marginRight: "20px",
                            marginTop: "10px",
                          }}
                          onClick={async () => {
                            await updatePayment({
                              playerId: player.id,
                              paymentId: player.payment.id,
                              action: "confirm",
                            });
                            mutate(`${leagueId}_payments`);
                            modal.destroy();
                          }}
                        >
                          Yes
                        </Button>
                        <Button
                          data-test-id="select-debit"
                          style={{
                            maxWidth: "100px",
                            borderRadius: "50px",
                            marginRight: "20px",
                            marginTop: "10px",
                          }}
                          onClick={async () => {
                            await updatePayment({
                              playerId: player.id,
                              paymentId: player.payment.id,
                              action: "reject",
                            });
                            mutate(`${leagueId}_payments`);
                            modal.destroy();
                          }}
                        >
                          No
                        </Button>
                      </div>
                    ),
                  });
                }}
              >
                Respond
              </WhiteButton>
            </Card>
          ))}
        </div>
      )}
      {!!owesList.length && (
        <div data-test-id="debitList">
          <Title style={{ paddingTop: "30px" }} level={4}>
            In Debit
          </Title>
          {owesList.map((player, index) => (
            <PlayerPayments
              key={player.id}
              leagueId={leagueId}
              player={player}
              index={index}
            />
          ))}
        </div>
      )}
      {!!creditList.length && (
        <div data-test-id="creditList">
          <Title style={{ paddingTop: "30px" }} level={4}>
            In Credit
          </Title>
          {creditList.map((player, index) => (
            <PlayerPayments
              key={player.id}
              leagueId={leagueId}
              player={player}
              index={index}
            />
          ))}
        </div>
      )}
      {!!equalList.length && (
        <div data-test-id="equalList">
          <Title style={{ paddingTop: "30px" }} level={4}>
            Up To Date
          </Title>
          {equalList.map((player, index) => (
            <PlayerPayments
              key={player.id}
              leagueId={leagueId}
              player={player}
              index={index}
            />
          ))}
        </div>
      )}
    </section>
  );
};

export default PaymentManagement;
