/* eslint-disable react/prop-types */
import React from "react";

import { Button, List, Divider, Row, Col, Tooltip, Typography } from "antd";
import randomiseTeams from "./randomiseTeams";
import { clearTeams, pickTeam } from "./updateGame";
import GameResultPicker from "../GameResultPicker";
import AccountIssue from "./AccountIssue";
import {
  calculateTeamWinPercentage,
  calculateTeamRanking,
} from "../../util/calculateWinPercentageAndRanking";

const { Title, Text } = Typography;

const TeamSelection = ({
  pendingGame,
  availablePlayers,
  updatePendingGameState,
  savePendingGame,
  deletePendingGame,
  loading,
  accountIssue,
  onFinish,
  inputPendingGame,
  hasPendingPlayers,
  userData,
  leagueId,
  nextGame,
}) => {
  /** @type {React.CSSProperties} */
  const statsTextStyle = {
    width: "95.5px",
    display: "block",
    fontWeight: "bold",
  };

  const teamOneCount = (pendingGame.teamOne && pendingGame.teamOne.length) || 0;
  const teamTwoCount = (pendingGame.teamTwo && pendingGame.teamTwo.length) || 0;

  const teamOneWinPercentage = calculateTeamWinPercentage(
    pendingGame.teamOne
  ).toFixed(0);
  const teamOneRanking = calculateTeamRanking(pendingGame.teamOne).toFixed(0);
  const teamTwoWinPercentage = calculateTeamWinPercentage(
    pendingGame.teamTwo
  ).toFixed(0);
  const teamTwoRanking = calculateTeamRanking(pendingGame.teamTwo).toFixed(0);

  const listColumns = [];

  if (hasPendingPlayers && nextGame && nextGame.date) {
    listColumns.push(
      ...availablePlayers.map((player) => {
        const inTeamOne = pendingGame.teamOne.find(
          ({ name }) => player.name === name
        );
        const inTeamTwo = pendingGame.teamTwo.find(
          ({ name }) => player.name === name
        );

        const teamOneDisabled = !inTeamOne && teamOneCount === pendingGame.size;
        const teamTwoDisabled = !inTeamTwo && teamTwoCount === pendingGame.size;

        let teamOneButtonType = "default";
        if (inTeamOne) {
          teamOneButtonType = "primary";
        } else if (teamOneDisabled) {
          teamOneButtonType = "dashed";
        }

        let teamTwoButtonType = "default";
        if (inTeamTwo) {
          teamTwoButtonType = "primary";
        } else if (teamTwoDisabled) {
          teamTwoButtonType = "dashed";
        }

        const teamOneButton = (
          <Button
            // disabled={teamOneDisabled}
            data-test-id="team-one-button"
            data-test-id-state={!!inTeamOne}
            type={teamOneButtonType}
            onClick={() =>
              pickTeam(player, "teamOne", pendingGame, updatePendingGameState)
            }
          >
            Team One
          </Button>
        );

        const teamTwoButton = (
          <Button
            // disabled={teamTwoDisabled}
            data-test-id="team-two-button"
            data-test-id-state={!!inTeamTwo}
            type={teamTwoButtonType}
            onClick={() =>
              pickTeam(player, "teamTwo", pendingGame, updatePendingGameState)
            }
          >
            Team Two
          </Button>
        );

        return {
          testId: player.name,
          name: player.name,
          actions: [teamOneButton, teamTwoButton],
        };
      })
    );
  }

  if (hasPendingPlayers && nextGame && nextGame.date) {
    listColumns.push(
      {
        testId: "player_count",
        name: <Text style={statsTextStyle}>Player Count</Text>,
        actions: [
          <Text
            style={statsTextStyle}
            data-test-id="Team-One-player-count"
            type={teamOneCount > pendingGame.size ? "danger" : undefined}
            strong={teamOneCount > pendingGame.size}
          >
            {teamOneCount}
          </Text>,
          <Text
            style={statsTextStyle}
            data-test-id="Team-Two-player-count"
            type={teamTwoCount > pendingGame.size ? "danger" : undefined}
            strong={teamTwoCount > pendingGame.size}
          >
            {teamTwoCount}
          </Text>,
        ],
      },
      {
        testId: "win_percentage",
        name: <Text style={statsTextStyle}>Win %</Text>,
        actions: [
          <Text style={statsTextStyle} data-test-id="Team-One-win-percentage">
            {teamOneWinPercentage}%
          </Text>,
          <Text style={statsTextStyle} data-test-id="Team-Two-win-percentage">
            {teamTwoWinPercentage}%
          </Text>,
        ],
      },
      {
        testId: "ranking",
        name: <Text style={statsTextStyle}>Ranking</Text>,
        actions: [
          <Text style={statsTextStyle} data-test-id="Team-One-ranking">
            {teamOneRanking}
          </Text>,
          <Text style={statsTextStyle} data-test-id="Team-Two-ranking">
            {teamTwoRanking}
          </Text>,
        ],
      }
    );
  }

  const resultIsSelected = [
    "team-one-wins",
    "team-two-wins",
    "draw",
    "void",
  ].includes(pendingGame.result);

  return (
    <>
      <Title level={4} style={{ textAlign: "left", paddingTop: "20px" }}>
        Team Selection
      </Title>
      <List
        data-test-id="team-selection-list"
        split={false}
        size="small"
        itemLayout="horizontal"
        dataSource={listColumns}
        style={{ textAlign: "left", paddingBottom: "10px" }}
        renderItem={({ name, actions }, index) => (
          <List.Item
            data-test-id={`team-selection-list-row-${index}`}
            actions={actions}
            style={{ padding: "8px 0" }}
          >
            <Text ellipsis style={{ maxWidth: "calc(100% - 245px)" }}>
              {name}
            </Text>
          </List.Item>
        )}
      />
      <Row gutter={[16, 16]} style={{ marginTop: "10px" }} justify="end">
        <Col span={24} sm={12}>
          <Button
            block
            onClick={() =>
              randomiseTeams(
                pendingGame,
                availablePlayers,
                updatePendingGameState
              )
            }
            data-test-id="randomise-teams"
          >
            Randomise Teams
          </Button>
        </Col>
        <Col span={24} sm={12}>
          <Button
            block
            onClick={() => clearTeams(pendingGame, updatePendingGameState)}
            data-test-id="clear-teams"
          >
            Clear Teams
          </Button>
        </Col>
        <Col span={24} sm={12}>
          <Button
            block
            data-test-id="delete-pending-game"
            danger
            onClick={deletePendingGame}
          >
            Delete Pending Game
          </Button>
        </Col>
        <Col span={24} sm={12}>
          <Button
            block
            data-test-id="save-pending-game"
            type="primary"
            onClick={savePendingGame}
            loading={loading[0]}
            disabled={accountIssue}
          >
            Save Pending Game
          </Button>
        </Col>
        <Col span={24}>
          <Divider style={{ color: "#aaa", margin: 0 }}>or</Divider>
        </Col>
        <Col span={24} sm={12}>
          <GameResultPicker
            teamOne={pendingGame.teamOne}
            teamTwo={pendingGame.teamTwo}
            setPendingGame={updatePendingGameState}
            pendingGame={pendingGame}
          />
        </Col>
        <Col span={24} sm={12}>
          <Tooltip title={resultIsSelected ? "" : "Select a result first"}>
            <Button
              block
              data-test-id="submit-result"
              type="primary"
              onClick={() =>
                onFinish({
                  pendingGame,
                  inputPendingGame,
                  hasPendingPlayers,
                })
              }
              loading={loading[1]}
              disabled={
                !["team-one-wins", "team-two-wins", "draw", "void"].includes(
                  pendingGame.result
                ) || accountIssue
              }
            >
              Submit Result
            </Button>
          </Tooltip>
        </Col>
        {accountIssue && (
          <AccountIssue userData={userData} leagueId={leagueId} />
        )}
      </Row>
    </>
  );
};

export default TeamSelection;
