/* eslint-disable no-console */
/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import useSWR from "swr";

import GameManagement from "./GameManagement/GameManagement";
import { getNextGame, getLeague } from "../services/api-service";
import UserContext from "../contexts/UserContext";
import Loading from "./atoms/Loading";
import findUsersLeague from "../util/findUsersLeague";

const TeamSelection = ({ leagueId }) => {
  const { userData } = useContext(UserContext);
  const { data, error } = useSWR(`getNextGame${leagueId}`, async () =>
    getNextGame({ leagueId })
  );
  const { data: leagueData } = useSWR(`getLeague${leagueId}`, () =>
    getLeague(leagueId)
  );

  if (error || !userData || !userData.leagues) {
    console.log({ error });
    console.log({ userData });
    throw new Error("issue in game management");
  }

  const { role } = findUsersLeague(userData, leagueId) || { role: "player" };

  if (!data || !leagueData) {
    return <Loading />;
  }

  if (!data.nextGame) {
    data.nextGame = {
      teamOne: {
        players: [],
        winPercentage: 0,
        ranking: 0,
      },
      teamTwo: {
        players: [],
        winPercentage: 0,
        ranking: 0,
      },
    };
  }

  const { nextGame } = data;
  const { available } = nextGame || { available: [] };

  return role === "admin" ? (
    <div style={{ paddingBottom: "150px" }}>
      <GameManagement
        availablePlayers={available}
        nextGame={nextGame}
        leagueData={leagueData}
      />
    </div>
  ) : (
    <div style={{ paddingBottom: "150px" }}>Admin Only</div>
  );
};

export default TeamSelection;

export const TeamSelectionPageTitle = "Game Management";
