/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { useNavigate } from "react-router-dom";

import { Modal } from "antd";
import UserContext from "../../contexts/UserContext";

import ResetPassword from "./ResetPassword";
import PasswordReset from "./PasswordReset";

const modalTitle = {
  resetPasswordExpired: "Reset password",
  resetPassword: "Reset password",
  passwordReset: "Password Reset",
};

const ModalContent = ({ modalState }) => {
  if (modalState === "resetPassword") {
    return <ResetPassword expired={false} />;
  }
  if (modalState === "resetPasswordExpired") {
    return <ResetPassword expired />;
  }
  if (modalState === "passwordReset") {
    return <PasswordReset />;
  }
  return <></>;
};

const Modals = () => {
  const { modalState, setModalState } = useContext(UserContext);
  const navigate = useNavigate();

  return (
    <Modal
      className="main-modal"
      visible={modalState}
      title={modalTitle[modalState]}
      closable
      onCancel={() => {
        setModalState("");
        navigate("/sign-up");
      }}
      footer={null}
    >
      <ModalContent modalState={modalState} />
    </Modal>
  );
};

export default Modals;
