import { mutation, query as gqlQuery } from "gql-query-builder";
import { gqlUrl } from "../util/config";
import { auth as firebaseAuth } from "../util/firebase";

const buildRequestWithoutAuth = async ({ query, variables = undefined }) => {
  const response = await fetch(gqlUrl, {
    method: "POST",
    headers: {
      "Access-Control-Allow-Origin": "*",
      "content-type": "application/json",
    },
    credentials: "include",
    body: JSON.stringify({ query, variables }),
  });

  const responseBody = await response.json();

  return responseBody;
};

const buildRequest = async ({ query, variables = undefined }) => {
  // eslint-disable-next-line consistent-return
  const isLoggedIn = await new Promise((resolve) => {
    if (firebaseAuth.currentUser) {
      resolve(true);
    }
    firebaseAuth.onAuthStateChanged((user) => {
      if (user) {
        resolve(true);
      } else {
        resolve(false);
      }
    });
  });

  if (isLoggedIn) {
    try {
      const JWTtoken = await firebaseAuth.currentUser.getIdToken();

      let auth = {};

      if (JWTtoken) {
        if (JWTtoken.length > 4000) {
          localStorage.removeItem("JWTtoken");
          localStorage.removeItem("userData");
          localStorage.removeItem("profilePicture");
          window.location.reload();
        }

        auth = { authorization: JWTtoken };
      }

      const response = await fetch(gqlUrl, {
        method: "POST",
        headers: {
          "Access-Control-Allow-Origin": "*",
          "content-type": "application/json",
          ...auth,
        },
        credentials: "include",
        body: JSON.stringify({ query, variables }),
      });

      let newJwt;
      let newUserData;

      try {
        const authHeaders =
          response.headers.get("x-amzn-remapped-authorization") ||
          response.headers.get("Authorization");
        newJwt = authHeaders.replace("Bearer ", "");
        newUserData = response.headers.get("x-UserData");
      } catch (err) {}

      const responseBody = await response.json();

      if (newJwt) {
        try {
          const token = Object.values(responseBody.data)[0].JWTtoken;
          if (!token) {
            localStorage.setItem("JWTtoken", newJwt);
          }
        } catch (err) {}
      }

      if (newUserData) {
        localStorage.setItem("userData", newUserData);
      }

      return responseBody;
    } catch (error) {
      console.log("error in buildRequest");
      console.log(error);
      return {};
    }
  } else {
    throw new Error("not logged in");
  }
};

