/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import { Button } from "antd";
import moment from "moment";
import React, { useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";
import { getNextGame } from "../../services/api-service";
import Players from "../Players";
import { containerStyle } from "../countdownStyles";
import Countdown from "./Countdown";
import LoadingBall from "../icons/LoadingBall";
import findUsersLeague from "../../util/findUsersLeague";
import UserContext from "../../contexts/UserContext";
import WhiteLink from "../atoms/WhiteLink";

/**
 * @param {Object} props
 * @param {any} props.children
 * @param {any=} props.Container
 * @param {Object=} props.style
 * @param {function=} props.onClick
 */
const GameButton = ({
  onClick,
  dataTestId,
  Container = Button,
  style,
  children,
}) => (
  <Container
    data-test-id={dataTestId || "countdown-dash-container"}
    className="gradient-background-red main-fader"
    onClick={onClick || (() => {})}
    style={{ ...containerStyle, ...style }}
    type="text"
  >
    <div>{children}</div>
  </Container>
);

const GameDash = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);
  const [awaitingResult, setAwaitingResult] = useState(false);
  const [showTeams, setShowTeams] = useState(false);

  const playerLeagueData = findUsersLeague(userData, leagueId);
  const isAdmin = playerLeagueData.role === "admin";

  let timer;

  useEffect(() => () => clearTimeout(timer));

  const { data } = useSWR(`getNextGame${leagueId}`, async () =>
    getNextGame({ leagueId })
  );

  if (!data) {
    return (
      <GameButton
        dataTestId="loading-game-dash"
        Container="div"
        style={{ fontSize: "20px", cursor: "default" }}
      >
        <LoadingBall fill="white" />
      </GameButton>
    );
  }

  let nextGame;
  try {
    nextGame = data.nextGame;
  } catch (err) {}

  if (!nextGame || (nextGame && !nextGame.date)) {
    return (
      <GameButton
        Container="div"
        dataTestId="no-games-container"
        style={{ fontSize: "14px", cursor: "default", whiteSpace: "normal" }}
      >
        There are no upcoming games scheduled
        <br />
        {isAdmin && (
          <WhiteLink href={`/#/league/${leagueId}/game-management`}>
            Game Management
          </WhiteLink>
        )}
      </GameButton>
    );
  }

  const matchTime = moment(nextGame.date).unix();
  const date = moment(nextGame.date).format("HH:mm - Do MMMM YYYY");
  const { location } = nextGame;

  const now = moment().unix();

  if (!awaitingResult) {
    if (matchTime - now <= 0) {
      setAwaitingResult(true);
    } else {
      timer = setTimeout(
        () => setAwaitingResult(true),
        (matchTime - now) * 1000
      );
    }
  } else if (matchTime - now > 0) {
    setAwaitingResult(timer);
  }

  const hasTeams =
    nextGame.teamOne.players.length > 0 || nextGame.teamTwo.players.length > 0;

  let gameState = "";

  if (nextGame.status === "AWAITING_RESULT") {
    gameState = "Awaiting Result";
  }
  if (nextGame.status === "IN_PLAY") {
    gameState = "In Play";
  }

  if (nextGame.status === "AWAITING_RESULT" || nextGame.status === "IN_PLAY") {
    return (
      <GameButton Container="div" style={{ cursor: "default" }}>
        <div style={{ fontSize: "14px" }}>{gameState}</div>
        {location && (
          <div
            data-test-id="game-location"
            style={{ fontSize: "14px", paddingTop: "30px" }}
          >
            {location}
          </div>
        )}

        <div
          data-test-id="game-date"
          style={{ fontSize: "14px", paddingTop: "5px" }}
        >
          {date}
        </div>
        {isAdmin && (
          <WhiteLink href={`/#/league/${leagueId}/game-management`}>
            Game Management
          </WhiteLink>
        )}
      </GameButton>
    );
  }

  return (
    <GameButton onClick={() => setShowTeams(!showTeams)}>
      <div style={{ width: "100%", fontSize: "14px" }}>Next Game</div>
      <Countdown matchTime={matchTime} />
      {location && (
        <div
          data-test-id="game-location"
          style={{ paddingTop: "10px", fontSize: "14px" }}
        >
          {location}
        </div>
      )}
      <div data-test-id="game-date" style={{ fontSize: "14px" }}>
        {date}
      </div>
      {isAdmin && (
        <WhiteLink href={`/#/league/${leagueId}/game-management`}>
          Game Management
        </WhiteLink>
      )}
      {Array.isArray(nextGame.available) &&
        nextGame.available.length > 0 &&
        !hasTeams && (
          <div style={{ paddingTop: "10px", fontSize: "12px" }}>
            View Available Players
            <svg width="20px" height="20px" viewBox="0 0 40 40">
              {!showTeams ? (
                <path
                  strokeWidth="3"
                  stroke="#FFFFFF"
                  fill="none"
                  d="M 15 25 l 10 10 l 10 -10"
                />
              ) : (
                <path
                  strokeWidth="3"
                  stroke="#FFFFFF"
                  fill="none"
                  d="M 15 37 l 10 -10 l 10 10"
                />
              )}
            </svg>
          </div>
        )}
      {hasTeams && (
        <div style={{ paddingTop: "10px", fontSize: "12px" }}>
          View Teams
          <svg width="20px" height="20px" viewBox="0 0 40 40">
            {!showTeams ? (
              <path
                strokeWidth="3"
                stroke="#FFFFFF"
                fill="none"
                d="M 15 25 l 10 10 l 10 -10"
              />
            ) : (
              <path
                strokeWidth="3"
                stroke="#FFFFFF"
                fill="none"
                d="M 15 37 l 10 -10 l 10 10"
              />
            )}
          </svg>
        </div>
      )}

      {showTeams && (
        <div style={{ marginTop: "10px" }}>
          <Players nextGame={nextGame} />
        </div>
      )}
    </GameButton>
  );
};

export default GameDash;
