import React from "react";
import { Drawer } from "antd";

import Navbar from "../NavBar/Navbar";
import UserContext from "../../../contexts/UserContext";
import "./header.css";
import AdminPanel from "../../loginAndSignup/AdminPanel";

const Header = () => {
  const { isLoginVisible, setLoginVisible, userData } =
    React.useContext(UserContext);

  return (
    <div>
      <Navbar />
      {userData && (
        <Drawer
          title={userData ? "Account" : "Log In or Sign Up"}
          placement="right"
          closable
          onClose={() => setLoginVisible(!isLoginVisible)}
          visible={isLoginVisible}
          width="350px"
          bodyStyle={userData ? { padding: 0 } : {}}
          headerStyle={{ textAlign: "center" }}
          maskStyle={{ backgroundColor: "rgba(0, 0, 0, 0.75)" }}
        >
          <AdminPanel onClose={setLoginVisible} />
        </Drawer>
      )}
    </div>
  );
};

export default Header;
