/* eslint-disable react/destructuring-assignment */
/* eslint-disable react/prop-types */
import React, { useState } from "react";
import { mutate } from "swr";
import moment from "moment";

import { Checkbox, Card, Modal, Input, Button, message } from "antd";
import { EditOutlined, PoundCircleOutlined } from "@ant-design/icons";

import { makePayment } from "../services/api-service";

import PlayerAvatar from "./atoms/PlayerAvatar";
import Ball from "./icons/LoadingBall";
import generateColor from "../util/generateColor";

const { confirm } = Modal;

/** @return {React.CSSProperties} */
const mobileCardStyle = (player) => {
  const { color } = generateColor(player.name);

  return {
    border: "none",
    marginTop: "1rem",
    textAlign: "left",
    borderRadius: "8px",
    background: `linear-gradient(to right, ${color[4]}, ${color[6]})`,
  };
};

const asMoney = (value) => `${(Math.abs(value) / 100).toFixed(2)}`;

const paymentMade = async ({ leagueId, playerId, gameId, amount }) => {
  message.info("Updating player's payments");
  const newEntry = {
    date: new Date().toISOString(),
    amount: -amount,
    ...(gameId ? { gameId } : {}),
  };

  await makePayment({ playerId, newEntry });

  await mutate(`${leagueId}_payments`);
  await mutate(`${playerId}_single_payments`);

  message.success("Update complete");
};

const editBalance = ({ leagueId, playerId, name }) => {
  let amount = "0.00";
  let paymentType;
  let title;
  let content;
  let okButtonShown = false;

  const modal = confirm();

  const changeState = () => {
    if (!paymentType) {
      title = `Do you want to add credit or debit?`;
      content = (
        <div style={{ textAlign: "center" }}>
          <Button
            data-test-id="select-credit"
            style={{
              maxWidth: "100px",
              borderRadius: "50px",
              marginRight: "20px",
              marginTop: "10px",
            }}
            onClick={() => {
              paymentType = "credit";
              changeState();
            }}
          >
            Credit
          </Button>
          <Button
            data-test-id="select-debit"
            style={{
              maxWidth: "100px",
              borderRadius: "50px",
              marginRight: "20px",
              marginTop: "10px",
            }}
            onClick={() => {
              paymentType = "debit";
              changeState();
            }}
          >
            Debit
          </Button>
        </div>
      );
    } else if (paymentType === "credit") {
      title = `How much would you like to credit ${name}?`;
      content = (
        <>
          <Input
            data-test-id="credit-input"
            prefix=""
            style={{ maxWidth: "100px", borderRadius: "50px" }}
            onChange={(e) => {
              amount = e.target.value;
              return true;
            }}
          />
        </>
      );
      okButtonShown = true;
    } else if (paymentType === "debit") {
      title = `How much would you like to debit ${name}?`;
      content = (
        <>
          <Input
            data-test-id="debit-input"
            prefix=""
            style={{ maxWidth: "100px", borderRadius: "50px" }}
            onChange={(e) => {
              amount = e.target.value;
              return true;
            }}
          />
        </>
      );
      okButtonShown = true;
    }
    modal.update({
      icon: <EditOutlined />,
      title,
      content,
      okButtonProps: { style: okButtonShown ? {} : { display: "none" } },
      closable: true,
      onOk: async () => {
        try {
          const amountInPounds = Math.trunc(Number.parseFloat(amount) * 100);

          if (amountInPounds < 0) {
            throw new Error("Input a positive amount");
          }

          if (!Number.isInteger(amountInPounds)) {
            throw new Error("not pounds");
          }

          await paymentMade({
            leagueId,
            playerId,
            amount: paymentType === "credit" ? -amountInPounds : amountInPounds,
          });

          return true;
        } catch (e) {
          if (e.message === "Input a positive amount") {
            message.error("Input a positive amount");
          } else {
            message.error("Input a number in pounds and pence");
          }

          throw new Error(e);
        }
      },
      onCancel() {},
      okText: paymentType === "debit" ? "Add Debit" : "Add Credit",
    });
  };

  changeState();
};

const Payment = ({
  leagueId,
  playerId,
  payment: { amount, date, paid, gameId },
  slideOut,
  balance,
  index,
}) => {
  let credit;
  let cost;

  if (amount > 0) {
    credit = amount;
  }

  if (amount < 0) {
    cost = amount;
  }

  return (
    <tr key={date} data-test-id={`payment-row-${index}`}>
      <td style={{ width: "26px", textAlign: "center" }}>
        <div style={{ width: "20px", height: "20px" }}>
          {gameId && !credit ? <Ball fill="white" /> : <PoundCircleOutlined />}
        </div>
      </td>
      <td data-test-id="game-date">{moment(date).format("DD/MM/YYYY")}</td>
      <td data-test-id="game-credit" style={{ textAlign: "center" }}>
        {credit && asMoney(credit)}
      </td>
      <td data-test-id="game-debit" style={{ textAlign: "center" }}>
        {cost && asMoney(cost)}
      </td>
      <td style={{ textAlign: "center" }}>
        {!!cost && (
          <Checkbox
            data-test-id="game-paid-checkbox"
            checked={paid}
            onChange={async (e) => {
              if (e.target.checked) {
                if (balance < 0) {
                  if (balance - cost >= 0) {
                    slideOut();
                  }
                }
                paymentMade({
                  leagueId,
                  playerId,
                  gameId,
                  amount: cost,
                });
              }
            }}
          />
        )}
      </td>
    </tr>
  );
};

const PlayerPayments = ({ player, leagueId, index }) => {
  const [expand, setExpand] = useState(false);
  const [transition, setTransition] = useState(false);

  const slideOut = () => setTransition(true);

  const { balance } = player;

  let paymentsToShow = [];

  if (player.payments && player.payments.length > 0) {
    paymentsToShow = player.payments.filter(({ pending }) => !pending);
    if (!expand) {
      paymentsToShow = player.payments.filter(
        ({ paid, amount }) => amount < 0 && !paid
      );
    }
  }

  let balanceText = "Up to date";

  if (balance < 0) {
    balanceText = "Owes:";
  }

  if (balance > 0) {
    balanceText = "Credit:";
  }

  return (
    <Card
      size="small"
      className={`mobile-payments ${transition ? "slideDown" : ""}`}
      style={{
        ...mobileCardStyle(player),
        color: "white",
      }}
      data-test-id={`payments-${index}`}
      key={player.id}
    >
      <PlayerAvatar name={player.name} picture={player.picture} />
      <span data-test-id="player-name">{player.name}</span>

      <p
        data-test-id="balance-status-text"
        style={{ float: "right", fontWeight: "bold", margin: "0px 10px" }}
      >
        {balanceText} {balance !== 0 && asMoney(balance)}
        <EditOutlined
          data-test-id="add-payment"
          style={{
            paddingLeft: "5px",
            cursor: "pointer",
            verticalAlign: "middle",
            fontSize: "25px",
            width: " 30px",
            height: "30px",
          }}
          onClick={() =>
            editBalance({ leagueId, playerId: player.id, name: player.name })
          }
        />
      </p>
      {!!paymentsToShow.length && (
        <div style={{ margin: "20px" }}>
          <table style={{ width: "100%" }}>
            <tr>
              <th> </th>
              <th>Date</th>
              <th style={{ textAlign: "center" }}>Credit</th>
              <th style={{ textAlign: "center" }}>Debit</th>
              <th style={{ textAlign: "center" }}>Paid</th>
            </tr>
            {paymentsToShow
              .sort((a, b) => new Date(b.date) - new Date(a.date))
              .map((payment, paymentIndex) => (
                <Payment
                  index={paymentIndex}
                  balance={balance}
                  leagueId={leagueId}
                  payment={payment}
                  playerId={player.id}
                  slideOut={slideOut}
                />
              ))}
          </table>
        </div>
      )}
      <div style={{ textAlign: "center" }}>
        <Button
          data-test-id="view-all-transactions"
          type="text"
          style={{
            textAlign: "center",
            marginBottom: "0px",
            color: "white",
          }}
          onClick={() => setExpand(!expand)}
        >
          {!expand ? "View all" : "View outstanding"} transactions
        </Button>
      </div>
    </Card>
  );
};

export default PlayerPayments;
