/* eslint-disable react/prop-types */
import React from "react";
import { Row, Col } from "antd";

import PlayersCardText from "./TeamSelectionCard/PlayersCardText";

const Players = ({ nextGame }) => {
  const { available, waitingList, teamOne, teamTwo } = nextGame;

  let teamOnePlayers = teamOne.players;
  let teamTwoPlayers = teamTwo.players;

  if (teamOnePlayers.length !== 0 || teamTwoPlayers.length !== 0) {
    teamOnePlayers = teamOnePlayers.concat(
      Array.from({ length: nextGame.size - teamOnePlayers.length }, () => ({}))
    );

    teamTwoPlayers = teamTwoPlayers.concat(
      Array.from({ length: nextGame.size - teamTwoPlayers.length }, () => ({}))
    );
  }

  return (
    <Row>
      {teamOne && teamTwo && teamOnePlayers.length && teamTwoPlayers.length ? (
        <>
          <PlayersCardText
            teamNumber="1"
            title={`${teamOne.name || "Team One"}`}
            players={teamOnePlayers}
            winPercentage={teamOne.winPercentage}
            ranking={teamOne.ranking}
          />
          <PlayersCardText
            teamNumber="2"
            title={`${teamTwo.name || "Team Two"}`}
            players={teamTwoPlayers}
            winPercentage={teamTwo.winPercentage}
            ranking={teamTwo.ranking}
          />
          {!!waitingList.length && (
            <>
              <h3
                style={{ width: "100%", textAlign: "center", color: "white" }}
              >
                Waiting List
              </h3>
              <Col
                style={{ textAlign: "center", width: "100%" }}
                data-test-id="waiting-list"
              >
                <PlayersCardText players={waitingList} />
              </Col>
            </>
          )}
        </>
      ) : (
        <Col style={{ textAlign: "center", width: "100%" }}>
          <PlayersCardText players={available} />
        </Col>
      )}
    </Row>
  );
};

export default Players;

export const TeamSelectionPageTitle = "Game Management";
