/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";
import { containerStyle } from "../countdownStyles";
import numberSuffix from "../../util/numberSuffix";
import getYears from "../../services/getYears";
import getStats from "../../services/getStats";
import findUsersLeague from "../../util/findUsersLeague";
import UserContext from "../../contexts/UserContext";
import LoadingBall from "../icons/LoadingBall";

const StatsLink = ({ style, children, leagueId }) => (
  <a
    href={`/#/league/${leagueId}/player-stats`}
    className="gradient-background main-fader"
    data-test-id="stats-dash-container"
    style={{ ...containerStyle, ...style }}
  >
    <div style={{ width: "100%" }}>{children}</div>
  </a>
);

const StatsDash = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);

  const thisYear = new Date().getFullYear().toString();

  const { data: yearsData } = useSWR(
    `${leagueId}_years`,
    getYears({ leagueId })
  );
  const { data } = useSWR(
    `${leagueId}_${thisYear}_stats`,
    getStats({ leagueId, year: thisYear })
  );

  let showZero;

  if (data === "getStats failed") {
    showZero = true;
  }

  let rankSuffix = "";

  let statsData = {
    rank: "-",
    wins: 0,
    draws: 0,
    losses: 0,
  };

  let playerStats;
  try {
    if ((yearsData && yearsData.length === 0) || showZero) {
      playerStats = "no games";
    } else {
      const { playerId } = findUsersLeague(userData, leagueId);
      playerStats = data.stats.find(({ id }) => id === playerId);
    }
  } catch (err) {}

  if (!data || (!yearsData && !showZero)) {
    return (
      <StatsLink
        leagueId={leagueId}
        style={{
          fontSize: "20px",
          cursor: "default",
          minHeight: "144px",
          paddingTop: "25px",
        }}
      >
        <LoadingBall fill="white" />
      </StatsLink>
    );
  }

  if (playerStats && playerStats !== "no games") {
    statsData = {
      rank: playerStats.rankingNum,
      wins: playerStats.wins,
      draws: playerStats.draws,
      losses: playerStats.losses,
    };

    rankSuffix = numberSuffix(statsData.rank);
  }

  return (
    <StatsLink leagueId={leagueId} style={{ minHeight: "0px", height: "auto" }}>
      <div style={{ paddingBottom: "10px", fontSize: "14px" }}>Your Stats</div>
      <div style={{ width: "100%", fontSize: "12px" }}>
        <div style={{ width: "20%", display: "inline-block" }}>
          <div data-test-id="rankNum" style={{ fontSize: "30px" }}>
            {statsData.rank}
            <span style={{ fontSize: "20px" }}>{rankSuffix}</span>
          </div>
          <div>Rank</div>
        </div>
        <div style={{ width: "20%", display: "inline-block" }}>
          <div data-test-id="winsNum" style={{ fontSize: "30px" }}>
            {statsData.wins}
          </div>
          <div>Wins</div>
        </div>
        <div style={{ width: "20%", display: "inline-block" }}>
          <div data-test-id="drawsNum" style={{ fontSize: "30px" }}>
            {statsData.draws}
          </div>
          <div>Draws</div>
        </div>
        <div style={{ width: "20%", display: "inline-block" }}>
          <div data-test-id="lossesNum" style={{ fontSize: "30px" }}>
            {statsData.losses}
          </div>
          <div>Losses</div>
        </div>
      </div>
      <div style={{ paddingTop: "15px", fontSize: "12px" }}>View Stats</div>
    </StatsLink>
  );
};

export default StatsDash;
