import React from "react";
import { Modal, message } from "antd";
import { mutate } from "swr";
import { ExclamationCircleOutlined } from "@ant-design/icons";

import { deleteGame } from "../../services/api-service";

const deletePendingGame = async ({
  pendingGame,
  // setConfirmLeaveShown,
  navigate,
  leagueId,
}) => {
  Modal.confirm({
    title: "Are you sure you want to delete this pending game?",
    icon: <ExclamationCircleOutlined />,
    content: "This will remove it from the upcoming fixtures list",
    okText: "Delete",
    okType: "danger",
    cancelText: "Cancel",
    onOk: async () => {
      // setConfirmLeaveShown(true);
      await deleteGame(pendingGame.id);
      message.success("Game deleted");

      await Promise.all([
        mutate(`getTeams${leagueId}`),
        mutate(`getAvailability${leagueId}`, () => null),
        mutate(`getNextGame${leagueId}`, () => null),
      ]);

      navigate(`/league/${leagueId}`);
    },
  });
};

export default deletePendingGame;
