/* eslint-disable no-console */
import {
  signInWithPopup,
  OAuthProvider,
  fetchSignInMethodsForEmail,
} from "firebase/auth";

import { getUserData } from "../../services/api-service";
import { auth } from "../../util/firebase";

const appleProvider = new OAuthProvider("apple.com");

appleProvider.addScope("email");
appleProvider.addScope("name");

const appleLogin =
  ({ setUserToken, updateUserData, navigate, setAlternativeLogin }) =>
  async () => {
    try {
      const result = await signInWithPopup(auth, appleProvider);
      const { user } = result;

      // This gives you a Facebook Access Token. You can use it to access the Facebook API.
      const credential = OAuthProvider.credentialFromResult(result);
      const { accessToken } = credential;

      console.log({ user, accessToken, credential });

      setUserToken(accessToken);

      const userDataResponse = await getUserData();

      const userData = userDataResponse.data.getUserData;

      console.log({ userData });

      if (userData) {
        updateUserData(userData);

        if (userData.leagues[0]) {
          const { leagueId } = userData.leagues[0];
          navigate(`/league/${leagueId}`);
        }
      } else {
        navigate(`/user-details`);
      }
    } catch (error) {
      // Handle Errors here.
      const errorCode = error.code;
      const errorMessage = error.message;
      // The email of the user's account used.
      const { email } = error.customData;
      // The AuthCredential type that was used.
      const credential = OAuthProvider.credentialFromError(error);

      console.log({ error, errorMessage, credential });

      if (errorCode === "auth/account-exists-with-different-credential") {
        try {
          const methods = await fetchSignInMethodsForEmail(auth, email);
          console.log(methods);
          if (methods[0] === "emailLink") {
            setAlternativeLogin({ method: "Email", email });
          } else if (methods[0] === "facebook.com") {
            setAlternativeLogin({ method: "Facebook", email });
          }
          // Asks the user their password.
          // In real scenario, you should handle this asynchronously.
          // const password = promptUserForPassword(); // TODO: implement promptUserForPassword.
          // getAuth()
          //   .signInWithEmailAndPassword(email, password)
          //   .then((result) =>
          //     // Step 4a.
          //     result.user.linkWithCredential(pendingCred)
          //   )
          //   .then(() => {
          //     // Google account successfully linked to the existing Firebase user.
          //     goToApp();
          //   });
        } catch (error3) {
          console.log(error3);
        }
      }
    }
  };

export default appleLogin;
