/* eslint-disable react/prop-types */
import { Modal, Button, List } from "antd";
import React, { useState, useContext, useEffect } from "react";
import { mutate } from "swr";
import UserContext from "../contexts/UserContext";
import {
  getAvailability,
  linkPlayerAndUser,
  setPlayerName,
} from "../services/api-service";
import PlayerAvatar from "./atoms/PlayerAvatar";

const setupOnFinish =
  ({ leagueId, updateUserData, setUserToken, setLoginVisible }) =>
  async ({ playerName }) => {
    const response = await setPlayerName({ leagueId, playerName });

    const jwt = response.data.setPlayerName.JWTtoken;
    const userData = JSON.parse(response.data.setPlayerName.data);

    setUserToken(jwt);
    updateUserData(userData);
    setLoginVisible(false);

    return "done";
  };

const PlayerList = ({ players, leagueId }) => {
  const { updateUserData, setUserToken, setLoginVisible } =
    useContext(UserContext);

  const selectPlayer = async (id) => {
    const response = await linkPlayerAndUser({ leagueId, playerId: id });

    const jwt = response.data.linkPlayerAndUser.JWTtoken;
    const userData = JSON.parse(response.data.linkPlayerAndUser.data);

    setUserToken(jwt);
    updateUserData(userData);
    setLoginVisible(false);

    return "done";
  };

  return (
    <List
      style={{ marginBottom: "15px" }}
      size="small"
      bordered
      dataSource={players}
      renderItem={(player) => (
        <List.Item>
          <PlayerAvatar name={player.name} picture={player.picture} />
          {player.name}
          <Button
            onClick={() => selectPlayer(player.id)}
            style={{ float: "right" }}
          >
            This is me
          </Button>
        </List.Item>
      )}
    />
  );
};

const LinkPlayerText = ({ leagueId, playerName }) => {
  const { updateUserData, setUserToken, setLoginVisible } =
    useContext(UserContext);

  const [nameState, setNameState] = useState("ask");
  const [players, setPlayers] = useState(false);

  const onFinish = setupOnFinish({
    leagueId,
    updateUserData,
    setUserToken,
    setLoginVisible,
  });

  const submit = async () => {
    setNameState("loading");
    await onFinish({ playerName });
    setNameState("complete");
    await mutate(`getAvailability${leagueId}`, () => false);
  };

  useEffect(async () => {
    const data = await getAvailability({ leagueId });

    const playersRes =
      data &&
      data.availability &&
      data.availability.availability &&
      Array.isArray(data.availability.availability) &&
      data.availability.availability.filter(({ userId }) => !userId);

    if (playersRes && playersRes.length === 0) {
      submit();
    }

    setPlayers(playersRes);
  }, []);

  if (players && players.length === 0) {
    return <></>;
  }
  if (!players || (players && players.length === 0)) {
    return <></>;
  }

  return (
    <Modal
      title="One last thing"
      visible={nameState === "ask" || nameState === "loading"}
      confirmLoading={false}
      footer={false}
      closable={false}
    >
      {players && players.length > 0 ? (
        <p>Find yourself in the list</p>
      ) : (
        <p>Input your name</p>
      )}
      {players && players.length > 0 && PlayerList({ players, leagueId })}
      {players && players.length > 0 && (
        <p>
          If you can&apos;t see yourself in the list then create a new player
        </p>
      )}
      <div
        style={{
          border: "1px solid #d9d9d9",
          padding: "8px 16px",
        }}
      >
        <PlayerAvatar name={playerName} /> {playerName}
        <Button
          data-test-id="set-name-button"
          loading={nameState === "loading"}
          type="primary"
          onClick={submit}
          style={{ float: "right" }}
        >
          Create Player
        </Button>
      </div>
    </Modal>
  );
};

const LinkPlayerModal = () => {
  const { userData } = useContext(UserContext);

  const leagues = (userData && userData.leagues) || [];

  return (
    <>
      {leagues
        .filter((league) => !league.playerName)
        .map(({ leagueId }) => {
          const userHasPlayer = userData.leagues.find(
            ({ leagueId: id }) => id === leagueId
          ).playerId;

          if (userHasPlayer) {
            return <></>;
          }

          return (
            <LinkPlayerText leagueId={leagueId} playerName={userData.name} />
          );
        })}
    </>
  );
};

export default LinkPlayerModal;
