import moment from "moment";

const setTime = async (e, pendingGame, updatePendingGameState) => {
  const time = e.target.value;

  if (time) {
    const [hour, minute] = time.split(":");
    const day = pendingGame.date ? pendingGame.date : Date.now();
    const date = moment(day)
      .set({ hour, minute, second: 0, millisecond: 0 })
      .toISOString();

    await updatePendingGameState({
      ...pendingGame,
      date,
    });
  }
};

export default setTime;
