import shuffle from "../../util/shuffle";

const clone = (input) => JSON.parse(JSON.stringify(input));

const randomiseTeams = (
  pendingGame,
  availablePlayers,
  updatePendingGameState
) => {
  const randomisedPlayers = shuffle(
    clone(availablePlayers).slice(0, pendingGame.size * 2)
  );
  const halfRandomised = Math.ceil(randomisedPlayers.length / 2);

  const tempPendingGame = clone(pendingGame);

  tempPendingGame.teamOne = randomisedPlayers.slice(0, halfRandomised);
  tempPendingGame.teamTwo = randomisedPlayers.slice(
    halfRandomised,
    randomisedPlayers.length
  );

  updatePendingGameState(tempPendingGame);
};

export default randomiseTeams;
