/* eslint-disable react/prop-types */
import React from "react";
import { Row, Col, Card, Typography, Result, Divider } from "antd";

const { Text } = Typography;

/** @type {React.CSSProperties} */
const cardStyle = {
  height: "100%",
  borderRadius: "8px",
  border: "none",
  textAlign: "center",
  verticalAlign: "center",
  minHeight: "90px",
};

/** @type {React.CSSProperties} */
const valueStyle = {
  border: "1px solid white",
  color: "white",
  width: "40px",
  height: "40px",
  display: "inline-block",
  textAlign: "center",
  fontSize: "20px",
  lineHeight: "40px",
  borderRadius: "50%",
};

/** @type {React.CSSProperties} */
const valueSubStyle = {
  fontSize: "14px",
  display: "block",
  color: "white",
  height: "20px",
  lineHeight: "25px",
};

const Stat = ({ people, title, property }) => {
  const multiStat = people[0].p1 && people[0].p2;

  return (
    <Col span={24} sm={12} style={{ textAlign: "center" }}>
      <Card
        className="gradient-background highlight-stats"
        size="small"
        style={cardStyle}
        bodyStyle={{ width: "100%", height: "calc(100% - 26px)" }}
      >
        <div
          style={{
            display: "block",
            color: "white",
            width: "100%",
            marginBottom: "10px",
          }}
        >
          {title}
        </div>
        <div
          style={{
            verticalAlign: "middle",
            height: "100%",
            display: "flex",
            paddingBottom: "10px",
          }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              width: "100%",
            }}
          >
            {people.length > 3 ? (
              <Text
                style={{
                  fontSize: "16px",
                  width: "100%",
                  textAlign: "center",
                  color: "white",
                }}
              >
                Too many players tied!
              </Text>
            ) : (
              <>
                <Text
                  style={{
                    textAlign: "left",
                    fontSize: "16px",
                    display: "inline-block",
                  }}
                >
                  {multiStat
                    ? people.map(({ p1, p2 }, index) => (
                        <div key={p1 || p2}>
                          {!!index && <Divider style={{ margin: "5px 0" }} />}
                          <span style={{ color: "white" }}>{p1}</span>
                          <br />
                          <span style={{ color: "white" }}>{p2}</span>
                        </div>
                      ))
                    : people.map(({ name }) => (
                        <div key={name}>
                          <p style={{ marginBottom: "0px", color: "white" }}>
                            {name}
                          </p>
                        </div>
                      ))}
                </Text>
                <span>
                  <span style={valueStyle}>{people[0][property]}</span>
                  {multiStat && <span style={valueSubStyle}>{property}</span>}
                </span>
              </>
            )}
          </div>
        </div>
      </Card>
    </Col>
  );
};

const Highlights = ({ statsDashboard, totalGames }) => {
  const {
    mostWins,
    mostGamesPlayed,
    highestRanked,
    mostCompatible,
    leastCompatible,
    mostWinsInARow,
  } = statsDashboard;

  if (totalGames < 3) {
    return (
      <Result
        style={{ maxWidth: "400px", margin: "auto" }}
        extra="Highlights will appear for the current year after 3 games have been played"
      />
    );
  }

  return (
    <>
      <Row gutter={[16, 16]}>
        <Stat title="Most Wins" people={mostWins} property="wins" />
        <Stat
          title="Most Games Played"
          people={mostGamesPlayed}
          property="games"
        />

        <Stat
          title="Highest Ranked"
          people={highestRanked}
          property="rankingNum"
        />
        <Stat
          title="Most Wins In A Row"
          people={mostWinsInARow}
          property="mostWinsInARow"
        />

        <Stat title="Most Compatible" people={mostCompatible} property="wins" />
        <Stat
          title="Least Compatible"
          people={leastCompatible}
          property="losses"
        />
      </Row>
    </>
  );
};

export default Highlights;
