/* eslint-disable react/prop-types */
import React from "react";
import { Form, Input, Button, Alert } from "antd";

const EmailSignUpInput = ({ feedback = {}, value = "" }) => (
  <div
    style={{
      width: "100%",
      maxWidth: "300px",
      display: "inline-block",
      marginTop: "20px",
    }}
  >
    <Form.Item
      defaultValue={value}
      name="Email"
      validateTrigger="focusout"
      rules={[
        {
          required: true,
          message: "Input your Email",
        },
        {
          type: "email",
          message: "Input a valid Email",
        },
      ]}
    >
      <Input
        size="large"
        placeholder="Email"
        data-test-id="login-email"
        type="email"
        defaultValue={value}
      />
    </Form.Item>
    <Form.Item>
      <Button
        block
        data-test-id="login-button"
        size="large"
        type="primary"
        htmlType="submit"
      >
        Next
      </Button>
    </Form.Item>
    {feedback.type && (
      <Alert
        data-test-id="login-alert"
        message={feedback.title}
        description={feedback.message}
        type={feedback.type}
        style={{ marginTop: "10px", marginBottom: "10px" }}
        showIcon
      />
    )}
  </div>
);

export default EmailSignUpInput;
