/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import React, { useState } from "react";
import useSWR from "swr";

import { useParams, useNavigate } from "react-router-dom";
import StatsPageComponent from "../components/StatsPageComponent";
import Loading from "../components/atoms/Loading";
import getYears from "../services/getYears";
import getStats from "../services/getStats";

const StatsPage = ({ leagueId, display }) => {
  const { year: yearParam, pageName } = useParams();
  const navigate = useNavigate();

  const [selectedYear, setSelectedYear] = useState(yearParam);

  const { data: statsData, error } = useSWR(
    `${leagueId}_${selectedYear}_stats`,
    getStats({ leagueId, year: selectedYear })
  );

  const setRequestedYear = (year) => {
    window.history.replaceState(
      null,
      null,
      `/#/league/${leagueId}/${pageName}/${year}`
    );
    setSelectedYear(year);
  };

  const { data: years } = useSWR(`${leagueId}_years`, getYears({ leagueId }));

  try {
    if (
      years &&
      years !== "getYears failed" &&
      years.length > 1 &&
      selectedYear &&
      ![...years, "All"].includes(selectedYear)
    ) {
      // eslint-disable-next-line no-console
      console.log(`navigating to /league/${leagueId}/player-stats`);
      navigate(`/league/${leagueId}/player-stats`);
    }

    if (!selectedYear && years.length) {
      setRequestedYear(years[years.length - 1]);
    }
  } catch (err) {}

  let showZero;

  if (error && error.message && error.message.includes("403")) {
    showZero = true;
  }

  if (statsData || (!selectedYear && !statsData) || showZero) {
    let stats;
    if (statsData) {
      const { stats: players, numGames } = statsData;
      stats = { players, numGames, selectedYear, years };
    }

    return (
      <StatsPageComponent
        display={display}
        data={stats}
        setRequestedYear={setRequestedYear}
      />
    );
  }

  return <Loading />;
};

export default StatsPage;

export const StatsPageTitle = "Player Stats";
