/* eslint-disable no-param-reassign */
/* eslint-disable no-nested-ternary */
const numberSuffix = (n) => {
  n %= 100;
  return Math.floor(n / 10) === 1
    ? "th"
    : n % 10 === 1
    ? "st"
    : n % 10 === 2
    ? "nd"
    : n % 10 === 3
    ? "rd"
    : "th";
};

export default numberSuffix;
