const setupConfig = () => {
  const envVars = {
    baseUrl: process.env.REACT_APP_BASE_URL,
    gqlUrl: process.env.REACT_APP_GQL_URL,
    logUrl: process.env.REACT_APP_LOG_URL,
    facebookAppId: process.env.REACT_APP_FACEBOOK_APP_ID,
    paypal: {
      planId: process.env.REACT_APP_PAYPAL_PLAN_ID,
      clientId: process.env.REACT_APP_PAYPAL_CLIENT_ID,
    },
    buildId: process.env.REACT_APP_BUILD_ID,
    cacheBucket: process.env.REACT_APP_CACHE_BUCKET,
    useEmulator: process.env.REACT_APP_USE_EMULATOR,
    bundleId: process.env.REACT_APP_BUNDLE_ID,
    firebaseDynamicLinkDomain:
      process.env.REACT_APP_FIREBASE_DYNAMIC_LINK_DOMAIN,
    firebaseApiKey: process.env.REACT_APP_FIREBASE_API_KEY,
    firebaseAuthDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
    firebaseProjectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
    firebaseStorageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
    firebaseMessagingSenderId:
      process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
    firebaseAppId: process.env.REACT_APP_FIREBASE_APP_ID,
    firebaseMeasurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID,
    firebaseDynamicLinkUrl: process.env.REACT_APP_FIREBASE_DYNAMIC_LINK_URL,
    firebaseDomainUriPrefix: process.env.REACT_APP_FIREBASE_DOMAIN_URI_PREFIX,
    firebaseEmulatorUrl: process.env.REACT_APP_FIREBASE_EMULATOR_URL,
  };
  if (!envVars.cacheBucket) {
    throw new Error("Please specify REACT_APP_CACHE_BUCKET in .env");
  }
  if (!envVars.baseUrl) {
    throw new Error("Please specify REACT_APP_BASE_URL in .env");
  }
  if (!envVars.gqlUrl) {
    throw new Error("Please specify REACT_APP_GQL_URL in .env");
  }
  if (!envVars.logUrl) {
    throw new Error("Please specify REACT_APP_LOG_URL in .env");
  }
  if (!envVars.facebookAppId) {
    throw new Error("Please specify REACT_APP_FACEBOOK_APP_ID in .env");
  }
  if (!envVars.bundleId) {
    throw Error("Please specify REACT_APP_BUNDLE_ID in .env");
  }

  // if (!envVars.paypal.planId || !envVars.paypal.clientId) {
  //     throw new Error('Please specify REACT_APP_PAYPAL_*** in .env')
  // }

  return envVars;
};

module.exports = setupConfig();
