/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import useSWR, { mutate } from "swr";
import { Switch, List, Button, Result } from "antd";
import { useNavigate } from "react-router-dom";
import { CheckOutlined, CloseOutlined } from "@ant-design/icons";

import Title from "antd/lib/typography/Title";
import { updateAvailability } from "../services/api-service";
import getAvailability from "../services/getAvailability";
import Loading from "../components/atoms/Loading";
import UserContext from "../contexts/UserContext";
import ErrorMessage from "../components/atoms/ErrorMessage";
import PlayerAvatar from "../components/atoms/PlayerAvatar";
import findUsersLeague from "../util/findUsersLeague";

const clone = (input) => JSON.parse(JSON.stringify(input));

const getRole = (leagueId, userData) => {
  try {
    return findUsersLeague(userData, leagueId).role;
  } catch {
    return false;
  }
};

const getPlayerId = (leagueId, userData) => {
  try {
    return findUsersLeague(userData, leagueId).playerId;
  } catch {
    return false;
  }
};

const AvailabilityPage = ({ leagueId }) => {
  const navigate = useNavigate();

  const { userData } = useContext(UserContext);
  const isAdmin = getRole(leagueId, userData) === "admin";
  const thisPlayerId = getPlayerId(leagueId, userData);

  if (!isAdmin) {
    navigate("/");
  }

  const { data, error } = useSWR(
    `getAvailability${leagueId}`,
    getAvailability(leagueId)
  );

  if (error || (data && !data.availability)) {
    return <ErrorMessage />;
  }

  if (!data) {
    return <Loading />;
  }

  if (data && !data.hasPendingGame) {
    return (
      <Result
        data-test-id="play-message"
        extra={
          <>
            <p>Availability will appear after you schedule a game</p>
            {isAdmin && (
              <Button
                style={{ marginTop: "20px", width: "100%", maxWidth: "300px" }}
                type="primary"
                onClick={() => navigate(`/league/${leagueId}/game-management`)}
                data-test-id={`${leagueId}-game-management`}
              >
                Game Management
              </Button>
            )}
          </>
        }
      />
    );
  }

  const tempAvailabilityList = clone(data.availability);

  const setAvailability = (playerId) => async (checked) => {
    const playerIndex = tempAvailabilityList.findIndex(
      ({ id }) => id === playerId
    );

    const player = tempAvailabilityList[playerIndex];
    const status = checked ? "AVAILABLE" : "NOT_AVAILABLE";
    tempAvailabilityList[playerIndex].status = status;

    await updateAvailability({
      id: player.id,
      status,
      leagueId,
    });

    await Promise.all([
      mutate(
        `getAvailability${leagueId}`,
        { availability: tempAvailabilityList, hasPendingGame: true },
        false
      ),
      mutate(`getTeams${leagueId}`, false),
    ]);
  };

  const availabilityCount =
    (data &&
      data.availability &&
      Array.isArray(data.availability) &&
      data.availability.filter(({ status }) => status === "AVAILABLE")
        .length) ||
    0;

  return (
    <div style={{ paddingBottom: "100px" }}>
      <p>
        Here are the players that belong to the league. View the player&apos;s
        availability for the upcoming game and update your own availability. The
        list is in priority order.
      </p>
      <div style={{ width: "100%" }}>
        <Button
          style={{
            marginTop: "10px",
            marginBottom: "10px",
            width: "100%",
            maxWidth: "300px",
          }}
          type="primary"
          onClick={async () => {
            await mutate(`getNextGame${leagueId}`, () => null);
            navigate(`/league/${leagueId}/player-management`);
          }}
          data-test-id={`${leagueId}-player-management`}
        >
          Player Management
        </Button>
      </div>
      <List
        header={
          <>
            Available Players
            <Title data-test-id="availability-count" level={3}>
              {availabilityCount}
            </Title>
          </>
        }
        bordered={false}
        pagination={false}
        dataSource={data.availability}
        size="small"
        className="availability-table"
        renderItem={({ name, status, picture, id }, index) => (
          <List.Item>
            <table style={{ width: "100%" }}>
              <tbody>
                <tr>
                  <td
                    data-test-id={`availability-player-row-${index}`}
                    style={{
                      whiteSpace: "nowrap",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      maxWidth: "120px",
                      textAlign: "left",
                    }}
                  >
                    <div
                      style={{
                        display: "flex",
                        whiteSpace: "nowrap",
                      }}
                    >
                      <div
                        style={{
                          overflow: "hidden",
                          textOverflow: "ellipsis",
                          flex: 1,
                        }}
                      >
                        <PlayerAvatar name={name} picture={picture} />
                        {name}
                      </div>
                    </div>
                  </td>
                  <td style={{ textAlign: "right" }}>
                    <span
                      style={{
                        color: "#bbb",
                        paddingRight: "10px",
                        whiteSpace: "nowrap",
                      }}
                    >
                      {status === "NOT_REPLIED" ? "Not Replied" : "Replied"}
                    </span>
                  </td>
                  <td
                    style={{
                      textAlign: "right",
                      width: "0.1%",
                      whiteSpace: "nowrap",
                    }}
                  >
                    <Switch
                      data-test-id="isAvailable"
                      disabled={!(isAdmin || thisPlayerId === id)}
                      checkedChildren={<CheckOutlined />}
                      unCheckedChildren={<CloseOutlined />}
                      defaultChecked={status === "AVAILABLE"}
                      data-ischecked={status === "AVAILABLE"}
                      onChange={setAvailability(id)}
                    />
                  </td>
                </tr>
              </tbody>
            </table>
          </List.Item>
        )}
      />
      {isAdmin && (
        <Button
          style={{ marginTop: "20px", width: "100%", maxWidth: "300px" }}
          type="primary"
          onClick={async () => {
            await mutate(`getNextGame${leagueId}`, () => null);
            navigate(`/league/${leagueId}/game-management`);
          }}
          data-test-id={`${leagueId}-game-management`}
        >
          Game Management
        </Button>
      )}
    </div>
  );
};

export default AvailabilityPage;

export const AvailabilityPageTitle = "Availability";
