/* eslint-disable react/prop-types */
import React from "react";
import { Menu, Dropdown, Button, Typography, Row, Col, Card } from "antd";
import { DownOutlined } from "@ant-design/icons";
import months from "../util/months";
import scrollToRef from "../util/scrollToRef";
import WhiteButton from "./atoms/WhiteButton";

const { Text } = Typography;

const GameDateSelection = ({
  games,
  selectedDate,
  setYear,
  setMonth,
  availableYears,
  availableMonths,
}) => {
  if (games.length === 0) {
    return (
      <div
        style={{ textAlign: "center", padding: "20px" }}
        data-test-id="play-message"
      >
        <Text>Stats will appear after the first game has been played</Text>
      </div>
    );
  }

  const isMonthSelected = (month) => selectedDate.month === month;
  const isMonthDisabled = (month) => !availableMonths.includes(month);

  const gamesForDate = games.filter(
    ({ year, month }) =>
      year === selectedDate.year && month === selectedDate.month
  );

  return (
    <section className="select-date" style={{ textAlign: "center" }}>
      <div style={{ textAlign: "center", width: "100%" }}>
        <Card
          size="small"
          style={{
            width: "260px",
            display: "inline-block",
            marginBottom: "20px",
          }}
          className="gradient-background-red"
        >
          <Row gutter={[8, 8]} style={{ marginBottom: "-4px" }}>
            <Col span={24}>
              {availableYears.length === 1 ? (
                <WhiteButton
                  style={{ fontSize: "12px", width: "100%", marginTop: 0 }}
                >
                  {selectedDate.year}
                </WhiteButton>
              ) : (
                <Dropdown
                  className="years-dropdown"
                  overlay={
                    <Menu>
                      {availableYears
                        .filter((y) => y !== selectedDate.year)
                        .map((y) => (
                          <Menu.Item
                            style={{ fontSize: "12px", textAlign: "center" }}
                            onClick={() => {
                              setYear(y);
                            }}
                            key={y}
                          >
                            <Button type="text" style={{ width: "100%" }}>
                              {y}
                            </Button>
                          </Menu.Item>
                        ))}
                    </Menu>
                  }
                  trigger={["click"]}
                >
                  <Button
                    block
                    type="primary"
                    size="small"
                    style={{ fontSize: "12px" }}
                  >
                    <DownOutlined
                      style={{ fontSize: "8px", visibility: "hidden" }}
                    />
                    {selectedDate.year}
                    <DownOutlined style={{ fontSize: "8px" }} />
                  </Button>
                </Dropdown>
              )}
            </Col>
            {months.map(([m, month]) => (
              <Col span={6} key={m}>
                <Button
                  className="months-button"
                  block
                  size="small"
                  style={{ fontSize: "12px" }}
                  type={isMonthSelected(month) ? "primary" : "default"}
                  disabled={isMonthDisabled(month)}
                  onClick={() => setMonth(month)}
                >
                  {m}
                </Button>
              </Col>
            ))}
          </Row>
        </Card>
        <Col span={24}>
          {gamesForDate.map((game) => (
            <Button
              className="gradient-background-red date-button"
              data-test-id={`button-${game.dateDay}`}
              key={game._id}
              onClick={() => scrollToRef(game.ref)}
              type="primary"
              shape="circle"
              // size='small'
              style={{ fontSize: "12px", margin: "0 10px" }}
            >
              {game.dateDay}
            </Button>
          ))}
        </Col>
      </div>
    </section>
  );
};

export default GameDateSelection;
