import * as colors from "@ant-design/colors";
import seed from "seed-random";

const colorNames = [
  "red",
  "volcano",
  "orange",
  "gold",
  "green",
  "blue",
  "geekblue",
  "purple",
  "magenta",
];

const generateColor = (name) => {
  const random = seed(name);
  const index = Math.floor(random() * colorNames.length);
  const colorName = colorNames[index];
  const color = colors.presetPalettes[colorName];
  return {
    color,
    style: {
      backgroundColor: color[2],
      color: color[7],
    },
  };
};

export default generateColor;
