import React from "react";
import { Modal, message } from "antd";
import { mutate } from "swr";
import moment from "moment";
import { ExclamationCircleOutlined } from "@ant-design/icons";

import { updatePendingGame } from "../../services/api-service";

const removeNull = (team) => team.filter((player) => player);

const setupOnFinish =
  ({
    // setConfirmLeaveShown,
    updatePendingGameState,
    navigate,
    leagueId,
    setLoading,
  }) =>
  async ({ inputPendingGame, hasPendingPlayers, pendingGame }) => {
    // setConfirmLeaveShown(true);

    if (
      ["team-one-wins", "team-two-wins", "draw", "void"].includes(
        pendingGame.result
      )
    ) {
      setLoading("submit result", true);
    } else {
      setLoading("save pending game", true);
    }

    let shouldResetAvailability;

    let {
      teamOne = [],
      teamTwo = [],
      available = [],
      notAvailable = [],
    } = pendingGame;

    if (
      pendingGame.result === "pending" &&
      hasPendingPlayers &&
      inputPendingGame.date &&
      new Date(pendingGame.date).getTime() !==
        new Date(inputPendingGame.date).getTime()
    ) {
      shouldResetAvailability = await new Promise((resolve) => {
        Modal.confirm({
          title: "Your fixture date or time has changed",
          icon: <ExclamationCircleOutlined />,
          content: "Would you like to reset the player availability?",
          okText: (
            <span data-test-id="reset-availability">Reset Availability</span>
          ),
          cancelText: (
            <span data-test-id="keep-availability">Keep Availability</span>
          ),
          onOk: () => resolve(true),
          onCancel: () => resolve(false),
        });
      });

      if (shouldResetAvailability) {
        teamOne = [];
        teamTwo = [];
        available = [];
        notAvailable = [];
      }
    }

    available = available.map(({ id }) => id);
    notAvailable = notAvailable.map(({ id }) => id);

    const newGame = {
      teamOne: removeNull(teamOne).map(({ id, name }) => ({
        id,
        name,
      })),
      teamTwo: removeNull(teamTwo).map(({ id, name }) => ({
        id,
        name,
      })),
      date: pendingGame.date,
      result: pendingGame.result || "pending",
      id: pendingGame.id,
      size: pendingGame.size,
      leagueId,
      location: pendingGame.location,
      available,
      notAvailable,
      cost: pendingGame.cost,
      teamOneName: pendingGame.teamOneName,
      teamTwoName: pendingGame.teamTwoName,
    };

    if (
      ["team-one-wins", "team-two-wins", "draw", "void"].includes(
        newGame.result
      )
    ) {
      message.info("Submitting game.");
    } else {
      message.info("Saving pending game.");
    }
    let response;
    try {
      response = await updatePendingGame(newGame);
      if (response.errors) {
        throw new Error(response.errors[0].message);
      }

      if (shouldResetAvailability) {
        await mutate(`getAvailability${leagueId}`, () => false);
      }
    } catch (err) {
      if (err.message === "not subscribed") {
        message.error("Sorry there is an issue with your account subscription");
      } else {
        message.error("Sorry an error occurred");
      }
      setLoading("save pending game", false);
      setLoading("submit result", false);
      return;
    }

    const newGameId = response.data.updatePendingGame.id;

    await updatePendingGameState({
      ...pendingGame,
      teamOne,
      teamTwo,
      id: newGameId,
    });

    await Promise.all([
      mutate(`${leagueId}_years`, () => null),
      mutate(
        `${leagueId}_${moment(pendingGame.date).year()}_stats`,
        () => null
      ),
      mutate(`${leagueId}_All_stats`, () => null),

      mutate(`getAvailability${leagueId}`, () => null),
      mutate(`getTeams${leagueId}`),
      mutate(`getNextGame${leagueId}`, () => null),
    ]);

    if (
      !["team-one-wins", "team-two-wins", "draw", "void"].includes(
        newGame.result
      )
    ) {
      message.success("Saved pending game");
      setLoading("save pending game", false);
      return;
    }

    message.success("Game submitted");
    setLoading("submit result", false);
    navigate(`/league/${leagueId}`);
  };

export default setupOnFinish;
