/* eslint-disable no-unused-expressions */
import React from "react";
import moment from "moment";
import { cacheBucket } from "../util/config";
import log from "./logger";

const addDatesToGames = (game) => ({
  ...game,
  dateText: moment(game.date).format("Do MMMM, YYYY"),
  dateMonthYear: moment(game.date).format("MMMM YYYY"),
  dateDay: moment(game.date).format("Do"),
  ref: React.createRef(),
});

const groupGamesByMonth = (acc, game) => {
  acc[game.dateMonthYear]
    ? acc[game.dateMonthYear].unshift(game)
    : (acc[game.dateMonthYear] = [game]);
  return acc;
};

const getGames =
  ({ leagueId }) =>
  async () => {
    let gamesResponse;
    try {
      const res = await fetch(
        `${cacheBucket}/${leagueId}_games.json?${Date.now()}`,
        {
          mode: "cors",
        }
      );

      if (res.status >= 300) {
        throw new Error(
          `request to get ${leagueId}_games.json?${Date.now()} returned ${
            res.status
          }`
        );
      }

      gamesResponse = (await res.json()).games;
    } catch (error) {
      log.warn({ message: "getGames failed", leagueId, error });
      return "getGames failed";
    }
    const nonPendingGames = gamesResponse.filter(
      ({ result }) => result !== "pending"
    );

    const games = nonPendingGames.map(addDatesToGames).reverse();
    const gameDates = games.reduce(groupGamesByMonth, {});

    return {
      games,
      gameDates,
      initialMonth: Object.keys(gameDates)[0],
    };
  };

export default getGames;
