/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { Button, Space, Typography, Modal } from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";

import { useNavigate } from "react-router-dom";

import UserContext from "../../contexts/UserContext";
import PlayerAvatar from "../atoms/PlayerAvatar";
import FootyStatsHero from "../atoms/FootyStatsHero";

import { deleteUser } from "../../services/api-service";

const { confirm } = Modal;
const { Title } = Typography;

const feedbackUrl =
  "https://docs.google.com/forms/d/e/1FAIpQLSf2MLilG9W8V39YvJsCciKW2JkigXYH462KK_qUocE5TYIsFg/viewform";

const AdminPanel = ({ onClose }) => {
  const { userData, logout, logoutFromThisDevice } = useContext(UserContext);
  const navigate = useNavigate();

  const deleteAccount = () => {
    confirm({
      title: "Are you sure you want to delete your account?",
      icon: <ExclamationCircleOutlined />,
      content: "This action can not be undone.",
      okText: "Delete",
      okType: "danger",
      cancelText: "Cancel",
      onOk: async () => {
        await deleteUser();
        await logoutFromThisDevice();
      },
    });
  };

  return (
    <>
      <FootyStatsHero noLogo hero="grass" heroHeight="120px" />
      <div style={{ width: "100%", textAlign: "center", marginBottom: "20px" }}>
        {userData.name && (
          <PlayerAvatar
            upload
            name={userData.name}
            picture={userData.picture}
            noMargin
            size={130}
            style={{
              fontSize: "80px",
              display: "inline-block",
              marginTop: "-70px",
              boxShadow: "0px 3px 5px 6px rgba(0,0,0,0.1)",
            }}
          />
        )}
      </div>
      <Space
        direction="vertical"
        style={{ textAlign: "center", width: "100%" }}
      >
        <Title
          level={3}
          style={{ fontWeight: "normal" }}
          data-test-id="users-name"
          data-user-id={userData._id}
        >
          {userData.name}
        </Title>
      </Space>
      <div style={{ padding: "10px 24px" }}>
        <Space direction="vertical" style={{ width: "100%" }} size="middle">
          <p style={{ fontSize: "20px", marginBottom: "5px" }}>Your Details</p>
          <Button type="text">Change Password</Button>
          <Button type="text" onClick={() => logout()}>
            Log out
          </Button>
        </Space>
        <div
          style={{
            textAlign: "left",
            position: "fixed",
            bottom: 0,
            display: "block",
          }}
        >
          <Space direction="vertical" style={{ width: "100%" }} size="middle">
            <Button
              style={{ color: "#FF7F7F" }}
              type="text"
              onClick={() => deleteAccount()}
            >
              Delete Your Account
            </Button>
            {/* <div> */}
            <Button
              type="text"
              onClick={() => {
                onClose();
                navigate("/privacy-policy");
              }}
            >
              Privacy Policy
            </Button>
            <Button
              type="text"
              style={{ marginBottom: "30px" }}
              onClick={() => window.open(feedbackUrl, "_blank")}
            >
              {" "}
              Give Feedback
            </Button>
          </Space>
        </div>
      </div>
    </>
  );
};

export default AdminPanel;
