/* eslint-disable no-console */
import { initializeApp } from "firebase/app";
import { getAuth, connectAuthEmulator } from "firebase/auth";

import {
  useEmulator,
  firebaseApiKey,
  firebaseAuthDomain,
  firebaseProjectId,
  firebaseStorageBucket,
  firebaseMessagingSenderId,
  firebaseAppId,
  firebaseMeasurementId,
  firebaseEmulatorUrl,
} from "./config";

const firebaseConfig = {
  apiKey: firebaseApiKey,
  authDomain: firebaseAuthDomain,
  projectId: firebaseProjectId,
  storageBucket: firebaseStorageBucket,
  messagingSenderId: firebaseMessagingSenderId,
  appId: firebaseAppId,
  measurementId: firebaseMeasurementId,
};

// Initialize Firebase
// eslint-disable-next-line no-unused-vars
const app = initializeApp(firebaseConfig);

const auth = getAuth();

console.log("in API call", useEmulator);

if (useEmulator) {
  console.log("using emulator in API call");
  console.log(firebaseEmulatorUrl);
  connectAuthEmulator(auth, firebaseEmulatorUrl, { disableWarnings: true });
  console.log("successfully set up the emulator");
}

export { app, auth };
