/* eslint-disable react/prop-types */
import React from "react";
import { Select, Form } from "antd";

const { Option } = Select;

const GameResultPicker = ({
  teamOne,
  teamTwo,
  setPendingGame,
  pendingGame,
}) => {
  const enoughPlayers =
    teamOne.filter((p) => p).length >= 4 &&
    teamTwo.filter((p) => p).length >= 4;
  const options = [
    {
      value: "team-one-wins",
      label: "Team One Wins",
      isDisabled: !enoughPlayers,
    },
    {
      value: "team-two-wins",
      label: "Team Two Wins",
      isDisabled: !enoughPlayers,
    },
    { value: "draw", label: "Draw", isDisabled: !enoughPlayers },
    { value: "void", label: "Void" },
  ];
  return (
    <Form.Item name="matchOutcome" noStyle>
      <Select
        placeholder="Select a result"
        style={{ width: "100%" }}
        className="test-result-selector"
        dropdownStyle={{ textAlign: "center" }}
        onChange={(e) => setPendingGame({ ...pendingGame, result: e })}
      >
        {options.map(({ value, label, isDisabled }) => (
          <Option
            key={value}
            data-test-disabled={isDisabled}
            data-test-id={`result-select-${value}`}
            disabled={isDisabled}
            value={value}
          >
            {label}
          </Option>
        ))}
      </Select>
    </Form.Item>
  );
};

export default GameResultPicker;
