/* eslint-disable react/prop-types */
import React, { useContext, useState } from "react";
import { Button, Divider, message, Alert, Modal, List, Typography } from "antd";

import useSWR, { mutate } from "swr";
import getPlayersAndUserData from "../services/getPlayersAndUserData";
import { setAdmin, setHidden, getLeague } from "../services/api-service";
import InviteLink from "../components/atoms/InviteLink";
import UserContext from "../contexts/UserContext";
import findUsersLeague from "../util/findUsersLeague";
import PlayerAvatar from "../components/atoms/PlayerAvatar";
import AddPlayerForm from "../components/AddPlayerForm";
import Loading from "../components/atoms/Loading";

const { Title } = Typography;

const LeagueSettings = ({ leagueId }) => {
  const { userData } = useContext(UserContext);

  const [playerName, setPlayerName] = useState("");

  const { data: players } = useSWR(
    `getPlayersAndUserData${leagueId}`,
    getPlayersAndUserData(leagueId)
  );

  const { data: leagueData } = useSWR(`getLeague${leagueId}`, () =>
    getLeague(leagueId)
  );

  if (!leagueData || !players) {
    return <Loading />;
  }

  const { leagueName } = findUsersLeague(userData, leagueId);

  const palyersSortedByAdmin =
    (players &&
      players.sort((a, b) => {
        if (b.creator) {
          return 1;
        }
        if (
          [null, "player"].includes(a.role) &&
          [null, "player"].includes(b.role)
        ) {
          return a.name.toLowerCase().localeCompare(b.name.toLowerCase());
        }
        if (b.role === "admin") {
          return 1;
        }
        return -1;
      })) ||
    [];

  const nonHiddenPlayers = palyersSortedByAdmin.filter((p) => !p.hidden);
  const hiddenPlayers = palyersSortedByAdmin.filter((p) => p.hidden);

  const updateAdmin = ({ id, admin }) => {
    const player = players.find((p) => p.id === id);

    let title;

    if (admin) {
      title = `Are you sure you want to make ${player.name} admin?`;
    } else {
      title = `Are you sure you want to remove ${player.name} as admin?`;
    }

    Modal.confirm({
      title,
      // content: 'Some descriptions',
      okText: "Yes",
      onOk: async () => {
        const response = await setAdmin({
          userId: player.userId,
          leagueName,
          admin,
          leagueId,
        });
        if (response.data.setAdmin.status === "Success") {
          if (admin) {
            message.success(`${player.name} is now an admin`);
          } else {
            message.success(`${player.name} is no longer an admin`);
          }
          mutate(`getPlayersAndUserData${leagueId}`);
        }
      },
    });
  };

  const hidePlayer = ({ id, hidden = true }) => {
    const player = players.find((p) => p.id === id);

    Modal.confirm({
      title: `Are you sure you want to ${hidden ? "hide" : "show"} ${
        player.name
      }?`,
      // content: 'Some descriptions',
      okText: "Yes",
      onOk: async () => {
        const response = await setHidden({
          playerId: id,
          hidden,
          leagueId,
        });
        if (response.data.setHidden.status === "Success") {
          if (hidden) {
            message.success(`${player.name} is now hidden`);
          } else {
            message.success(`${player.name} is no longer hidden`);
          }
          mutate(`getPlayersAndUserData${leagueId}`);
        }
      },
    });
  };

  return (
    <div
      style={{ textAlign: "center", minHeight: "100%", paddingBottom: "100px" }}
      className="league-settings-page"
    >
      <div style={{ maxWidth: "600px", margin: "auto", marginTop: "20px" }}>
        <div style={{ margin: "20px 50px" }}>
          <p>
            Share this invite link to invite new players to join the &quot;
            <span style={{ fontWeight: "bold" }}>{leagueName}</span>&quot;
            league
          </p>
          <div style={{ width: "100%" }}>
            <InviteLink
              leagueId={leagueId}
              block={false}
              leagueName={leagueData.name}
            />
          </div>
          <Divider>or</Divider>
          <p>
            Manually add a player to the &quot;
            <span style={{ fontWeight: "bold" }}>{leagueName}</span>&quot;
            league. This player can later be linked when a user signs up
          </p>
          {playerName === "" ? (
            <AddPlayerForm leagueId={leagueId} setPlayerName={setPlayerName} />
          ) : (
            <div style={{ maxWidth: 300, margin: "auto" }}>
              <Alert
                style={{ marginTop: "20px" }}
                message={`${playerName} has been added to the league.`}
                type="success"
              />
              <Button
                style={{ marginTop: "20px" }}
                block
                onClick={() => setPlayerName("")}
              >
                Add another player
              </Button>
            </div>
          )}
          <Divider />
          <List
            header={<Title level={4}>Your Squad</Title>}
            bordered={false}
            pagination={false}
            dataSource={nonHiddenPlayers}
            renderItem={(player, index) => {
              const { id, name, provider, email, picture, role, creator } =
                player;

              return (
                <List.Item data-test-id={`player-row-${index}`}>
                  <div style={{ textAlign: "left" }}>
                    <p data-test-id="player-name">
                      <PlayerAvatar name={name} picture={picture} />
                      {name}
                    </p>
                    <p>
                      <span data-test-id="linked-status">
                        {provider || "unlinked"}
                      </span>
                      <br />
                      <span data-test-id="email">{email}</span>
                    </p>
                  </div>
                  <div style={{ textAlign: "right" }}>
                    {role === "admin" && !creator && (
                      <Button
                        style={{ padding: 0 }}
                        type="link"
                        onClick={() => updateAdmin({ id, admin: false })}
                        danger
                      >
                        Remove admin
                      </Button>
                    )}
                    {role === "player" && (
                      <Button
                        style={{ padding: 0 }}
                        type="link"
                        onClick={() => updateAdmin({ id, admin: true })}
                      >
                        Make admin
                      </Button>
                    )}
                    {creator && "Creator"}
                    <br />
                    {role !== "admin" && (
                      <Button
                        style={{ padding: 0 }}
                        type="link"
                        onClick={() => hidePlayer({ id, hidden: true })}
                        danger
                      >
                        Hide player
                      </Button>
                    )}
                  </div>
                </List.Item>
              );
            }}
          />
          <List
            header={<Title level={4}>Hidden Players</Title>}
            bordered={false}
            pagination={false}
            dataSource={hiddenPlayers}
            renderItem={(player, index) => {
              const { id, name, provider, email, picture, role } = player;

              return (
                <List.Item data-test-id={id}>
                  <div style={{ textAlign: "left" }}>
                    <p data-test-id={`player-row-${index}`}>
                      <PlayerAvatar name={name} picture={picture} />
                      {name}
                    </p>
                    <p>
                      {provider || "unlinked"}
                      <br />
                      {email}
                    </p>
                  </div>
                  <div style={{ textAlign: "right" }}>
                    {role !== "admin" && (
                      <Button
                        style={{ padding: 0 }}
                        type="link"
                        onClick={() => hidePlayer({ id, hidden: false })}
                        danger
                      >
                        Show player
                      </Button>
                    )}
                  </div>
                </List.Item>
              );
            }}
          />
        </div>
      </div>
    </div>
  );
};

export default LeagueSettings;
