/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useContext } from "react";
import { Button, Typography, Input, Divider, Form } from "antd";

import { useNavigate } from "react-router-dom";
import UserContext from "../contexts/UserContext";
// import FootyStatsHero from "../components/atoms/FootyStatsHero";

const { Title, Link } = Typography;

const NewLeaguePage = () => {
  const { userData, setLoginVisible } = useContext(UserContext);
  const navigate = useNavigate();

  if (!userData) {
    navigate("/sign-up");
  }

  const createNewLeague = async ({ leagueName }) => {
    localStorage.setItem("newLeague", JSON.stringify({ leagueName }));
    navigate(`/checkout`);
  };

  return (
    <div
      style={{ textAlign: "center", minHeight: "100%", paddingBottom: "100px" }}
    >
      {/* <FootyStatsHero /> */}
      <Form
        name="createNewLeague"
        onFinish={createNewLeague}
        style={{
          maxWidth: "300px",
          textAlign: "center",
          display: "inline-block",
          marginTop: "40px",
        }}
      >
        <Title level={2} style={{ padding: "20px 0" }}>
          Create a new group
        </Title>
        <Form.Item
          name="leagueName"
          rules={[{ required: true, message: "Enter the group name." }]}
        >
          <Input
            name="New group name"
            placeholder="new group name"
            data-test-id="new-group-input-box"
          />
        </Form.Item>
        <Form.Item>
          <Button
            htmlType="submit"
            type="primary"
            block
            data-test-id="submit-new-group"
          >
            Create a new group
          </Button>
        </Form.Item>
        <Divider style={{ padding: "40px 0" }}>or</Divider>
        <Title level={2}>Join an existing group</Title>
        <p style={{ padding: "40px 0" }}>
          Ask your league&apos;s admin for an invitation link to join an
          existing group.
        </p>
        {!userData && (
          <p>
            Or <Link onClick={() => setLoginVisible(true)}>login</Link> if you
            are already a group member.
          </p>
        )}
      </Form>
    </div>
  );
};

export default NewLeaguePage;
