import moment from "moment";

const setDate = (e, pendingGame, updatePendingGameState) => {
  const [hour, minute] = pendingGame.date
    ? moment(pendingGame.date).format("HH:mm").split(":")
    : ["12", "00"];
  const date = moment(e)
    .set({
      hour: Number.parseInt(hour, 10),
      minute: Number.parseInt(minute, 10),
      second: 0,
      millisecond: 0,
    })
    .toISOString();

  updatePendingGameState({
    ...pendingGame,
    date,
  });
};

export default setDate;
