import { cacheBucket } from "../util/config";
import log from "./logger";

const fetchData =
  ({ leagueId }) =>
  async () => {
    let yearsResponse;
    try {
      const res = await fetch(
        `${cacheBucket}/${leagueId}_years.json?${Date.now()}`,
        {
          mode: "cors",
        }
      );

      if (res.status >= 300) {
        throw new Error(
          `request to get ${leagueId}_years.json?${Date.now()} returned ${
            res.status
          }`
        );
      }

      yearsResponse = (await res.json()).years.sort((a, b) =>
        a.localeCompare(b)
      );
    } catch (error) {
      log.warn({ message: "getYears failed", leagueId, error });
      return "getYears failed";
    }
    return yearsResponse;
  };

export default fetchData;
