const calculateTeamWinPercentage = (selectedPlayers) =>
  Object.values(selectedPlayers)
    .filter((i) => i)
    .reduce((acc, { winPercentage }) => acc + winPercentage, 0) /
    Object.keys(selectedPlayers).length || 0;
const calculateTeamRanking = (selectedPlayers) =>
  Object.values(selectedPlayers)
    .filter((i) => i)
    .reduce((acc, { ranking }) => acc + ranking, 0) /
    Object.keys(selectedPlayers).length || 0;

module.exports = {
  calculateTeamWinPercentage,
  calculateTeamRanking,
};
