/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import React, { useContext, useEffect, useState } from "react";
import { Row, Col } from "antd";
import { useNavigate, useParams } from "react-router-dom";

import UserContext from "../contexts/UserContext";
import Loading from "../components/atoms/Loading";
import TeamSelection from "../components/TeamSelection";
import GamesPage from "./GamesPage";
import FootyStatsHero from "../components/atoms/FootyStatsHero";

import AvailabilityPage from "./AvailabilityPage";
import StatsPage from "./StatsPage";
import LeagueSettings from "./LeagueSettings";
import findUsersLeague from "../util/findUsersLeague";
import GameDash from "../components/dashboard/GameDash";
import AccountIssueDash from "../components/dashboard/AccountIssueDash";
import StatsDash from "../components/dashboard/StatsDash";
import FormDash from "../components/dashboard/FormDash";
import AvailableDash from "../components/dashboard/AvailableDash";
import PaymentCard from "../components/dashboard/PaymentCard";
// import HighlightsDash from "../components/dashboard/HighlightsDash";
import HistoryDash from "../components/dashboard/HistoryDash";
import InvitePlayerDash from "../components/dashboard/InvitePlayerDash";
import AvailabilityDash from "../components/dashboard/AvailabilityDash";
import LeagueSettingsDash from "../components/dashboard/LeagueSettingsDash";
import PaymentDash from "../components/dashboard/PaymentDash";
import PaymentManagement from "./PaymentManagement";

const LeagueLinks = ({ userData, selectedLeague }) => {
  let playerRole;

  try {
    const { role } = findUsersLeague(userData, selectedLeague) || {};
    playerRole = role;
  } catch (error) {}

  return (
    <>
      <div
        className="dashboard"
        style={{
          maxWidth: "600px",
          margin: "auto",
          padding: `20px`,
          paddingBottom: "100px",
        }}
      >
        <Row gutter={[16, 16]}>
          <AccountIssueDash />
          <AvailableDash />
          <Col span={24}>
            <GameDash />
          </Col>
          <PaymentCard />
          <Col span={24}>
            <StatsDash />
          </Col>
          <Col span={24}>
            <FormDash />
          </Col>
          <Col span={24}>
            <HistoryDash />
          </Col>
          <Col span={24}>
            <InvitePlayerDash />
          </Col>
          {playerRole === "admin" && (
            <>
              <Col span={24}>
                <h2>Admin</h2>
              </Col>
              <Col span={12}>
                <AvailabilityDash />
              </Col>
              <Col span={12}>
                <LeagueSettingsDash />
              </Col>
              <Col span={12}>
                <PaymentDash />
              </Col>
            </>
          )}
        </Row>
      </div>
    </>
  );
};

const PanelContainer = ({ content }) => <>{content}</>;

const PanelContent = ({ props: { pageName, selectedLeague } }) => {
  if (pageName === "game-management") {
    return (
      <div style={{ maxWidth: "600px", margin: "auto", padding: `30px 20px` }}>
        <TeamSelection leagueId={selectedLeague} />
      </div>
    );
  }

  if (pageName === "availability") {
    return (
      <div style={{ maxWidth: "600px", margin: "auto", padding: `30px 20px` }}>
        <AvailabilityPage leagueId={selectedLeague} />
      </div>
    );
  }

  if (pageName === "highlights") {
    return (
      <div style={{ maxWidth: "600px", margin: "auto", padding: `30px 20px` }}>
        <StatsPage display="highlights" leagueId={selectedLeague} />
      </div>
    );
  }

  if (pageName === "player-stats") {
    return <StatsPage display="stats" leagueId={selectedLeague} />;
  }

  if (pageName === "game-history") {
    return (
      <div style={{ maxWidth: "600px", margin: "auto", padding: `30px 20px` }}>
        <GamesPage leagueId={selectedLeague} />
      </div>
    );
  }

  if (pageName === "payment-management") {
    return (
      <div style={{ maxWidth: "600px", margin: "auto", padding: `30px 20px` }}>
        <PaymentManagement leagueId={selectedLeague} />
      </div>
    );
  }

  if (pageName === "player-management") {
    return (
      <div style={{ maxWidth: "600px", margin: "auto" }}>
        <LeagueSettings leagueId={selectedLeague} />
      </div>
    );
  }
  return <></>;
};

const LeaguePanel = ({ userData, pageName, setLocation, selectedLeague }) => {
  if (pageName) {
    return (
      <PanelContainer
        content={<PanelContent props={{ pageName, selectedLeague }} />}
      />
    );
  }
  return (
    <LeagueLinks
      userData={userData}
      setLocation={setLocation}
      selectedLeague={selectedLeague}
    />
  );
};

const LeagueDashboard = () => {
  const [loading, setLoading] = useState(true);
  const { userData, setUserData } = useContext(UserContext);
  const { leagueId, pageName } = useParams();
  const navigate = useNavigate();

  let defaultLeague;

  try {
    defaultLeague = userData.leagues[0].leagueId;
  } catch (err) {}

  useEffect(() => {
    if (
      userData &&
      userData.leagues &&
      userData.leagues[0] &&
      !pageName &&
      leagueId === "undefined"
    ) {
      const newUserData = JSON.parse(localStorage.getItem("userData"));
      setUserData(newUserData);
      // eslint-disable-next-line no-console
      console.log(
        `leagueId is 'undefined' so navigating to '/league/${defaultLeague}'`
      );
      navigate(`/league/${defaultLeague}`);
      return;
    }

    if (!leagueId && defaultLeague) {
      // eslint-disable-next-line no-console
      console.log(
        `leagueId is falsey so navigating to '/league/${defaultLeague}'`
      );
      navigate(`/league/${defaultLeague}`);
      return;
    }

    if (!userData || !userData.leagues || !userData.leagues.length) {
      navigate("/features");
      return;
    }

    setLoading(false);
  });

  const selectedLeague = leagueId || defaultLeague;

  const changeLocation = (name) => {
    navigate(`/league/${selectedLeague}/${name}`);
  };

  const showHero = false;

  if (loading || !userData) {
    return <Loading />;
  }

  return (
    <>
      {showHero && <FootyStatsHero />}
      <div
        style={{
          textAlign: "center",
          width: "100%",
        }}
      >
        <LeaguePanel
          userData={userData}
          pageName={pageName}
          setLocation={changeLocation}
          selectedLeague={selectedLeague}
        />
      </div>
    </>
  );
};

export default LeagueDashboard;
