/* eslint-disable react/prop-types */
import React from "react";
import { Typography, Row, Col, Divider, Dropdown, Menu, Result } from "antd";
import { DownOutlined } from "@ant-design/icons";
import { useParams } from "react-router-dom";

import MobileCards from "./MobileCards";
import Highlights from "./Highlights";
import ModalContent from "./ModalContent";

const { Title } = Typography;

const buildMenu = (gameDates, onYearSelect) => (
  <Menu>
    {gameDates.map((year) => (
      <Menu.Item
        key={year}
        data-test-id={`year-${year}`}
        onClick={() => onYearSelect(year)}
      >
        {year}
      </Menu.Item>
    ))}
  </Menu>
);

const StatsPage = ({ data, setRequestedYear, display }) => {
  const { leagueId, playerId } = useParams();
  const playerStats = data && data.players;
  const highlights = data && data.highlights;

  const year = data && data.selectedYear;
  const totalGames = (data && data.numGames) || 0;

  const onYearSelect = async (newYear) => setRequestedYear(newYear);

  if (playerId) {
    return <ModalContent playerId={playerId} leagueId={leagueId} />;
  }

  const columns = [
    ...["Games", "Wins", "Draws", "Losses"].map((title) => ({
      title,
      testId: `stats-${title.toLowerCase()}`,
      dataIndex: title.toLowerCase(),
      key: title.toLowerCase(),
    })),
    {
      title: "Win%",
      dataIndex: "winPercentage",
      key: "winPercentage",
      testId: "stats-winPercentage",
      format: (winPercentage) => winPercentage.toFixed(0),
    },
  ];

  let years = [];

  if (data && data.years && Array.isArray(data.years)) {
    years = data.years.sort((a, b) => b.localeCompare(a));
  }

  if (display === "highlights") {
    return (
      <div
        style={{
          maxWidth: "600px",
          margin: "auto",
          padding: `0 20px`,
          paddingBottom: "100px",
        }}
      >
        <p>View the Highlights from each season.</p>
        {!highlights ? (
          <Result
            data-test-id="play-message"
            extra="Stats will appear after the first game has been played"
          />
        ) : (
          <>
            <Divider>
              Select Year
              <div style={{ cursor: "pointer" }}>
                <Dropdown
                  className="pointer"
                  overlay={buildMenu(["All", ...years], onYearSelect)}
                  trigger={["click"]}
                >
                  <Title
                    level={3}
                    className="ant-dropdown-link"
                    data-test-id="year-select"
                    style={{ color: "#3b5998" }}
                  >
                    {year} <DownOutlined style={{ verticalAlign: "0.2em" }} />
                  </Title>
                </Dropdown>
              </div>
            </Divider>
            <Highlights statsDashboard={highlights} totalGames={totalGames} />
          </>
        )}
      </div>
    );
  }

  const playersWithGames = playerStats
    ? playerStats.filter((p) => p.games > 0)
    : [];

  const playersWithoutGames = playerStats
    ? playerStats.filter((p) => p.games === 0)
    : [];

  return (
    <div
      className="stats-page"
      style={{
        maxWidth: "600px",
        margin: "auto",
        padding: `0 20px`,
        paddingBottom: "100px",
      }}
    >
      <p style={{ paddingTop: "40px" }}>
        View the Stats from each season. See who&apos;s performing well. Click a
        player to get a breakdown of an individual&apos;s performance.
      </p>
      {totalGames === 0 && (
        <Result
          data-test-id="play-message"
          extra="Stats will appear after the first game has been played"
        />
      )}

      <Row justify="space-between">
        {totalGames !== 0 && (
          <Col
            span={12}
            style={{ textAlign: "left" }}
            data-test-id="statsHeader"
            className="stats-header"
          >
            <p
              style={{
                fontWeight: "normal",
                fontSize: "16px",
                marginBottom: "10px",
                marginTop: "20px",
              }}
            >
              Total Games {totalGames}
            </p>
          </Col>
        )}
        {totalGames !== 0 && (
          <Col span={12} style={{ textAlign: "right", cursor: "pointer" }}>
            <Dropdown
              className="pointer"
              overlay={buildMenu(["All", ...years], onYearSelect)}
              trigger={["click"]}
            >
              <p
                className="ant-dropdown-link"
                data-test-id="year-select"
                style={{
                  color: "#3b5998",
                  fontSize: "20px",
                  fontWeight: "bold",
                  marginBottom: "10px",
                  marginTop: "20px",
                }}
              >
                {year} <DownOutlined style={{ verticalAlign: "0.2em" }} />
              </p>
            </Dropdown>
          </Col>
        )}
      </Row>

      {totalGames !== 0 && (
        <section className="data-view">
          <MobileCards playerStats={playersWithGames} columns={columns} />
          {playersWithoutGames.length > 0 && (
            <Title style={{ paddingTop: "50px" }} level={4}>
              Played Zero Games
            </Title>
          )}
          <MobileCards playerStats={playersWithoutGames} columns={columns} />
        </section>
      )}
    </div>
  );
};

export default StatsPage;
