/* eslint-disable react/no-children-prop */
import React, { useState } from "react";
import { Route, Routes, HashRouter } from "react-router-dom";
import ReactGA from "react-ga";
import { Modal, Button, Result } from "antd";
import { ErrorBoundary } from "react-error-boundary";
import Layout from "./components/Layout/Layout";
import ConfirmEmail from "./pages/ConfirmEmail";
import NewLeaguePage from "./pages/NewLeaguePage";
import Invite from "./pages/Invite";
import LeagueDashboard from "./pages/LeagueDashboard";
import Sales from "./pages/Sales";
import SalesJourney from "./pages/SalesJourney";
import SignUp from "./pages/SignUp";

import UserContext from "./contexts/UserContext";
import PrivacyPolicy from "./pages/PrivacyPolicy";
import TermsOfService from "./pages/TermsOfService";
import Modals from "./components/modals/Modals";

import "./custom.css";
import Checkout from "./pages/Checkout";
import log from "./services/logger";
import { logUrl } from "./util/config";
import LogIn from "./pages/LogIn";
import UserDetailsInput from "./pages/UserDetailsInput";

const feedbackUrl =
  "https://docs.google.com/forms/d/e/1FAIpQLSf2MLilG9W8V39YvJsCciKW2JkigXYH462KK_qUocE5TYIsFg/viewform";

ReactGA.initialize("UA-187572617", {
  debug: true,
  titleCase: false,
  // testMode: true,
});

const getUserDataFromLocalStorage = () => {
  try {
    const userData = JSON.parse(localStorage.getItem("userData"));
    return userData;
  } catch (e) {
    return false;
  }
};

const ErrorPage = () => (
  <Result
    stats="warning"
    title="Sorry we are experiencing some difficulties."
    subTitle="Please try again later"
    extra={[
      <Button type="primary" onClick={() => window.open(feedbackUrl, "_blank")}>
        Give Feedback
      </Button>,
      <Button
        type="primary"
        onClick={() => {
          window.open("/");
        }}
      >
        Back
      </Button>,
    ]}
  />
);

function App() {
  const JWTtoken = localStorage.getItem("JWTtoken");
  const userDataFromStorage = getUserDataFromLocalStorage();

  const [userToken, setUserTokenState] = useState(JWTtoken);
  const [isLoginVisible, setLoginVisible] = useState(false);
  const [extraLoginInfo, setExtraLoginInfo] = useState({});

  const [userData, setUserData] = useState(userDataFromStorage);
  const [pageTitle, setPageTitle] = useState("");
  const [modalState, setModalState] = useState("");

  const defaultLeague =
    userData && userData.leagues && userData.leagues[0]
      ? userData.leagues[0].leagueId
      : null;
  const [currentLeague, setCurrentLeague] = useState(defaultLeague);

  const [logoutVisible, setLogoutVisible] = useState(false);

  if (userData) {
    ReactGA.set({
      userId: userData._id,
    });
  }

  const setLoginVisible2 = (bool) => {
    setLoginVisible(bool);
  };

  const updateUserData = (newUserData) => {
    localStorage.setItem("userData", JSON.stringify(newUserData));
    setUserData(newUserData);
  };

  const setUserToken = (token) => {
    localStorage.setItem("JWTtoken", token);
    setUserTokenState(token);
  };

  const logout = () => {
    setLogoutVisible(true);
  };

  const logoutFromThisDevice = () => {
    setLogoutVisible(false);
    localStorage.removeItem("JWTtoken");
    localStorage.removeItem("userData");
    setUserTokenState(null);
    setUserData(null);
  };

  const buttonSpacing = { marginBottom: "10px" };

  const LogoutModal = () => (
    <Modal
      visible={logoutVisible}
      title="Log out?"
      onCancel={() => setLogoutVisible(false)}
      footer={null}
    >
      <Button
        data-test-id="logout-confirm"
        style={buttonSpacing}
        block
        key="log out"
        type="primary"
        onClick={() => logoutFromThisDevice()}
      >
        Log out
      </Button>
      <Button
        style={buttonSpacing}
        block
        key="all"
        onClick={() => logoutFromThisDevice()}
      >
        Log out from all devices
      </Button>
      <Button
        style={buttonSpacing}
        block
        key="cancel"
        onClick={() => setLogoutVisible(false)}
      >
        Cancel
      </Button>
    </Modal>
  );

  const Analytics = () => {
    const pageUrl = window.location.hash.replace("#", "");
    log.info({ info: "page view", location: pageUrl });
    if (logUrl !== "local") {
      ReactGA.set({ page: pageUrl });
      ReactGA.pageview(pageUrl);
    }

    return <></>;
  };

  return (
    <UserContext.Provider
      value={{
        setUserData,
        modalState,
        setModalState,
        currentLeague,
        setExtraLoginInfo,
        setCurrentLeague,
        extraLoginInfo,
        userToken,
        setUserToken,
        isLoginVisible,
        setLoginVisible: setLoginVisible2,
        userData,
        updateUserData,
        logout,
        pageTitle,
        setPageTitle,
        logoutFromThisDevice,
      }}
    >
      <HashRouter>
        <ErrorBoundary
          FallbackComponent={() => <></>}
          onError={async (error, info) => {
            try {
              await log.error({ error: error.message, info });
            } finally {
              window.location.replace("/#/error");
            }
          }}
        >
          <Analytics />
          <Routes>
            <Route
              path="/new-group"
              element={
                <Layout>
                  <NewLeaguePage />
                </Layout>
              }
            />
            <Route
              exact
              path="/"
              element={
                <Layout>
                  <LeagueDashboard />
                </Layout>
              }
            />
            <Route
              exact
              path="/dashboard"
              element={
                <Layout>
                  <LeagueDashboard />
                </Layout>
              }
            />
            {/* <Route
              path="/verifyEmail/:leaguePin"
              element={
                <Layout>
                  <ConfirmEmail />
                </Layout>
              }
            /> */}
            <Route
              path="/verifyEmail"
              element={
                <Layout>
                  <ConfirmEmail />
                </Layout>
              }
            />
            <Route
              path="/checkout"
              element={
                <Layout>
                  <Checkout />
                </Layout>
              }
            />
            <Route
              path="/league/:leagueId/:pageName/:year/:playerId"
              element={
                <Layout>
                  <LeagueDashboard />
                </Layout>
              }
            />
            <Route
              path="/league/:leagueId/:pageName/:year"
              element={
                <Layout>
                  <LeagueDashboard />
                </Layout>
              }
            />
            <Route
              path="/league/:leagueId/:pageName"
              element={
                <Layout>
                  <LeagueDashboard />
                </Layout>
              }
            />
            <Route
              path="/league/:leagueId"
              element={
                <Layout>
                  <LeagueDashboard />
                </Layout>
              }
            />
            <Route
              path="/join/:inviteLink"
              element={
                <Layout>
                  <Invite />
                </Layout>
              }
            />
            <Route
              path="/privacy-policy"
              element={
                <Layout>
                  <PrivacyPolicy />
                </Layout>
              }
            />
            <Route
              path="/terms-of-service"
              element={
                <Layout>
                  <TermsOfService />
                </Layout>
              }
            />
            <Route
              path="/features"
              element={
                <Layout>
                  <Sales />
                </Layout>
              }
            />
            <Route
              path="/all-features"
              element={
                <Layout>
                  <SalesJourney />
                </Layout>
              }
            />
            <Route
              path="/sign-up"
              element={
                <Layout>
                  <SignUp />
                </Layout>
              }
            />
            <Route
              path="/log-in"
              element={
                <Layout>
                  <LogIn />
                </Layout>
              }
            />
            <Route
              path="/user-details"
              element={
                <Layout>
                  <UserDetailsInput />
                </Layout>
              }
            />
            <Route
              path="/error"
              element={
                <Layout>
                  <ErrorPage />
                </Layout>
              }
            />
            <Route
              path=""
              element={
                <Layout>
                  <p>404 page</p>
                </Layout>
              }
            />
          </Routes>
          <Modals />
          <LogoutModal />
        </ErrorBoundary>
      </HashRouter>
    </UserContext.Provider>
  );
}

// backgroundColor: #deeeff

export default App;
