/* eslint-disable react/prop-types */
import React, { useContext, useState, useEffect } from "react";
import { useNavigate, useParams } from "react-router-dom";
import moment from "moment";
import {
  Form,
  Button,
  Typography,
  Modal,
  message,
  Row,
  Col,
  Select,
  Input,
  DatePicker,
} from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";

import {
  setCost,
  setTeamTwoName,
  setTeamOneName,
  setLocation,
  setSize,
} from "./updateGame";
import deletePendingGame from "./deletePendingGame";
import setTime from "./setTime";
import setDate from "./setDate";
import AccountIssue from "./AccountIssue";
// import { arraysEqual } from "../../util/equal";
import logger from "../../services/logger";

import UserContext from "../../contexts/UserContext";
import findUsersLeague from "../../util/findUsersLeague";
import TeamSelection from "./TeamSelection";
import setupOnFinish from "./setupOnFinish";

const { Option } = Select;
const { confirm } = Modal;
const { Text, Title } = Typography;

const clone = (input) => JSON.parse(JSON.stringify(input));

const onFinishFailed = (errorInfo) => {
  logger.error({
    info: "Error in game mangement update game",
    data: errorInfo,
  });
};

const activeLeagueStatuses = ["VIP", "FREE_TRIAL", "ACTIVE"];

const GameManagement = ({ availablePlayers, nextGame, leagueData }) => {
  const navigate = useNavigate();
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);

  const [costError, setCostError] = useState(false);
  const [loading, setLoadingArray] = useState([false, false]);
  const accountIssue = !activeLeagueStatuses.includes(leagueData.status);

  const setLoading = (action, state) => {
    if (action === "save pending game") {
      setLoadingArray([state, loading[1]]);
    } else if (action === "submit result") {
      setLoadingArray([loading[0], state]);
    }
  };

  const { role } = findUsersLeague(userData, leagueId) || { role: "player" };

  if (role !== "admin") {
    navigate("/");
  }

  const inputPendingGame = {
    id: nextGame.id,
    teamOne: clone(nextGame.teamOne.players),
    teamTwo: clone(nextGame.teamTwo.players),
    date: nextGame.date,
    result: "pending",
    size: nextGame.size ? nextGame.size : 5,
    location: nextGame.location,
    available: nextGame.available,
    notAvailable: nextGame.notAvailable,
    cost: nextGame.cost || 0,
    teamOneName: nextGame.teamOne.name,
    teamTwoName: nextGame.teamTwo.name,
  };

  const [pendingGame, updatePendingGameState] = useState(inputPendingGame);
  // const [confirmLeaveShown, setConfirmLeaveShown] = useState(false);

  const hasPendingPlayers = availablePlayers && availablePlayers.length !== 0;

  const onFinish = setupOnFinish({
    // setConfirmLeaveShown,
    updatePendingGameState,
    navigate,
    leagueId,
    setLoading,
  });

  const savePendingGame = async () => {
    if (!pendingGame.date) {
      return message.info("Select a game date first");
    }

    const largestTeamSize = Math.max(
      pendingGame.teamOne.length,
      pendingGame.teamTwo.length
    );

    if (pendingGame.size < largestTeamSize) {
      confirm({
        title: `You have selected too many players`,
        icon: <ExclamationCircleOutlined />,
        content: `Would you like to update the team size to ${largestTeamSize} players?`,
        okText: "Yes",
        onOk() {
          pendingGame.size = largestTeamSize;

          return onFinish({
            pendingGame: { ...pendingGame, result: "pending" },
            inputPendingGame,
            hasPendingPlayers,
          });
        },
        onCancel() {},
      });

      return false;
    }
    return onFinish({
      pendingGame: { ...pendingGame, result: "pending" },
      inputPendingGame,
      hasPendingPlayers,
    });
  };

  const hasDateSet = nextGame && nextGame.date;

  // eslint-disable-next-line arrow-body-style
  // const unsavedChanges = () => {
  //   return !(
  //     arraysEqual(
  //       pendingGame.teamOne,
  //       nextGame && nextGame.teamOne && nextGame.teamOne.players
  //     ) &&
  //     arraysEqual(
  //       pendingGame.teamTwo,
  //       nextGame && nextGame.teamTwo && nextGame.teamTwo.players
  //     ) &&
  //     (pendingGame.date === nextGame.date || nextGame.date === undefined)
  //   );
  // };

  // const confirmExit = () => {
  //     if (unsavedChanges && !confirmLeaveShown) {
  //         return "Are you sure you want to leave without saving your changes?"
  //     }
  //     return false
  // }

  const showConfirm = (e) => {
    // console.log({ e });

    e.preventDefault();
    e.returnValue = "";

    // setConfirmLeaveShown(true);
    confirm({
      icon: <ExclamationCircleOutlined />,
      content: "Would you like to save your changes to the game?",
      onOk: () => {
        savePendingGame();
      },
      onCancel: () => {
        navigate(e.pathname);
      },
      okText: "Yes",
      cancelText: "No",
    });
  };

  useEffect(() => {
    window.addEventListener("beforeunload", showConfirm);
    return () => {
      window.removeEventListener("beforeunload", showConfirm);
    };
  }, []);

  return (
    <Form
      name="basic"
      onFinish={() =>
        onFinish({ pendingGame, inputPendingGame, hasPendingPlayers })
      }
      onFinishFailed={onFinishFailed}
      className="game-management-form"
    >
      {/* <Beforeunload onBeforeunload={confirmExit} /> */}
      {/* <Prompt
        when={unsavedChanges() && !confirmLeaveShown}
        message={handleNavigation}
      /> */}
      <Text style={{ display: "block", paddingTop: "15px" }}>
        Choose your teams for the next fixture. You can randomise teams or you
        can customise your teams based on win percentage or ranking.
      </Text>
      <Title
        level={4}
        style={{ textAlign: "left", paddingTop: "20px", paddingBottom: "5px" }}
      >
        Fixture details
      </Title>
      <Row gutter={[16, 16]} style={{ paddingTop: "8px" }}>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="date">What is the date of the game?</label>
        </Col>
        <Col span={24} sm={12}>
          <DatePicker
            format="DD/MM/YYYY"
            id="date"
            defaultValue={
              pendingGame &&
              pendingGame.date &&
              moment(pendingGame && pendingGame.date)
            }
            onChange={(e) => setDate(e, pendingGame, updatePendingGameState)}
            style={{ width: "100%", marginBottom: "24px" }}
            data-test-id="date-selector"
            allowClear={false}
          />
        </Col>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="time">What is the time of the game?</label>
        </Col>
        <Col span={24} sm={12}>
          {/* <Button block data-test-id='set-game-date' type="primary" onClick={savePendingGame}>Change date</Button> */}
          <Form.Item>
            <Input
              id="time"
              type="time"
              data-test-id="select_time"
              defaultValue={
                (pendingGame &&
                  pendingGame.date &&
                  moment(pendingGame && pendingGame.date).format("HH:mm")) ||
                "12:00"
              }
              style={{ width: "100%" }}
              onChange={(e) => setTime(e, pendingGame, updatePendingGameState)}
              required
            />
          </Form.Item>
        </Col>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="number-of-players">How many players per team?</label>
        </Col>
        <Col span={24} sm={12}>
          <Form.Item>
            <Select
              id="number-of-players"
              name="How many players per team"
              defaultValue={pendingGame.size}
              style={{ width: "100%" }}
              dropdownStyle={{ textAlign: "center" }}
              onChange={(e) => setSize(e, pendingGame, updatePendingGameState)}
            >
              {[5, 6, 7, 8, 9, 10, 11].map((n) => (
                <Option value={n} key={n}>
                  {n}
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Col>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="game-cost">How much does it cost per player?</label>
        </Col>
        <Col span={24} sm={12}>
          <Form.Item
            {...(costError
              ? { validateStatus: "error", help: "Must be a number" }
              : {})}
          >
            <Input
              id="game-cost"
              prefix=""
              data-test-id="game-cost"
              defaultValue={(pendingGame.cost / 100).toFixed(2)}
              style={{ width: "100%", borderRadius: "50px" }}
              onChange={(e) =>
                setCost(e, pendingGame, updatePendingGameState, setCostError)
              }
              placeholder="6.50"
            />
          </Form.Item>
        </Col>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="location">Where is the fixture location?</label>
        </Col>
        <Col span={24} sm={12}>
          <Form.Item>
            <Input
              id="location"
              defaultValue={pendingGame.location}
              style={{ width: "100%" }}
              onChange={(e) =>
                setLocation(e, pendingGame, updatePendingGameState)
              }
              placeholder="Input address"
            />
          </Form.Item>
        </Col>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="teamOneName">What is the name of Team One?</label>
        </Col>
        <Col span={24} sm={12}>
          <Form.Item>
            <Input
              id="teamOneName"
              defaultValue={pendingGame.teamOneName}
              style={{ width: "100%" }}
              onChange={(e) =>
                setTeamOneName(e, pendingGame, updatePendingGameState)
              }
              placeholder="e.g. Bibs"
            />
          </Form.Item>
        </Col>
        <Col
          span={24}
          sm={12}
          style={{ textAlign: "left", paddingBottom: "0px" }}
        >
          <label htmlFor="teamTwoName">What is the name of Team Two?</label>
        </Col>
        <Col span={24} sm={12}>
          <Form.Item>
            <Input
              id="teamTwoName"
              defaultValue={pendingGame.teamTwoName}
              style={{ width: "100%" }}
              onChange={(e) =>
                setTeamTwoName(e, pendingGame, updatePendingGameState)
              }
              placeholder="e.g. Non Bibs"
            />
          </Form.Item>
        </Col>
        {(!hasDateSet || !hasPendingPlayers) && (
          <>
            <Col span={24} sm={12}>
              <Button
                block
                data-test-id="delete-pending-game"
                danger
                onClick={() =>
                  deletePendingGame({
                    pendingGame,
                    // setConfirmLeaveShown,
                    navigate,
                    leagueId,
                  })
                }
                disabled={!pendingGame.id}
              >
                Delete Pending Game
              </Button>
            </Col>
            <Col span={24} sm={12}>
              <Button
                block
                data-test-id="set-game-date"
                type="primary"
                onClick={savePendingGame}
                loading={loading[0]}
                disabled={accountIssue}
              >
                Save Pending Game
              </Button>
            </Col>
            {accountIssue && (
              <AccountIssue userData={userData} leagueId={leagueId} />
            )}
          </>
        )}
      </Row>
      {hasDateSet && !hasPendingPlayers && (
        <Row style={{ width: "100%" }}>
          <Col span={24}>
            <p
              data-test-id="no-availability-text"
              style={{ marginTop: "80px" }}
            >
              No players are currently available.
            </p>
            <p>
              Either ask the players to set their own availability or do it
              manually.
            </p>
            <Button
              // type='primary'
              onClick={() => navigate(`/league/${leagueId}/availability`)}
              data-test-id="availability-button"
            >
              Availability
            </Button>
          </Col>
        </Row>
      )}
      {hasDateSet && hasPendingPlayers && (
        <TeamSelection
          pendingGame={pendingGame}
          availablePlayers={availablePlayers}
          updatePendingGameState={updatePendingGameState}
          savePendingGame={savePendingGame}
          deletePendingGame={() =>
            deletePendingGame({
              pendingGame,
              // setConfirmLeaveShown,
              navigate,
              leagueId,
            })
          }
          loading={loading}
          accountIssue={accountIssue}
          onFinish={onFinish}
          inputPendingGame={inputPendingGame}
          hasPendingPlayers={hasPendingPlayers}
          userData={userData}
          leagueId={leagueId}
          nextGame={nextGame}
        />
      )}
    </Form>
  );
};

export default GameManagement;
