/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";
import { nanoid } from "nanoid";
import { containerStyle } from "../countdownStyles";
import findUsersLeague from "../../util/findUsersLeague";
import UserContext from "../../contexts/UserContext";
import LoadingBall from "../icons/LoadingBall";

import getYears from "../../services/getYears";
import getStats from "../../services/getStats";

const FormBox = ({ children, href, style }) => (
  <a
    href={href}
    data-test-id="form-dash-container"
    className="gradient-background main-fader"
    style={{
      ...containerStyle,
      fontSize: "20px",
      minHeight: "150px",
      ...style,
    }}
    type="text"
  >
    <div>{children}</div>
  </a>
);

const FormDash = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);
  const { playerId } = findUsersLeague(userData, leagueId);

  const currentYear = new Date().getFullYear();

  const { data: yearsData } = useSWR(
    `${leagueId}_years`,
    getYears({ leagueId })
  );
  const { data: statsData } = useSWR(
    `${leagueId}_${currentYear}_stats`,
    getStats({ leagueId, year: currentYear })
  );

  let showZero;
  if (statsData === "getStats failed") {
    showZero = true;
  }

  let mostRecentYear;
  try {
    if (yearsData && yearsData !== "getYears failed") {
      const years = yearsData;
      mostRecentYear = years[years.length - 1];
    } else {
      mostRecentYear = new Date().getFullYear().toString();
    }
  } catch (err) {}

  let statForIndividualPlayer;
  try {
    if (showZero) {
      statForIndividualPlayer = { form: "------" };
    }

    const thisPlayer = statsData.stats.find(({ id }) => id === playerId);

    if (thisPlayer) {
      statForIndividualPlayer = thisPlayer;
    } else {
      statForIndividualPlayer = { form: "------" };
    }
  } catch (err) {}

  if (!statForIndividualPlayer) {
    return (
      <FormBox
        style={{ cursor: "default" }}
        href={`/#/league/${leagueId}/player-stats/${mostRecentYear}/${playerId}`}
      >
        <LoadingBall fill="white" />
      </FormBox>
    );
  }

  const { form } = statForIndividualPlayer;

  /** @type {React.CSSProperties} */
  const formStyle = {
    display: "inline-block",
    width: "35px",
    padding: "4px",
    margin: "4px",
    fontWeight: "bold",
    fontSize: "16px",
    border: "1px solid white",
  };

  const FormLetters = ({ results }) => {
    const items = results.map((letter) => (
      <span
        className="form-letter form-dash-letter"
        style={formStyle}
        key={nanoid()}
      >
        {letter}
      </span>
    ));
    return <>{items}</>;
  };

  return (
    <FormBox
      href={`/#/league/${leagueId}/player-stats/${mostRecentYear}/${playerId}`}
      style={{ height: "auto" }}
    >
      <div style={{ paddingBottom: "10px", fontSize: "14px" }}>
        Your form for the past 6 games
      </div>
      <FormLetters results={form.slice(-6).split("")} />
      <div style={{ paddingTop: "15px", fontSize: "12px" }}>
        View Your Stats
      </div>
    </FormBox>
  );
};

export default FormDash;
