import { cacheBucket } from "../util/config";
import log from "./logger";

const fetchData =
  ({ leagueId, year }) =>
  async () => {
    let statsResponse;
    try {
      const res = await fetch(
        `${cacheBucket}/${leagueId}_${year}_stats.json?${Date.now()}`,
        {
          mode: "cors",
        }
      );

      if (res.status >= 300) {
        throw new Error(
          `request to get ${leagueId}_${year}_stats.json?${Date.now()} returned ${
            res.status
          }`
        );
      }

      const { stats, numGames } = await res.json();
      statsResponse = { stats, numGames };
    } catch (error) {
      log.warn({ message: "getStats failed", leagueId, error });
      return "getStats failed";
    }

    return statsResponse;
  };

export default fetchData;
