/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";
import { Col } from "antd";

import { containerStyle } from "../countdownStyles";
import WhiteLink from "../atoms/WhiteLink";
import UserContext from "../../contexts/UserContext";
import { getLeague } from "../../services/api-service";
import findUsersLeague from "../../util/findUsersLeague";

const activeLeagueStatuses = ["VIP", "FREE_TRIAL", "ACTIVE"];

const Container = ({ children, style }) => (
  <Col span={24}>
    <div
      className="gradient-background-red main-fader"
      data-test-id="admin-payment-management-dash-container"
      style={{ ...containerStyle, ...style }}
    >
      <div style={{ width: "100%" }}>{children}</div>
    </div>
  </Col>
);

const AccountIssueDash = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);

  const playerLeagueData = findUsersLeague(userData, leagueId);
  const isAdmin = playerLeagueData.role === "admin";

  if (!isAdmin) {
    return <></>;
  }

  const { data: leagueData } = useSWR(`getLeague${leagueId}`, () =>
    getLeague(leagueId)
  );

  if (
    !leagueData ||
    (leagueData && activeLeagueStatuses.includes(leagueData.status))
  ) {
    return <></>;
  }

  return (
    <Container leagueId={leagueId} style={{ minHeight: "100px" }}>
      <h2
        style={{
          color: "white",
          fontSize: "14px",
          fontWeight: "bold",
          paddingTop: "12px",
        }}
      >
        There is an issue with your account subscription.
      </h2>
      <WhiteLink
        style={{ marginTop: "10px", marginBottom: "10px" }}
        href={`https://docs.google.com/forms/d/e/1FAIpQLSdViiqsTj8aBwpIuILwoECs06qfba0VIL_WacgclFX3xy7gCw/viewform?usp=pp_url&entry.1029615170=${userData.name}&entry.485228527=${leagueId}&emailAddress=${userData.email}`}
        target="_blank"
      >
        Contact Support
      </WhiteLink>
    </Container>
  );
};

export default AccountIssueDash;
