import React, { useContext } from "react";
import { useParams, useNavigate } from "react-router-dom";
import useSWR from "swr";

import { Result, Button, Spin } from "antd";

import { getLeagueByPin, joinLeagueAsPlayer } from "../services/api-service";
import UserContext from "../contexts/UserContext";

const buildJoinLeague =
  ({ leagueId, leagueName, link, userContext, navigate }) =>
  async () => {
    const {
      userData,
      updateUserData,
      setUserToken,
      setCurrentLeague,
      setExtraLoginInfo,
    } = userContext;

    if (userData) {
      // user is added as a player to the league
      const response = await joinLeagueAsPlayer({ link });

      const { JWTtoken } = response.data.joinLeagueAsPlayer;
      const newUserData = JSON.parse(response.data.joinLeagueAsPlayer.data);

      setUserToken(JWTtoken);
      updateUserData(newUserData);
      setCurrentLeague(leagueId);
      navigate(`/league/${leagueId}`);
    } else {
      // user is prompted to login with the invite as an extra param
      setExtraLoginInfo({
        leagueId,
        message: (
          <p>
            To join the <strong>&quot;{leagueName}&quot;</strong> group
          </p>
        ),
        link,
        journey: "Sign Up",
      });
      navigate(`/sign-up`);
    }
  };

const buildGetInvite = (link) => async () => {
  const response = await getLeagueByPin(link);
  return response;
};

const Invite = () => {
  const { inviteLink } = useParams();
  const userContext = useContext(UserContext);
  const navigate = useNavigate();

  const { data } = useSWR(inviteLink, buildGetInvite(inviteLink));

  if (!data) {
    return <Result icon={<Spin size="large" />} title="Finding League" />;
  }

  const joinGroup = buildJoinLeague({
    leagueName: data.name,
    leagueId: data.id,
    link: inviteLink,
    userContext,
    navigate,
  });

  if (data.id && data.name) {
    return (
      <Result
        status="success"
        title={
          <p data-test-id="join-group-text">
            Are you sure you want to join the{" "}
            <strong>{`"${data.name}"`}</strong> group
          </p>
        }
        extra={
          <Button
            type="primary"
            onClick={joinGroup}
            data-test-id="join-group-button"
          >
            Join Group
          </Button>
        }
      />
    );
  }

  return (
    <Result
      status="warning"
      title="The invite link is invalid, contact the admin for a new link."
    />
  );
};

export default Invite;
