/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable no-empty */
/* eslint-disable react/prop-types */
import React, { useContext } from "react";
import { useParams } from "react-router-dom";
import useSWR, { mutate } from "swr";
import { Col, Modal, Input, message } from "antd";
import { EditOutlined } from "@ant-design/icons";

import { containerStyle } from "../countdownStyles";
import findUsersLeague from "../../util/findUsersLeague";
import UserContext from "../../contexts/UserContext";
import LoadingBall from "../icons/LoadingBall";
import WhiteButton from "../atoms/WhiteButton";

import { makePayment, singlePayments } from "../../services/api-service";

const asMoney = (value) => `${(Math.abs(value) / 100).toFixed(2)}`;

const PaymentBox = ({ children, style }) => (
  <Col key="balance" span={24}>
    <div
      style={{
        ...containerStyle,
        fontSize: "20px",
        minHeight: "150px",
        ...style,
        flexDirection: "column",
      }}
      className="gradient-background main-fader"
      data-test-id="payment-card"
    >
      {children}
    </div>
  </Col>
);

const PaymentCard = () => {
  const { leagueId } = useParams();
  const { userData } = useContext(UserContext);
  const { playerId } = findUsersLeague(userData, leagueId);

  const { data: player } = useSWR(`${playerId}_single_payments`, () =>
    singlePayments({ playerId })
  );

  if (!player) {
    return (
      <PaymentBox>
        <LoadingBall fill="white" />
      </PaymentBox>
    );
  }

  const { balance, awaitingConfirmationBalance } = player;

  let balanceText = "Up to date";

  if (balance < 0) {
    balanceText = "Owe:";
  }

  if (balance > 0) {
    balanceText = "Credit:";
  }

  return (
    <PaymentBox>
      <div style={{ paddingBottom: "10px", fontSize: "14px" }}>
        Your Balance
      </div>
      <div>
        {balanceText} {balance !== 0 && asMoney(balance)}
      </div>
      {awaitingConfirmationBalance !== 0 && (
        <div style={{ fontSize: "14px", marginTop: "4px" }}>
          Awaiting confirmation: 
          {(awaitingConfirmationBalance / 100).toFixed(2)} Credit
        </div>
      )}
      <WhiteButton
        style={{ marginTop: "15px" }}
        onClick={() => {
          const { confirm } = Modal;

          let paymentAmount;

          confirm({
            icon: <EditOutlined />,
            title: (
              <label htmlFor="credit-input">
                How much money have you sent?
              </label>
            ),
            content: (
              <Input
                id="credit-input"
                data-test-id="credit-input"
                prefix=""
                style={{ maxWidth: "100px", borderRadius: "50px" }}
                onChange={(e) => {
                  paymentAmount = e.target.value;
                }}
              />
            ),
            closable: true,
            onOk: async () => {
              try {
                const amountInPounds = Math.trunc(
                  Number.parseFloat(paymentAmount) * 100
                );

                if (amountInPounds < 0) {
                  throw new Error("Input a positive amount");
                }

                if (!Number.isInteger(amountInPounds)) {
                  throw new Error("not pounds");
                }

                await makePayment({
                  playerId,
                  newEntry: {
                    amount: amountInPounds,
                    date: new Date().toISOString(),
                    pending: true,
                  },
                });

                mutate(`${playerId}_single_payments`);
                message.success("Payment added");
              } catch (error) {
                if (error.message === "Input a positive amount") {
                  message.error("Input a positive amount");
                } else {
                  message.error("Input a number in pounds and pence");
                }
              }
            },
          });
        }}
      >
        Confirm Payment
      </WhiteButton>
    </PaymentBox>
  );
};

export default PaymentCard;
