const clone = (input) => JSON.parse(JSON.stringify(input));

const setLocation = (e, pendingGame, updatePendingGameState) => {
  const { value } = e.target;
  updatePendingGameState({
    ...pendingGame,
    location: value,
  });
};

const setTeamOneName = (e, pendingGame, updatePendingGameState) => {
  const { value } = e.target;
  updatePendingGameState({
    ...pendingGame,
    teamOneName: value,
  });
};

const setTeamTwoName = (e, pendingGame, updatePendingGameState) => {
  const { value } = e.target;
  updatePendingGameState({
    ...pendingGame,
    teamTwoName: value,
  });
};

const setCost = (e, pendingGame, updatePendingGameState, setCostError) => {
  const { value } = e.target;

  if (!value) {
    setCostError(false);
    return true;
  }

  if (!Number(value)) {
    setCostError(true);
    return false;
  }
  updatePendingGameState({
    ...pendingGame,
    cost: Math.round(value * 100),
  });
  setCostError(false);
  return true;
};

const clearTeams = (pendingGame, updatePendingGameState) => {
  const tempPendingGame = clone(pendingGame);
  tempPendingGame.teamOne = [];
  tempPendingGame.teamTwo = [];
  updatePendingGameState(tempPendingGame);
};

const setSize = (size, pendingGame, updatePendingGameState) => {
  updatePendingGameState({ ...pendingGame, size });
};

const pickTeam = (player, team, pendingGame, updatePendingGameState) => {
  const tempPendingGame = clone(pendingGame);
  const teamOneIndex = tempPendingGame.teamOne.findIndex(
    ({ name }) => player.name === name
  );
  const teamTwoIndex = tempPendingGame.teamTwo.findIndex(
    ({ name }) => player.name === name
  );

  if (team === "teamOne") {
    if (teamOneIndex !== -1) {
      // remove from teamOne
      tempPendingGame.teamOne.splice(teamOneIndex, 1);
    } else if (teamTwoIndex !== -1) {
      tempPendingGame.teamTwo.splice(teamTwoIndex, 1); // remove from team two
      tempPendingGame.teamOne.push(player); // add to team one
    } else {
      tempPendingGame.teamOne.push(player); // add to team one
    }
  } else if (team === "teamTwo") {
    if (teamTwoIndex !== -1) {
      tempPendingGame.teamTwo.splice(teamTwoIndex, 1); // remove from teamTwo
    } else if (teamOneIndex !== -1) {
      tempPendingGame.teamOne.splice(teamOneIndex, 1); // remove from team one
      tempPendingGame.teamTwo.push(player); // add to team two
    } else {
      tempPendingGame.teamTwo.push(player); // add to team two
    }
  }

  updatePendingGameState(tempPendingGame);
};

export {
  clearTeams,
  setSize,
  setCost,
  setTeamTwoName,
  setTeamOneName,
  setLocation,
  pickTeam,
};
