import React, { useContext } from "react";
import { useParams, NavLink, useLocation } from "react-router-dom";
import { isMobileSafari } from "react-device-detect";
import { Layout } from "antd";
import { HistoryOutlined } from "@ant-design/icons";

import Dashboard from "../icons/Dashboard";
import Clipboard from "../icons/simple/clipboard";

import UserContext from "../../contexts/UserContext";

const { Footer } = Layout;

const footerHeight = isMobileSafari ? "70px" : "58px";

/** @type {React.CSSProperties} */
const footerStyle = {
  position: "fixed",
  bottom: "0",
  width: "100%",
  height: footerHeight,
  background: "white",
  paddingTop: "6px",
  paddingLeft: 0,
  paddingRight: 0,
  borderTop: "1px solid #f0f0f0",
};

const navLinkStyle = {
  width: "30%",
  display: "inline-block",
  height: "100%",
  padding: 0,
};

const iconStyle = { color: "#999", verticalAlign: "middle" };

const SiteFooter = () => {
  const { userData } = useContext(UserContext);
  const { leagueId } = useParams();
  const { pathname } = useLocation();

  const urlParts = pathname.split("/");

  const defaultLeague =
    userData &&
    userData.leagues &&
    userData.leagues[0] &&
    userData.leagues[0].leagueId;
  const selectedLeague = leagueId || defaultLeague;

  const menuItems = [
    {
      name: "Dashboard",
      to: `/league/${selectedLeague}`,
      Icon: Dashboard,
      style: { height: "2em", width: "2.5em", fill: "#999" },
      activeStyle:
        urlParts.length === 3 && urlParts[1] === "league"
          ? { fill: "#187499" }
          : {},
      activeTextStyle:
        urlParts.length === 3 && urlParts[1] === "league"
          ? { color: "#187499" }
          : { color: "#999" },
    },
    {
      name: "Game History",
      to: `/league/${selectedLeague}/game-history`,
      Icon: HistoryOutlined,
      style: { width: "2.5em", color: "#999", fontSize: "27px" },
      activeStyle: urlParts.includes("game-history")
        ? { color: "#187499" }
        : {},
      activeTextStyle: urlParts.includes("game-history")
        ? { color: "#187499" }
        : { color: "#999" },
    },
    {
      name: "Player Stats",
      to: `/league/${selectedLeague}/player-stats`,
      Icon: Clipboard,
      style: { height: "2em", width: "2.2em" },
      activeStyle: urlParts.includes("player-stats")
        ? { color: "#187499" }
        : {},
      activeTextStyle: urlParts.includes("player-stats")
        ? { color: "#187499" }
        : { color: "#999" },
    },
  ];

  if (!userData) {
    return <> </>;
  }

  return (
    <Footer style={footerStyle}>
      <div
        style={{
          textAlign: "center",
          width: "100%",
        }}
      >
        <div style={{ maxWidth: "600px", margin: "auto", padding: `0 20px` }}>
          {menuItems.map(
            ({ name, to, Icon, style, activeStyle, activeTextStyle }) => (
              <NavLink
                to={to}
                key={name}
                style={navLinkStyle}
                data-test-id={`footer-${name}`}
              >
                <Icon style={{ ...style, ...iconStyle, ...activeStyle }} />
                <p style={{ ...activeTextStyle, fontSize: "10px" }}>{name}</p>
              </NavLink>
            )
          )}
        </div>
      </div>
    </Footer>
  );
};

export default SiteFooter;
