/* eslint-disable no-console */
import React, { useContext, useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";

import { Form, Typography } from "antd";

import { isSignInWithEmailLink, signInWithEmailLink } from "firebase/auth";

import { auth } from "../util/firebase";

import { getUserData } from "../services/api-service";

import UserContext from "../contexts/UserContext";
import Loading from "../components/atoms/Loading";
import log from "../services/logger";
import EmailSignUpInput from "../components/loginAndSignup/EmailSignUpInput";
// const useQuery = () => new URLSearchParams(useLocation().search);

const { Title } = Typography;

const confirmEmailWithFirebase = async ({
  email,
  setUserToken,
  updateUserData,
  navigate,
  searchParams,
}) => {
  try {
    const pin = searchParams.get("pin");

    console.log({ email, href: window.location.href });

    const result = await signInWithEmailLink(auth, email, window.location.href);

    console.log(result.user);
    console.log(result.additionalUserInfo);

    setUserToken(result.user.accessToken);

    const userDataResponse = await getUserData();

    console.log({ userDataResponse });

    const userData =
      userDataResponse &&
      userDataResponse.data &&
      userDataResponse.data.getUserData;

    console.log({ userData });

    if (userData) {
      updateUserData(userData);

      if (userData.leagues[0]) {
        const { leagueId } = userData.leagues[0];
        window.history.pushState("", "", "/");
        navigate(`/league/${leagueId}`, { replace: true });
      }
    } else {
      window.history.pushState("", "", "/");
      let url = `/user-details`;
      if (pin) {
        url = `${url}?pin=${pin}`;
      }
      navigate(url, { replace: true });
    }
  } catch (error) {
    console.log(error);
    log.error({ message: "error in signInWithEmailLink", error });
    if (error.code === "auth/invalid-action-code") {
      navigate({
        pathname: "/log-in",
        search: `?authError=${error.code}`,
      });
    }
  }
};

const checkEmail = async ({
  navigate,
  updateUserData,
  setUserToken,
  setShowEmailPrompt,
  setSearchParams,
  searchParams,
}) => {
  if (isSignInWithEmailLink(auth, window.location.href)) {
    const email = window.localStorage.getItem("emailForSignIn");
    if (!email) {
      console.log("didn't get email from local storage");
      setShowEmailPrompt(true);
    } else {
      console.log("got email from local storage");
      confirmEmailWithFirebase({
        email,
        navigate,
        updateUserData,
        setUserToken,
        setSearchParams,
        searchParams,
      });
    }
  }
};

const ConfirmEmail = () => {
  const navigate = useNavigate();

  const [showEmailPrompt, setShowEmailPrompt] = useState(false);
  const [searchParams, setSearchParams] = useSearchParams();

  const {
    updateUserData,
    setUserToken,
    // setLoginVisible
  } = useContext(UserContext);

  useEffect(() => {
    try {
      checkEmail({
        navigate,
        setUserToken,
        updateUserData,
        setShowEmailPrompt,
        setSearchParams,
        searchParams,
      });
    } catch (err) {
      log.error({ message: "confirm email error", err });
    }
  }, []);

  if (showEmailPrompt) {
    return (
      <div
        style={{
          maxWidth: "400px",
          margin: "auto",
          textAlign: "center",
          paddingTop: "40px",
        }}
      >
        <Title level={4}>Confirm Email Address</Title>
        <Form
          className="confirm-email-form"
          style={{ textAlign: "center" }}
          name="basic"
          initialValues={{
            remember: true,
          }}
          onFinish={(values) =>
            confirmEmailWithFirebase({
              email: values.Email,
              setUserToken,
              updateUserData,
              navigate,
              searchParams,
            })
          }
        >
          <EmailSignUpInput />
        </Form>
      </div>
    );
  }

  return <Loading />;
};

export default ConfirmEmail;
