/* eslint-disable react/prop-types */
import moment from "moment";
import React, { useEffect, useRef, useState } from "react";
import { colon, counterStyle, counterTextStyle } from "../countdownStyles";

const Countdown = ({ matchTime: inputMatchTime }) => {
  const [matchTime, setMatchTime] = useState(inputMatchTime);
  const [duration, setDuration] = useState(null);

  if (matchTime !== inputMatchTime || !duration) {
    setMatchTime(inputMatchTime);
    const now = moment().unix();
    setDuration(moment.duration(inputMatchTime - now, "seconds"));
  }

  const timeout = useRef();

  useEffect(() => {
    if (timeout.current) {
      clearTimeout(timeout.current);
    }

    const id = setTimeout(() => {
      const now = moment().unix();
      setDuration(moment.duration(matchTime - now, "seconds"));
    }, 1000);

    timeout.current = id;

    return () => {
      clearTimeout(timeout.current);
    };
  });

  if (!duration) {
    return <></>;
  }

  const days = duration.days().toString().padStart(2, "0");
  const hours = duration.hours().toString().padStart(2, "0");
  const minutes = duration.minutes().toString().padStart(2, "0");
  const seconds = duration.seconds().toString().padStart(2, "0");

  return (
    <>
      <div style={{ height: "55px" }}>
        <span style={counterStyle}>{days[0]}</span>
        <span style={counterStyle}>{days[1]}</span>
        <span style={colon}>:</span>
        <span style={counterStyle}>{hours[0]}</span>
        <span style={counterStyle}>{hours[1]}</span>
        <span style={colon}>:</span>
        <span style={counterStyle}>{minutes[0]}</span>
        <span style={counterStyle}>{minutes[1]}</span>
        <span style={colon}>:</span>
        <span style={counterStyle}>{seconds[0]}</span>
        <span style={counterStyle}>{seconds[1]}</span>
      </div>
      <div>
        <span style={counterTextStyle}>days</span>
        <span style={counterTextStyle}>hours</span>
        <span style={counterTextStyle}>minutes</span>
        <span style={counterTextStyle}>seconds</span>
      </div>
    </>
  );
};

export default Countdown;
