/* eslint-disable react/prop-types */
import React, { useContext, useState } from "react";
import Carousel from "re-carousel";
import { Button } from "antd";
import { useNavigate } from "react-router-dom";
import Title from "antd/lib/typography/Title";
import { nanoid } from "nanoid";
import UserContext from "../contexts/UserContext";
import log from "../services/logger";

/** @type {React.CSSProperties} */
const heroStyle = {
  height: "300px",
  textAlign: "center",
  backgroundPosition: "center",
  backgroundSize: "cover",
  backgroundImage: "url(/images/pitch.jpeg)",
  overflow: "hidden",
  marginLeft: "40px",
  marginRight: "40px",
  maxWidth: "500px",
  visibility: "hidden",
};

const pill = {
  display: "inline-block",
  width: "12px",
  height: "5px",
  background: "lightGrey",
  margin: "2px",
  borderRadius: "6px",
};

const selected = {
  background: "#3b5998",
};

const Progress = ({ step }) => {
  const pills = Array(3).fill(false);

  pills[step - 1] = true;

  return (
    <div
      style={{
        marginBottom: "40px",
      }}
    >
      {pills.map((active) => (
        <div key={nanoid()} style={{ ...pill, ...(active ? selected : {}) }} />
      ))}
    </div>
  );
};

const Sales = () => {
  // https://www.pexels.com/photo/high-angle-photo-of-grass-field-3571065/
  const navigate = useNavigate();
  const { userData } = useContext(UserContext);
  const carousel = React.createRef();
  const [currentPage, setCurrentPage] = useState(1);
  const [nextButtonText, setNextButtonText] = useState("Next");

  const nextClick = () => {
    if (currentPage === 3) {
      navigate("/checkout");
      return;
    }
    carousel.current.autoSlide("next");
    if (currentPage > 1) {
      setNextButtonText("Try free and subscribe");
    } else {
      setNextButtonText("Next");
    }
  };

  const onSlide = (e) => {
    try {
      const newId = e.current.childNodes[0].id;
      setCurrentPage(Number(newId));
      if (newId > 2) {
        setNextButtonText("Try free and subscribe");
      } else {
        setNextButtonText("Next");
      }
    } catch (err) {
      log.error({ message: "error in onSlide", err });
    }
  };

  return (
    <div
      className="sales"
      style={{ textAlign: "center", height: "calc(100vh - 100px)" }}
    >
      <Carousel ref={carousel} onTransitionEnd={onSlide}>
        <div id="1">
          <div
            style={{
              margin: "auto",
              display: "block",
              textAlign: "center",
              maxWidth: "500px",
            }}
          >
            <div
              style={heroStyle}
              className={currentPage === 1 ? "features-hero" : ""}
            >
              <div style={{ overflow: "auto" }}>
                <div
                  className="phone"
                  style={{
                    backgroundImage: "url('/images/fixtures-phone.png')",
                    marginTop: "250px",
                    width: "100% ",
                    height: "400px",
                    backgroundSize: "contain",
                    backgroundPosition: "center",
                    backgroundRepeat: "no-repeat",
                  }}
                />
              </div>
            </div>
          </div>
          <div
            style={{
              maxWidth: "350px",
              margin: "auto",
              height: `calc(100vh - ${userData ? "600px" : "500px"})`,
              minHeight: "200px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "column",
            }}
          >
            <Title level={3}>Get player availability</Title>
            <p>
              All the players of your league can view the next game. They can
              choose if they are available or not and view the team selection
              for that match.
            </p>
          </div>
        </div>
        <div id="2">
          <div
            style={{
              margin: "auto",
              display: "block",
              textAlign: "center",
              maxWidth: "500px",
            }}
          >
            <div
              style={heroStyle}
              className={currentPage === 2 ? "features-hero" : ""}
            >
              <div style={{ overflow: "auto" }}>
                <div
                  className="phone"
                  style={{
                    backgroundImage: "url('/images/stats-phone.png')",
                    marginTop: "250px",
                    width: "100% ",
                    height: "400px",
                    backgroundSize: "contain",
                    backgroundPosition: "center",
                    backgroundRepeat: "no-repeat",
                  }}
                />
              </div>
            </div>
          </div>
          <div
            style={{
              maxWidth: "350px",
              margin: "auto",
              height: `calc(100vh - ${userData ? "600px" : "500px"})`,
              minHeight: "200px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "column",
            }}
          >
            <Title level={3}>Play and save the result</Title>
            <p>
              Submit the game results and view the updated stats for each of the
              players
            </p>
          </div>
        </div>
        <div id="3">
          <div
            style={{
              margin: "auto",
              display: "block",
              textAlign: "center",
              maxWidth: "500px",
            }}
          >
            <div
              style={heroStyle}
              className={currentPage === 3 ? "features-hero" : ""}
            >
              <div style={{ overflow: "auto" }}>
                <div
                  className="phone"
                  style={{
                    backgroundImage: "url('/images/single-stats-phone.png')",
                    marginTop: "250px",
                    width: "100% ",
                    height: "400px",
                    backgroundSize: "contain",
                    backgroundPosition: "center",
                    backgroundRepeat: "no-repeat",
                  }}
                />
              </div>
            </div>
          </div>
          <div
            style={{
              maxWidth: "350px",
              margin: "auto",
              height: `calc(100vh - ${userData ? "600px" : "500px"})`,
              minHeight: "200px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "column",
            }}
          >
            <Title level={3}>Player Stats</Title>
            <p>
              View a breakdown of stats for each player, these are used to
              generate balanced teams in upcoming games.
            </p>
          </div>
        </div>
      </Carousel>
      <div
        style={{
          textAlign: "center",
          padding: "70px",
          paddingTop: "30px",
          color: "black",
          position: "absolute",
          bottom: "0",
          width: "100%",
          pointerEvents: "none",
        }}
      >
        <Progress step={currentPage} />
        <Button
          block
          type="primary"
          style={{ maxWidth: "300px", pointerEvents: "auto" }}
          onClick={nextClick}
        >
          {nextButtonText}
        </Button>
      </div>
    </div>
  );
};

export default Sales;
