/* eslint-disable react/prop-types */
import React, { useContext } from "react";

import { Typography, Space, Row, Col } from "antd";
import useSWR from "swr";
import { useParams } from "react-router-dom";
import { nanoid } from "nanoid";
import getStats from "../services/getStats";
import Loading from "./atoms/Loading";
import PlayerAvatar from "./atoms/PlayerAvatar";
import UserContext from "../contexts/UserContext";

const { Title, Text } = Typography;

const ModalContent = ({ playerId, leagueId }) => {
  const { year } = useParams();
  const { userData } = useContext(UserContext);

  let defaultYear;

  if (!year) {
    defaultYear = new Date().getFullYear().toString();
  } else {
    defaultYear = year;
  }

  const { data: statsData } = useSWR(
    `${leagueId}_${defaultYear}_stats`,
    getStats({ leagueId, year: defaultYear })
  );

  let showZero;

  if (statsData === "getStats failed") {
    showZero = true;
  }

  const zeroStats = {
    draws: 0,
    form: "------",
    games: 0,
    id: userData.id,
    losses: 0,
    mostGamesUnbeaten: 0,
    mostGamesWithoutAWin: 0,
    mostLossesInARow: 0,
    mostWinsInARow: 0,
    name: userData.name,
    picture: userData.picture,
    playerStats: [],
    ranking: 0,
    rankingNum: 0,
    winPercentage: 0,
    wins: 0,
  };

  if (!statsData && !showZero) {
    return <Loading />;
  }

  let statForIndividualPlayer;

  if (showZero) {
    statForIndividualPlayer = zeroStats;
  } else {
    statForIndividualPlayer = statsData.stats.find(({ id }) => playerId === id);
  }

  const playerStats = statForIndividualPlayer.playerStats.filter(
    (stat) => stat.games > 0
  );

  const {
    name,
    picture,
    form,
    mostWinsInARow,
    mostGamesUnbeaten,
    mostLossesInARow,
    mostGamesWithoutAWin,
  } = statForIndividualPlayer;

  const columns = [
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
    },
    ...["games", "wins", "draws", "losses"].map((dataIndex) => ({
      dataIndex,
      title: dataIndex.slice(0, 1).toUpperCase(),
      key: dataIndex,
    })),
    {
      title: "Win %",
      dataIndex: "winPercentage",
      key: "winPercentage",
      format: (text) => Number.parseInt(text, 10).toFixed(0),
      style: { textAlign: "right" },
    },
  ];

  const formStyle = {
    display: "inline-block",
    width: "35px",
    height: "35px",
    padding: "4px",
    margin: "4px",
    fontWeight: "bold",
    fontSize: "18px",
    color: "white",
    border: "1px solid white",
  };

  const FormLetters = ({ results }) => {
    const items = results.map((letter) => (
      <span
        className="form-letter form-dash-letter"
        style={formStyle}
        key={nanoid()}
      >
        {letter}
      </span>
    ));
    return <>{items}</>;
  };

  return (
    <div
      className="modal-body"
      style={{ textAlign: "center", paddingBottom: "100px" }}
    >
      <div
        style={{
          maxWidth: "600px",
          margin: "auto",
          padding: "20px 20px 100px",
        }}
      >
        <Row gutter={[16, 16]}>
          <Col span={24}>
            <div
              style={{
                paddingBottom: "20px",
                maxWidth: "800px",
                margin: "auto",
                borderRadius: "8px",
                paddingTop: "20px",
              }}
            >
              <div
                style={{
                  width: "100%",
                  textAlign: "center",
                  marginBottom: "20px",
                }}
              >
                <PlayerAvatar
                  name={name}
                  picture={picture}
                  noMargin
                  size={130}
                  style={{
                    fontSize: "80px",
                    display: "inline-block",
                    boxShadow: "0px 3px 5px 6px rgba(0,0,0,0.1)",
                  }}
                />
              </div>
              <Title
                level={3}
                style={{ fontWeight: "normal", marginBottom: "0px" }}
              >
                {name}
              </Title>
            </div>
          </Col>
          <Col span={24}>
            <div
              className="gradient-background"
              style={{
                paddingBottom: "20px",
                maxWidth: "800px",
                margin: "auto",
                borderRadius: "8px",
                paddingTop: "20px",
              }}
            >
              <div style={{ textAlign: "center" }}>
                <Text
                  data-test-id="selectedPlayerForm"
                  style={{
                    color: "white",
                    display: "block",
                    marginBottom: "10px",
                    fontSize: "18px",
                  }}
                >
                  Last 6 games
                </Text>
                <FormLetters results={form.slice(-6).split("")} />
              </div>
            </div>
          </Col>
          <Col span={24}>
            <div
              className="gradient-background"
              style={{
                paddingBottom: "20px",
                maxWidth: "800px",
                margin: "auto",
                borderRadius: "8px",
                paddingTop: "20px",
              }}
            >
              <Row style={{ width: "100%", justifyContent: "center" }}>
                <h2
                  style={{
                    color: "white",
                    marginBottom: "5px",
                    fontWeight: "bold",
                  }}
                >
                  This season they have
                </h2>
                <div
                  style={{ color: "white", width: "100%", fontSize: "14px" }}
                >
                  Won{" "}
                  <span style={{ fontWeight: "bold" }}>{mostWinsInARow}</span>{" "}
                  games in a row
                </div>
                <div
                  style={{ color: "white", width: "100%", fontSize: "14px" }}
                >
                  Gone{" "}
                  <span style={{ fontWeight: "bold" }}>
                    {mostGamesUnbeaten}
                  </span>{" "}
                  games unbeaten
                </div>
                <div
                  style={{ color: "white", width: "100%", fontSize: "14px" }}
                >
                  Lost{" "}
                  <span style={{ fontWeight: "bold" }}>{mostLossesInARow}</span>{" "}
                  games in a row
                </div>
                <div
                  style={{ color: "white", width: "100%", fontSize: "14px" }}
                >
                  Gone{" "}
                  <span style={{ fontWeight: "bold" }}>
                    {mostGamesWithoutAWin}
                  </span>{" "}
                  games without a win
                </div>
              </Row>
            </div>
          </Col>
          {playerStats.length !== 0 && (
            <Col span={24}>
              <div
                className="gradient-background"
                style={{
                  padding: "20px 20px",
                  maxWidth: "800px",
                  margin: "auto",
                  borderRadius: "8px",
                }}
              >
                <div style={{ maxWidth: "600px", margin: "auto" }}>
                  <Space direction="vertical" style={{ width: "100%" }}>
                    <div style={{ textAlign: "center", width: "100%" }}>
                      <p
                        style={{
                          display: "inline-block",
                          color: "white",
                          maxWidth: "400px",
                        }}
                      >
                        The table below shows your performance with other
                        players when they have been on your team
                      </p>
                      <div
                        style={{
                          display: "inline-block",
                          maxWidth: "400px",
                          width: "100%",
                          textAlign: "center",
                        }}
                      >
                        <table
                          className="playerTable"
                          data-test-id="personalStatsTable"
                          style={{
                            width: "100%",
                            textAlign: "left",
                            color: "white",
                            marginTop: "10px",
                            maxWidth: "400px",
                          }}
                        >
                          <thead>
                            <tr>
                              {columns.map(({ title, key, style = {} }) => (
                                <th key={`title-${key}`} style={style}>
                                  {title}
                                </th>
                              ))}
                            </tr>
                          </thead>
                          <tbody>
                            {playerStats.map((player) => (
                              <tr key={`row-${player._id || nanoid()}`}>
                                {columns.map(
                                  ({ dataIndex, key, format, style = {} }) => (
                                    <td
                                      key={`data-${dataIndex}-${key}`}
                                      style={{ ...style, paddingTop: "5px" }}
                                    >
                                      {format
                                        ? format(player[dataIndex])
                                        : player[dataIndex]}
                                    </td>
                                  )
                                )}
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </Space>
                </div>
              </div>
            </Col>
          )}
        </Row>
      </div>
    </div>
  );
};

export default ModalContent;
