/* eslint-disable react/prop-types */
import React from "react";
import { useParams } from "react-router-dom";
import { HistoryOutlined } from "@ant-design/icons";
import { containerStyle } from "../countdownStyles";

const Container = ({ children, leagueId, style }) => (
  <a
    href={`/#/league/${leagueId}/game-history`}
    className="gradient-background main-fader"
    data-test-id="game-history-dash-container"
    style={{ ...containerStyle, ...style }}
  >
    <div style={{ width: "100%" }}>{children}</div>
  </a>
);

const HistoryDash = () => {
  const { leagueId } = useParams();

  return (
    <Container leagueId={leagueId} style={{ minHeight: "100px" }}>
      <HistoryOutlined style={{ color: "white", fontSize: "50px" }} />
      <h2
        style={{
          color: "white",
          fontSize: "12px",
          fontWeight: "bold",
          paddingTop: "12px",
        }}
      >
        View Game History
      </h2>
    </Container>
  );
};

export default HistoryDash;
