import React from "react";
import { Form, Input, Button, message } from "antd";
import { mutate } from "swr";
import { createPlayer } from "../services/api-service";

const cap = (string) => string.charAt(0).toUpperCase() + string.slice(1);

// eslint-disable-next-line react/prop-types
const AddPlayerForm = ({ leagueId, setPlayerName }) => {
  const addPlayer = async ({ "First Name": first, "Last Name": last }) => {
    try {
      const newPlayerName = `${cap(first.trim())} ${cap(last.trim())}`;

      const result = await createPlayer({ name: newPlayerName, leagueId });
      if (!result.data.createPlayer.id) {
        throw Error("fail");
      }
      setPlayerName(newPlayerName);

      mutate(`getPlayersAndUserData${leagueId}`);
    } catch (error) {
      message.error(`Sorry and error occurred. Try again later.`);
    }
  };

  return (
    <Form
      name="newPlayer"
      onFinish={addPlayer}
      style={{
        width: "100%",
        maxWidth: 300,
        textAlign: "center",
        display: "inline-block",
        paddingTop: "20px",
      }}
    >
      <Form.Item
        name="First Name"
        rules={[
          {
            required: true,
            message: "Enter the player's First Name.",
          },
        ]}
      >
        <Input placeholder="First Name" data-test-id="new-player-first-name" />
      </Form.Item>
      <Form.Item
        name="Last Name"
        rules={[
          {
            required: true,
            message: "Enter the player's Last Name.",
          },
        ]}
      >
        <Input placeholder="Last Name" data-test-id="new-player-last-name" />
      </Form.Item>
      <Form.Item>
        <Button
          type="primary"
          htmlType="submit"
          block
          data-test-id="submit-new-player"
        >
          Add Player
        </Button>
      </Form.Item>
    </Form>
  );
};

export default AddPlayerForm;
