/* eslint-disable no-console */
import React, { useState, useContext } from "react";
import { Form, Input, Button, Typography, Modal } from "antd";
import { useNavigate } from "react-router-dom";

import UserContext from "../contexts/UserContext";
import log from "../services/logger";

import {
  createUserAndGroup,
  createUserAndJoinGroup,
} from "../services/api-service";

const { Title, Text } = Typography;

const cap = (str) => str.charAt(0).toUpperCase() + str.slice(1);

const UserDetailsInput = () => {
  const [createOrJoinPrompt, setCreateOrJoinPrompt] = useState(false);
  const [createNewGroup, setCreateNewGroup] = useState(false);
  const [name, setName] = useState("");
  const [picture, setPicture] = useState("");
  const [joinGroup, setJoinGroup] = useState(false);
  const navigate = useNavigate();

  const { updateUserData, setCurrentLeague, extraLoginInfo } =
    useContext(UserContext);

  const pin = extraLoginInfo.link;

  if (!name && extraLoginInfo.name) {
    console.log("setting name");
    setName(extraLoginInfo.name);
    setCreateOrJoinPrompt(true);
  }

  if (!picture && extraLoginInfo.picture) {
    console.log("setting picture");
    setPicture(extraLoginInfo.picture);
    setCreateOrJoinPrompt(true);
  }

  console.log({ pin });

  const groupPinInput = async ({
    "Group Pin": inputGroupPin,
    name: inputName,
  }) => {
    if (!inputName && !name) {
      throw new Error("name is null");
    }
    try {
      const response = await createUserAndJoinGroup({
        picture,
        name: inputName || name,
        groupPin: inputGroupPin,
      });

      console.log(response);

      const newUserData = response.createUserAndJoinGroup;

      if (!newUserData) {
        Modal.warning({
          title: "The pin you entered is incorrect",
        });
        return;
      }

      const { leagueId } = response.createUserAndJoinGroup.leagues[0];

      updateUserData(newUserData);
      setCurrentLeague(leagueId);
      navigate(`/league/${leagueId}`);
    } catch (error) {
      log.error(error);
      navigate(`/error`);
    }
  };

  const nameInput = async ({ "First Name": first, "Last Name": last }) => {
    const chosenName = `${cap(first)} ${cap(last)}`;

    setName(chosenName);
    if (pin) {
      groupPinInput({ "Group Pin": pin, name: chosenName });
    } else {
      setCreateOrJoinPrompt(true);
    }
  };

  const groupNameInput = async ({ "Group Name": inputGroupName }) => {
    const chosenGroupName = cap(inputGroupName);

    let newUserData;

    try {
      const result = await createUserAndGroup({
        picture,
        name,
        groupName: chosenGroupName,
      });
      newUserData = result.setUserDataAndCreateLeague;
    } catch (error) {
      console.log(error);
    }

    console.log({ newUserData });

    updateUserData(newUserData);
    const { leagueId } = newUserData.leagues[0].leagueId;
    navigate(`/league/${leagueId}`);
  };

  if (joinGroup) {
    return (
      <div
        style={{
          textAlign: "center",
        }}
      >
        <div
          style={{
            width: "100%",
            maxWidth: "300px",
            display: "inline-block",
            marginTop: "20px",
          }}
        >
          <Title level={2}>Group Pin</Title>
          <p>Input the pin for the group</p>
          <Form
            style={{ textAlign: "center" }}
            // name="basic"
            onFinish={groupPinInput}
          >
            <Form.Item
              name="Group Pin"
              validateTrigger="focusout"
              rules={[
                {
                  required: true,
                  message: "Input a six digit pin",
                },
              ]}
            >
              <Input
                size="large"
                placeholder="Group Pin"
                data-test-id="group-pin-input"
                type="text"
              />
            </Form.Item>
            <Form.Item>
              <Button
                block
                data-test-id="group-pin-join-button"
                size="large"
                type="primary"
                htmlType="submit"
              >
                Join
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }

  if (createNewGroup) {
    return (
      <div
        style={{
          textAlign: "center",
        }}
      >
        <div
          style={{
            width: "100%",
            maxWidth: "300px",
            display: "inline-block",
            marginTop: "20px",
          }}
        >
          <Title level={4}>Group Details</Title>
          <p>Input a name for your group</p>
          <Form
            style={{ textAlign: "center" }}
            // name="basic"
            onFinish={groupNameInput}
          >
            <Form.Item
              name="Group Name"
              validateTrigger="focusout"
              rules={[
                {
                  required: true,
                  message: "Choose a name for your group",
                },
              ]}
            >
              <Input
                size="large"
                placeholder="Group Name"
                data-test-id="group-name-input"
                type="text"
              />
            </Form.Item>
            <Form.Item>
              <Button
                block
                data-test-id="group-name-create-button"
                size="large"
                type="primary"
                htmlType="submit"
              >
                Create
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }

  if (createOrJoinPrompt) {
    return (
      <div
        style={{
          textAlign: "center",
        }}
      >
        <div
          style={{
            width: "100%",
            maxWidth: "300px",
            display: "inline-block",
            marginTop: "20px",
          }}
        >
          <Title level={4}>Create or Join Group</Title>
          <Text>Create a new group</Text>
          <Button
            block
            data-test-id="login-button"
            size="large"
            type="primary"
            onClick={() => setCreateNewGroup(true)}
          >
            New Group
          </Button>
          <Text>Join an existing group</Text>
          <Button
            block
            data-test-id="login-button"
            size="large"
            type="primary"
            onClick={() => setJoinGroup(true)}
          >
            Join Group
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div
      style={{
        textAlign: "center",
      }}
    >
      <div
        style={{
          width: "100%",
          maxWidth: "300px",
          display: "inline-block",
          marginTop: "20px",
        }}
      >
        <Title level={4}>User Details</Title>
        <p>Input your first and last name</p>
        <Form
          style={{ textAlign: "center" }}
          // name="basic"
          onFinish={nameInput}
        >
          <Form.Item
            name="First Name"
            validateTrigger="focusout"
            rules={[
              {
                required: true,
                message: "Input your First Name",
              },
            ]}
          >
            <Input
              size="large"
              placeholder="First Name"
              data-test-id="login-first-name"
              type="text"
            />
          </Form.Item>
          <Form.Item
            name="Last Name"
            validateTrigger="focusout"
            rules={[
              {
                required: true,
                message: "Input your Last Name",
              },
            ]}
          >
            <Input
              placeholder="Last Name"
              data-test-id="login-last-name"
              size="large"
            />
          </Form.Item>
          <Form.Item>
            <Button
              block
              data-test-id="login-button"
              size="large"
              type="primary"
              htmlType="submit"
            >
              Next
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default UserDetailsInput;
