/* eslint-disable react/prop-types */
import { Alert, Button, Form, Input } from "antd";
import React, { useState } from "react";
import { resetPasswordEmail } from "../../services/api-service";

const Feedback = ({ expired, resetPasswordMessage }) => {
  if (resetPasswordMessage) {
    return (
      <Alert
        message="Check your emails and click on the Who's Game link."
        type="success"
        style={{ marginBottom: "20px" }}
      />
    );
  }
  if (expired) {
    return (
      <Alert
        message="Sorry the password reset link expired. Enter your email."
        type="error"
        style={{ marginBottom: "20px" }}
      />
    );
  }
  return <> </>;
};

const ResetPassword = ({ expired }) => {
  const [resetPasswordMessage, setResetPasswordMessage] = useState(false);

  const resetPassword = async ({ email }) => {
    await resetPasswordEmail({ email });
    setResetPasswordMessage(true);
  };

  return (
    <>
      <Feedback expired={expired} resetPasswordMessage={resetPasswordMessage} />
      <p>
        Enter the email address associated with your account, and we&apos;ll
        email you a link to reset your password.
      </p>
      <Form onFinish={resetPassword}>
        <Form.Item
          name="email"
          rules={[{ type: "email", message: "Input a valid Email" }]}
        >
          <Input type="email" placeholder="email" />
        </Form.Item>
        <Button block type="primary" htmlType="submit">
          Send reset link
        </Button>
      </Form>
    </>
  );
};

export default ResetPassword;
